.proxy-hero {
  padding: 170px 0 110px ;
  overflow: hidden;
  @media #{$maxTab} {
    padding: 50px 0 60px ;
  }

  &__text {
    p {
      margin: 20px 0 60px;
      @media #{$maxTab} {
        margin: 20px 0 40px;
      }
    }
    .proxy-btn {
      @media #{$maxTab} {
        text-align: center;
      }
      a {
        max-width: 260px;
        width: 100%;
        line-height: 64px;
        @media #{$mobile} {
          max-width: unset;
        }
      }
    }
  }
}

.free-proxies,.bypass-youtube,.youtube-secure,.proxies {
  padding: 60px 0;
}