// Start Upgrade Plan page

.choose-steps.upgrade-plan .pricing-tables .marked .award img {
  width: 45px;
}

.choose-steps.upgrade-plan .pricing-tables .price-card.two .price-top .special-offer {
  .month,
  .free {
    font-size: 14px;
  }
}

.choose-steps.upgrade-plan .pricing-tables .price-card.two .price-top .special-offer {
  max-width: 195px;
  @media #{$betweentabdesk} {
    max-width: 165px;
  }
}

.choose-steps.upgrade-plan .pricing-tables .price-card .label-radio {
  &:after {
    top: 44px;
    left: 40px;
    @media #{$betweentabdesk} {
      top: 41px;
      left: 35px;
    }
  }
  &::before {
    top: 30px;
    left: 20px;
    @media #{$mobile} {
      top: 33px;
      left: 23px;
    }
  }
}

.choose-steps.upgrade-plan .pricing-tables .price-card.one {
  @media #{$tablet} {
    margin-top: 0;
    height: 100%;
  }
}

.choose-steps.upgrade-plan .pricing-tables .price-card.three, .choose-steps.upgrade-plan .pricing-tables .price-card.four {
  @media #{$tablet} {
    height: auto;
    margin: 35px auto 0 ;
  }
}
