.port-forwarding {
  background: #fff;
  padding-top: 155px;
  position: relative;
  .shape {
    position: absolute;
    right: 0;
    bottom: 0;
    @media #{$betweentabdesk} {
      width: 1150px;
    }
    @media #{$tablet} {
      width: 850px;
    }
    @media #{$mobile} {
      width: 1000px;
    }
  }
  @media #{$tablet} {
    padding-top: 175px;
  }
  @media #{$mobile} {
    padding-top: 135px;
    text-align: center;
  }
  h1 {
    font-size: 56px;
    line-height: 70px;
    margin-bottom: 22px;
    @media (min-width: 1200px) {
      margin-top: 100px;
    }
    @media #{$betweentabdesk} {
      font-size: 49px;
      line-height: 60px;
    }
    @media #{$tablet} {
      font-size: 42px;
      line-height: 52px;
      margin-bottom: 20px;
    }
    @media #{$mobile} {
      font-size: 32px;
      line-height: 42px;
    }
  }
  .port-forwarding-text {
    font-size: 22px;
    line-height: 32px;
    max-width: 510px;
    opacity: 0.8;

    @media #{$betweentabdesk} {
      font-size: 20px;
      line-height: 30px;
      max-width: 430px;
    }
    @media #{$maxTab} {
      font-size: 18px;
      line-height: 28px;
    }
    @media #{$mobile} {
      max-width: 320px;
      margin: 0 auto;
    }
  }
  .image {
    text-align: right;
    @media #{$mobile} {
      margin-top: 50px;
      text-align: center;
    }
    img {
      position: relative;
      top: 1px;
      width: 470px;
      @media #{$betweentabdesk} {
        width: 370px;
      }
      @media #{$mobile} {
        width: 260px;
      }
    }
  }
  .port-forwarding-button {
    display: inline-block;
    text-align: center;
    margin-top: 50px;
    @media #{$mobile} {
      display: block;
    }
    @media #{$tablet} {
      margin-top: 30px;
    }
    a {
      width: 300px;
      height: 77px;
      line-height: 77px;
      @media #{$betweentabdesk} {
        width: 270px;
        height: 66px;
        line-height: 66px;
        font-size: 18px;
      }
      @media #{$maxTab} {
        width: 100%;
        line-height: 64px;
        height: 64px;
        font-size: 16px;
      }
      @media #{$tablet} {
        width: 250px;
      }
    }
    p {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      margin-top: 14px;
      @media #{$maxTab} {
        margin-top: 10px;
        font-size: 14px;
      }
    }
  }
}

.portforwarding-meaning {
  padding: 55px 0;
  .content {
    h2 {
      max-width: 400px;
      margin-bottom: 30px;
      @media #{$tablet} {
        max-width: unset;
      }
      @media #{$maxTab} {
        margin-bottom: 20px;
      }
    }
    ul {
      li {
        position: relative;
        padding-left: 30px;
        @media #{$tablet} {
          padding-left: 22px;
          font-size: 14px;
        }
        &:not(:last-child) {
          margin-bottom: 10px;
        }
        &:before {
          content: "";
          position: absolute;
          width: 9px;
          height: 9px;
          border-radius: 50%;
          background-color: $primaryColor;
          left: 0;
          top: 13px;
          @media #{$maxTab} {
            top: 7px;
          }
        }
        @media #{$mobile} {
          padding-left: 23px;
        }
      }
    }
  }
  .image {
    @media #{$mobile} {
      margin-bottom: 40px;
    }
  }
}

.wevpn-devices.global {
  @media #{$maxTab} {
    padding-bottom: 60px;
  }
}

.open-ports {
  .content {
    p {
      margin-top: 30px;
      @media #{$tablet} {
        font-size: 14px;
      }
      @media #{$maxTab} {
        margin-top: 20px;
      }
    }
  }
  .image {
    text-align: right;
    @media #{$mobile} {
      margin-bottom: 50px;
      text-align: center;
    }
  }
}

.portforwarding-benefits {
  @media #{$mobile} {
    padding: 60px 0 30px;
  }
  h2 {
    margin-bottom: 70px;
    @media #{$maxTab} {
      margin-bottom: 40px;
    }
  }
  .benefit {
    background: #ffffff;
    box-shadow: 0px 4px 20px rgba(225, 233, 237, 0.5);
    border-radius: 20px;
    padding: 80px 35px 50px;
    text-align: center;
    width: 100%;
    @media #{$tablet} {
      padding: 50px 18px 30px;
    }
    @media #{$mobile} {
      margin-bottom: 30px;
      padding: 40px;
    }
    .image {
      width: 255px;
      height: 120px;
      line-height: 120px;
      text-align: center;
      margin: auto;
      img {
        max-width: 100%;
        height: auto;
        &.gaming {
          @media #{$tablet} {
            width: 100px;
          }
        }
      }
      @media #{$betweentabdesk} {
        width: 200px;
      }
      @media #{$tablet} {
        width: auto;
        transform: scale(0.85);
      }
      @media #{$mobile} {
        transform: scale(0.8);
      }
    }
    h3 {
      margin: 55px 0 20px;
      @media #{$maxTab} {
        margin: 5px 0 15px;
      }
    }
    p {
      @media #{$tablet} {
        font-size: 14px;
      }
    }
    &.last {
      p {
        max-width: 210px;
        margin: auto;
      }
    }
  }
}



// port forwading new page 
.port-forwarding-hero {
  padding-top: 145px;
  @media #{$betweentabdesk} {
    padding-top: 115px;
  }
  @media #{$maxTab} {
    padding-top: 55px;
  }
  .port-forwarding-text {
    margin: 20px 0 50px;
    font-size: 20px;
    @media #{$betweentabdesk} {
      font-size: 18px;
    }
    @media #{$tablet} {
      margin: 15px 0 30px;
      font-size: 14px;
    }
    @media #{$mobile} {
      margin: 20px 0 40px;
      font-size: 16px;
    }
  }
  .port-forwarding-button {
    a {
      line-height: 64px;
      width: 300px;
      @media #{$tablet} {
        font-size: 16px;
        width: 250px;
      }
      @media #{$mobile} {
        max-width: unset;
        width: 100%;
      }
    }
    p {
      font-size: 14px;
    }
  }
  .image {
    @media #{$mobile} {
      margin-top: 40px;
    }
    img {
      position: relative;
      top: 1px;
    }
  }
}