
.main-section {
  padding-top: 205px;
  padding-bottom: 170px;
  position: relative;
  overflow: hidden;
  @media #{$betweentabdesk} {
    padding-bottom: 107px;
  }
  @media #{$tablet} {
    padding-top: 175px;
    padding-bottom: 100px;
  }
  @media #{$mobile} {
    padding-top: 135px;
    padding-bottom: 0;
    text-align: center;
  }
  h1 {
    margin-bottom: 27px;
    @media #{$tablet} {
      margin-bottom: 20px;
    }
  }
  p {
    font-size: 24px;
    line-height: 34px;
    max-width: 460px;
    @media #{$tablet} {
      font-size: 18px;
      line-height: 28px;
    }
    @media #{$mobile} {
      font-size: 18px;
      line-height: 28px;
      max-width: 320px;
      margin: 0 auto 70px;
    }
  }
  .image {
    position: absolute;
    right: -40px;
    top: -100px;
    @media #{$betweentabdesk} {
      right: -10px;
      top: -80px;
      left: 80px;
    }
    @media #{$tablet} {
      top: -47px;
      left: 60px;
    }
    @media #{$mobile} {
      position: static;
      img {
        max-width: 300px;
      }
    }
  }
}

// Start Multi features
.multi-features {
  background: #fafafa;
  padding: 75px 0 85px;
  @media #{$mobile} {
    padding: 25px 0 60px;
  }
  .feat-row {
    &:not(:last-child) {
      border-bottom: 1px solid #e3ebef;
      @media #{$mobile} {
        border-bottom: 0;
      }
    }
  }
  .feat-box {
    display: flex;
    padding: 45px 0;
    position: relative;
    width: 100%;
    @media #{$mobile} {
      padding: 30px 0;
    }
    @media (min-width: 768px) and (max-width: 1199px) {
      padding: 30px 0;
      &.bord-right:after {
        content: "";
        position: absolute;
        height: 100%;
        width: 1px;
        background-color: #e3ebef;
        right: -15px;
        top: 0;
      }
    }
    &.bord-right {
      @media (min-width: 1200px) {
        border-right: 1px solid #e3ebef;
      }
    }
    @media #{$maxTab} {
      flex-direction: column;
      text-align: center;
    }
    @media #{$mobile} {
      border-bottom: 1px solid #e3ebef;
    }
    .image {
      display: flex;
      justify-content: center;
      min-width: 80px;
      @media (min-width: 1200px) {
        margin-right: 40px;
      }
      @media #{$betweentabdesk} {
        margin-right: 20px;
      }
      @media #{$mobile} {
        margin-bottom: 30px;
      }
      @media #{$tablet} {
        width: 85px;
        height: 130px;
        line-height: 130px;
        text-align: center;
        margin: 0 auto 20px;
      }
    }
    img {
      margin: auto;
    }
    .feat-text {
      h3 {
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        @media #{$maxTab} {
          justify-content: center;
        }
        .coming-soon {
          font-size: 11px;
          line-height: 16px;
          color: #FFFFFF;
          padding: 0 7px;
          background: #B3B3B3;
          border-radius: 3px;
          margin-left: 12px;
          font-weight: 500;
        }
        @media #{$betweentabdesk} {
          font-size: 20px;
        }
        @media #{$maxTab} {
          font-size: 18px;
        }
      }
      p {
        font-size: 18px;
        line-height: 160%;
        max-width: 420px;
        @media #{$betweentabdesk} {
          font-size: 15px;
        }
        @media #{$maxTab} {
          font-size: 14px;
          line-height: 24px;
          max-width: unset;
        }
      }
    }
    &.last {
      @media #{$mobile} {
        padding-bottom: 0;
        margin-bottom: 0;
        border-bottom: 0;
      }
      p {
        max-width: unset;
        @media #{$tablet} {
          max-width: 530px;
          margin: auto;
        }
      }
    }
  }
}

.pick-plan.choose-steps {
  padding: 60px 0 80px;
  background-color: #fafafa;
  @media #{$mobile} {
    padding: 50px 0 70px;
  }
  .pick-plan-content {
    h2 {
      text-align: center;
      margin-bottom: 20px;
    }
  }
}
