.maintenance {
  padding: 100px 0;
  @media #{$mobile} {
    padding-top: 100px;
  }
  h2 {
    font-size: 36px;
    margin-bottom: 20px;
    margin-top: 60px;
    @media #{$mobile} {
      font-size: 30px;
    }
  }
  p {
    font-size: 20px;
    margin-bottom: 20px;
    @media #{$mobile} {
      font-size: 18px;
    }
  }
  ul {
    padding-left: 20px;
    li {
      list-style: disc;
      font-size: 20px;
      margin-bottom: 15px;
      @media #{$mobile} {
        font-size: 18px;
      }
    }
  }
  dl {
    padding-left: 20px;
    margin-top: 15px;
    li {
      list-style: circle;
    }
  }
}