body.order-success-page {
  background: #f4f6f7;
}

.confirmation {
  background-color: $primaryColor;
  color: #fff;
  padding-top: 150px;
  .thank-text {
    padding-bottom: 60px;
    @media #{$mobile} {
      padding-bottom: 28px;
    }
    @media #{$tablet} {
      padding-bottom: 45px;
    }
    h2 {
      font-size: 46px;
      line-height: 56px;
      margin-bottom: 10px;

      @media #{$mobile} {
        font-size: 32px;
        line-height: 38px;
        width: 80%;
        margin: auto;
      }

      @media #{$tablet} {
        font-size: 40px;
        line-height: 46px;
      }
    }
    p {
      font-weight: 500;
      font-size: 22px;
      line-height: 32px;
      color: rgba(255, 255, 255, 0.8);
      @media #{$tablet} {
        font-size: 20px;
      }
      @media #{$mobile} {
        font-size: 16px;
        line-height: 22px;
        margin-top: 12px;
      }
    }
  }

  @media #{$mobile} {
    padding-top: 114px;
  }

  @media #{$tablet} {
    padding-top: 130px;
    .container {
      max-width: 100%;
    }
  }
}

.devices-instructions {
  margin: 28px 0 56px;
  &-content {
    background: #ffffff;
    border-radius: 10px;
    padding: 30px;
    @media #{$mobile} {
      padding: 30px 20px;
    }
  }
  .device {
    border: 1px solid #e0ded5;
    border-radius: 10px;
    padding-top: 36px;
    padding-bottom: 36px;
    margin-bottom: 30px;
    @media #{$mobile} {
      padding-top: 28px;
      padding-bottom: 24px;
      margin-bottom: 20px;
    }
  }

  .image {
    width: 250px;
    height: 180px;
    display: flex;
    margin: auto;
    @media #{$betweentabdesk} {
      width: 235px;
    }
    @media #{$mobile} {
      width: 235px;
      height: 140px;
    }
    img {
      width: 100%;
      margin: auto;
      @media #{$mobile} {
        width: auto;
        max-width: 100%;
        display: block;
        height: 140px;
      }
    }
  }
  h3 {
    font-size: 24px;
    line-height: 56px;
    margin-bottom: 10px;
    @media (max-width: 1199px) {
      font-size: 22px;
      line-height: 27px;
      margin: 15px 0;
    }
  }
  .instuctions-btn {
    text-align: right;
    padding: 0 24px;
    @media #{$mobile} {
      padding: 0 17px;
    }
    button {
      background: #ffecf1;
      border-radius: 3px;
      border: 0;
      width: 140px;
      height: 40px;
      line-height: 40px;
      @media #{$mobile} {
        display: block;
        width: 100%;
        font-size: 14px;
      }
      svg {
        margin-right: 5px;
        transition: all 0.3s ease-in-out;
        &.active {
          transform: rotate(180deg);
        }
      }
      span {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #fd3e72;
        @media #{$mobile} {
          font-size: 14px;
        }
      }
    }
  }
  .dashboard-btn {
    height: 70px;
    line-height: 70px;
    @media #{$maxTab} {
      height: 60px;
      line-height: 60px;
      margin-top: 10px;
      font-size: 16px;
    }
  }
  .help-text {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    line-height: 56px;
    margin-top: 14px;
    color: rgba(91, 88, 99, 0.8);
    @media #{$maxTab} {
      font-size: 16px;
      line-height: 36px;
    }
    a {
      color: rgba(91, 88, 99, 0.8);
      text-decoration: underline !important;
      transition: all 0.3s ease-in-out;
      &:hover {
        color: $primaryColor;
      }
    }
  }
  .setup-steps {
    border-top: 1px dashed #e3e0d3;
    padding-top: 25px;
    margin: 24px 24px 0;
    display: none;
    @media #{$mobile} {
      margin: 20px 18px 0;
      padding-top: 20px;
    }

    ul {
      li {
        font-weight: 600;
        font-size: 16px;
        line-height: 23px;
        margin-bottom: 15px;
        display: flex;
        flex-direction: column;
        @media #{$mobile} {
          font-size: 14px;
          line-height: 23px;
        }
        p {
          position: relative;
          padding-left: 40px;
          a {
            text-decoration-line: underline !important;
            color: #fd3e72;
          }
        }

        span {
          font-weight: 500;
          font-size: 24px;
          line-height: 23px;
          color: #fd3e72;
          position: absolute;
          left: 0;
          @media #{$mobile} {
            font-size: 24px;
            line-height: 23px;
          }
        }
        .download-options {
          display: flex;
          align-items: center;
          margin: 10px;
          @media #{$mobile} {
            margin: 10px 0;
          }

          .qr-code {
            @media #{$maxTab} {
              min-width: 100px;
              img {
                width: 100%;
                display: block;
              }
            }
          }
        }
        .android-btns {
          padding: 20px 0 20px 10px;
          a {
            display: block;
            font-weight: 500;
            font-size: 14px;
            color: #fcf9f2;
            width: 140px;
            height: 40px;
            line-height: 40px;
            min-height: 40px;
            &:first-child {
              margin-bottom: 10px;
            }
          }
        }
        .iphone-btn {
          margin-left: 10px;

          a {
            display: block;
          }
        }
        .chrome-btn {
          a {
            display: block;
          }
          margin-top: 10px;
          margin-left: 30px;
        }
      }
    }
  }
}
