/* Start login */
div.login-page,
div.contact-us {
  background-image: url(../imgs/login-bg.png);
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: 50% 150px;
  padding: 140px 0 80px;

  @media #{$mobile} {
    background-size: 120%;
    background-position: 50% 112px;
    padding: 120px 0 40px;
  }

  @media #{$tablet} {
    background-size: 110%;
  }

  @media #{$betweentabdesk} {
    background-size: 80%;
  }

  .padd {
    padding-right: 5px;
    padding-left: 5px;

    @media #{$mobile} {
      padding-right: 15px;
      padding-left: 15px;
    }

    @media #{$betweentabdesk} {
      padding-right: 0;
      padding-left: 0;
    }
  }

  h1 {
    margin-bottom: 40px;
    font-size: 36px;
    line-height: 56px;

    @media #{$mobile} {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 20px;
    }

    @media #{$tablet} {
      margin-bottom: 30px;
    }
  }

  .login-content,
  .contact-us-content {
    background: #ffffff;
    box-shadow: 0px 20px 45px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    padding: 80px 60px 60px;
    border-radius: 10px;

    @media #{$mobile} {
      padding: 24px;
    }

    @media #{$tablet} {
      padding: 50px;
    }

    @media #{$betweentabdesk} {
      padding: 60px 50px 50px;
    }

    label {
      margin-bottom: 4px;

      @media #{$mobile} {
        margin-bottom: 0;
        line-height: 1.5;
      }
    }

    // form:focus-within label {
    //   color: #8d8b92 !important;
    // }

    input,
    select,
    textarea {
      border: 0;
      outline: none;
      box-shadow: none;
      padding-left: 0;
      padding: 0;
      height: auto;
      font-weight: 500;

      @media #{$mobile} {
        font-size: 14px;
      }

      &::placeholder {
        font-size: 16px;
        line-height: 20px;
        font-weight: normal;

        @media #{$mobile} {
          font-size: 14px;
        }
      }
    }

    .form-group {
      border: 1px solid #e0ded5;
      padding: 15px 30px;
      margin-bottom: 20px;
      transition: all 0.3s ease-in-out;
      border-radius: 5px;

      &:hover {
        border: 1px solid #c7c4bd;
      }

      @media #{$mobile} {
        padding: 5px 16px 9px;
        margin-bottom: 8px;
      }

      label {
        font-size: 12px;
        font-family: "Averta";
        margin-bottom: 0;
      }
    }

    .forgot-password {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @media #{$mobile} {
        flex-direction: column;
      }

      a {
        font-size: 14px;
        line-height: 20px;
        color: $primaryColor;
        text-decoration: underline !important;
        transition: color 0.3s ease-in-out;

        @media #{$mobile} {
          order: 2;
          font-size: 12px;
        }

        &:hover {
          color: #e33866;
          text-decoration: none !important;
        }
      }

      button {
        padding: 13px 58px;

        @media #{$mobile} {
          font-size: 16px;
          width: 100%;
          display: block;
          padding: 16px 0;
          margin-bottom: 11px;
        }
      }
    }

    &.reset-password-page {
      padding: 60px;

      @media #{$mobile} {
        padding: 24px;
      }

      .reset {
        display: block;
        width: 100%;
        margin-bottom: 20px;
      }

      p {
        font-size: 13px;
        line-height: 14px;
        color: #8d8b92;
        text-align: center;

        a {
          font-size: 14px;
        }
      }
    }
  }

  .login-footer {
    text-align: center;
    margin-top: 30px;

    @media #{$mobile} {
      margin-top: 20px;
    }

    p {
      font-size: 14px;
      line-height: 17px;
      color: #8d8b92;
    }
  }

  &.reset-password-page {
    @media #{$mobile} {
      background-position-y: 69%;
    }

    @media (min-width: 768px) {
      background-position-y: 150px;
    }
  }
}

/* End login */

.login2fa-page {
  .login-content {
    p {
      font-size: 16px;
      line-height: 28px;
      width: 90%;
      margin-bottom: 45px;
      @media #{$tablet} {
        width: 100%;
        font-size: 15px;
      }
      @media #{$mobile} {
        width: 99%;
        text-align: center;
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 24px;
        margin-top: 10px;
      }
    }
    .validate-fa {
      display: flex;
      flex-direction: column;
      input {
        background: #ffffff;
        border: 1px solid #e0ded5;
        height: 56px;
        line-height: 56px;
        margin-bottom: 12px;
        padding-left: 30px;
        @media #{$mobile} {
          font-size: 14px;
          padding-left: 16px;
          height: 44px;
          line-height: 44px;
          &::placeholder {
            font-size: 14px;
          }
        }
      }
      button {
        height: 56px;
        line-height: 56px;
        @media #{$mobile} {
          font-size: 16px;
          height: 44px;
          min-height: 44px;
          line-height: 44px;
        }
      }
    }
  }

  .login-footer {
    p {
      a {
        font-weight: normal;
      }
    }
  }
}
