.signin-banner,.contact-banner {
  padding-bottom: 300px;
}
.signin-content,.contact-us-content {
  margin-top: -165px;
  padding: 40px;
  margin-bottom: 95px;
  @media #{$maxTab} {
    margin-bottom: 55px;
    margin-top: -255px;
    padding: 30px 20px;
  }
  @media #{$betweentabdesk} {
    padding: 30px 20px;
  }

  h1 {
    margin-bottom: 10px;
    font-size: 38px;
    line-height: 140%;
    @media #{$mobile} {
      font-size: 28px;
      margin-bottom: 20px;
    }
    @media #{$tablet} {
      margin-bottom: 30px;
      font-size: 32px;
    }
  }
  .signin-desc,.contact-desc {
    margin-bottom: 36px;
    opacity: 0.8;
    @media (min-width: 768px) {
      max-width: 500px;
    }
    @media #{$tablet} {
      margin: 0 auto 36px;
    }
    @media #{$mobile} {
      font-size: 16px;
    }
  }

  input,
  select,
  textarea {
    border: 0;
    outline: none;
    box-shadow: none;
    padding-left: 0;
    padding: 0;
    height: auto;
    font-weight: 500;

    @media #{$mobile} {
      font-size: 14px;
    }

    &::placeholder {
      font-size: 16px;
      line-height: 20px;
      font-weight: normal;
      opacity: 0.4;
      @media #{$mobile} {
        font-size: 14px;
      }
    }
  }

  .form-group {
    border: 1px solid #e0ded5;
    padding: 15px 30px;
    margin-bottom: 20px;
    transition: all 0.3s ease-in-out;
    border-radius: 5px;
    overflow: hidden;

    &:hover {
      border: 1px solid #c7c4bd;
    }

    @media #{$mobile} {
      margin-bottom: 10px;
      padding: 15px 20px;
    }

    label {
      font-size: 12px;
      font-family: "Averta";
      margin-bottom: 4px;
      font-weight: 500;
      color: #8D8B92;
      display: block;
      @media #{$mobile} {
        margin-bottom: 0;
        line-height: 1.5;
      }
    }
  }

  .forgot-password {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media #{$mobile} {
      flex-direction: column;
    }
    a,button {
      width: 48%;
      line-height: 64px;
      @media #{$mobile} {
        width: 100%;
      }
    }
    .signin-btn {
      @media #{$mobile} {
        margin: 10px 0 15px;
      }

    }
    .forgot-password-btn {
      border: 1px solid $primaryColor;
      border-radius: 5px;
      text-align: center;
      color: $primaryColor;
      font-size: 20px;
      @media #{$mobile} {
        font-size: 16px;;
      }
    }
  }
}
