.about_us_banner {
  background: linear-gradient(116.02deg, #fe6556 -8.45%, #fd3e72 53.73%, #49a8ff 130.16%);
  padding: 160px 0 85px;
  color: #fff;
  @media #{$mobile} {
    text-align: center;
    padding: 120px 0 65px;
  }
  h1 {
    font-size: 47px;
    line-height: 140%;
    @media #{$tablet} {
      font-size: 42px;
    }
    @media #{$mobile} {
      font-size: 38px;
      margin-top: 40px;
    }
  }
  p {
    font-size: 22px;
    line-height: 140%;
    margin-top: 22px;
    @media #{$tablet} {
      font-size: 18px;
    }
    @media #{$mobile} {
      font-size: 16px;
    }
  }
}

.wevpn_privacy {
  padding: 70px 0;
  @media #{$betweentabdesk} {
    h2 {
      font-size: 31px;
    }
  }
  @media #{$tablet} {
    h2 {
      font-size: 27px;
    }
  }
  @media #{$mobile} {
    text-align: center;
    img {
      margin-bottom: 30px;
      max-width: 85%;
    }
    h2 {
      line-height: 30px;
    }
  }
  
  p {
    font-size: 20px;
    line-height: 140%;
    opacity: 0.7;
    margin: 35px 0;
    @media #{$tablet} {
      font-size: 18px;
      margin: 25px 0;
    }
    @media #{$mobile} {
      margin: 20px 0;
      font-size: 16px;
    }
  }
  h4 {
    color: $primaryColor;
    font-size: 22px;
    @media #{$tablet} {
      font-size: 18px;
    }
    @media #{$mobile} {
      font-size: 16px;
    }
  }
}

.press-roadmap.timeline-new {
  position: relative;
  padding: 85px 0 210px;
  @media #{$tablet} {
    padding: 80px 0;
  }
  @media #{$mobile} {
    padding: 60px 0 80px;
  }
  &::after {
    content: "";
    position: absolute;
    background-image: url(../imgs/press/timeline-shape.svg);
    width: 100%;
    height: 450px;
    top: 185px;
    left: 0;
    background-repeat: no-repeat;
    background-position: center;
    @media (min-width: 768px) and (max-width: 1200px) {
      display: none;
    }
    @media #{$mobile} {
      background-image: url(../imgs/press/timeline-shape-m.svg);
      top: 27px;
      height: 100%;
    }
  }

  .timeline_new {
    position: relative;
    display: flex;
    justify-content: space-between;
    z-index: 2;
    flex-wrap: wrap;
    @media #{$betweentabdesk} {
      display: block;
      &:before {
        content: " ";
        position: absolute;
        background: #ebebeb;
        height: 4px;
        bottom: 25px;
        left: 100px;
        width: 78%;
      }
    }
    @media #{$tablet} {
      display: block;
      &:before {
        content: " ";
        position: absolute;
        background: #ebebeb;
        bottom: 27px;
        width: 4px;
        height: 75%;
        left: 200px;
        top: 105px;
      }
    }
    @media #{$mobile} {
      flex-direction: column;
    }
    .timeline-item {
      display: flex;
      min-width: 240px;
      max-width: 270px;
      position: relative;
      min-height: 320px;
      @media #{$betweentabdesk} {
        display: table-cell;
        padding-right: 20px;
        max-width: 300px;
      }
      @media #{$tablet} {
        max-width: none;
        min-width: auto;
        justify-content: space-between;
        align-items: center;
        min-height: auto;
      }
      @media #{$mobile} {
        margin: auto;
        padding: 50px 0 30px;
        min-width: unset;
        min-height: unset;
      }
      .timeline-badge {
        display: none;
        @media #{$betweentabdesk} {
          display: block;
          width: 32px;
          height: 32px;
          line-height: 32px;
          text-align: center;
          position: absolute;
          left: 50%;
          margin-left: -26px;
          border-radius: 50%;
          bottom: 11px;
          background: #f4f4f4;
          border: 2px solid #fd3e72;
          &:after {
            content: "";
            position: absolute;
            width: 18px;
            height: 18px;
            background-color: #fd3e72;
            border-radius: 50%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
        @media #{$tablet} {
          display: block;
          width: 32px;
          height: 32px;
          line-height: 32px;
          text-align: center;
          border-radius: 50%;
          background: #f4f4f4;
          border: 2px solid $primaryColor;
          margin-left: 0;
          position: relative;
          left: 0;
          bottom: 0;
          order: 2;
          &:after {
            content: "";
            position: absolute;
            width: 18px;
            height: 18px;
            background-color: $primaryColor;
            border-radius: 50%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
      .timeline-panel {
        position: relative;
        padding: 0 11px;
        bottom: 0;
        width: 100%;
        @media #{$betweentabdesk} {
          position: relative;
          background: #ffffff;
          border-radius: 10px;
          padding: 15px;
          bottom: 0;
          display: inline-block;
          width: 100%;
          margin-bottom: 20px;
          box-shadow: 0px 4px 20px rgba(234, 228, 228, 0.5);
          &:before {
            position: absolute;
            bottom: -12px;
            left: 50%;
            display: inline-block;
            content: "";
            transform: translateX(-50%);
            border-right: 16px solid transparent;
            border-top: 16px solid #fff;
            border-bottom: 0 solid #fff;
            border-left: 16px solid transparent;
          }
        }
        @media #{$tablet} {
          background: #ffffff;
          border-radius: 10px;
          padding: 15px;
          display: inline-block;
          margin-bottom: 20px;
          box-shadow: 0px 4px 20px rgba(234, 228, 228, 0.5);
          max-width: 440px;
          order: 3;
          &:before {
            content: " ";
            position: absolute;
            display: inline-block;
            left: -30px;
            top: 55%;
            transform: translateY(-50%);
            bottom: auto;
            border-right: 16px solid #fff;
            border-top: 16px solid transparent;
            border-bottom: 16px solid transparent;
            border-left: 16px solid transparent;
          }
        }
        @media #{$mobile} {
          align-self: flex-start !important;
          padding: 0;
        }
        &.time_1 {
          align-self: flex-start;
        }
        &.time_2 {
          align-self: flex-end;
          & + .time {
            bottom: auto;
            top: -6px;
            left: 44%;
            @media #{$betweentabdesk} {
              bottom: -80px;
              left: 0;
              top: auto;
            }
            @media #{$tablet} {
              top: 0;
              left: 0;
            }
            @media #{$mobile} {
              top: 0;
              left: 50%;
            }
          }
        }
        &.time_3 {
          align-self: center;
          & + .time {
            bottom: -42px;
            left: 106px;
            @media #{$betweentabdesk} {
              bottom: -80px;
              left: 0;
            }
            @media #{$tablet} {
              bottom: 0;
              left: 0;
            }
            @media #{$mobile} {
              top: -8px;
              left: 50%;
            }
          }
        }
        &.time_4 {
          align-self: flex-end;
          & + .time {
            top: 26px;
            bottom: auto;
            left: 104px;
            @media #{$betweentabdesk} {
              bottom: -80px;
              left: 0;
              top: auto;
            }
            @media #{$tablet} {
              top: 0;
              left: 0;
            }
            @media #{$mobile} {
              top: -15px;
              left: 50%;
            }
          }
        }
        li {
          line-height: 24px;
          position: relative;
          padding-left: 20px;
          margin-bottom: 5px;
          @media #{$betweentabdesk} {
            font-size: 14px;
            margin-bottom: 2px;
            padding-left: 15px;
          }
          @media #{$mobile} {
            margin-bottom: 8px;
          }
          &:before {
            content: "";
            position: absolute;
            top: 10px;
            left: 0;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: $primaryColor;
            @media #{$mobile} {
              top: 7px;
            }
          }
        }
      }

      .time {
        background: #fff;
        color: $primaryColor;
        width: 96px;
        height: 96px;
        font-weight: bold;
        font-size: 18px;
        text-align: center;
        bottom: -97px;
        position: absolute;
        border-radius: 50%;
        left: 50%;
        transform: translateX(-50%);
        padding: 23px 5px;
        box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.08);
        @media #{$betweentabdesk} {
          background: #fd3e72;
          color: #ffffff;
          width: 150px;
          height: 55px;
          line-height: 55px;
          border-radius: 10px;
          margin: auto;
          bottom: -80px;
          position: relative;
          padding: 0;
          left: 0;
          font-size: 16px;
          transform: translateX(0);
          &::before {
            position: absolute;
            top: -12px;
            left: 50%;
            display: inline-block;
            content: "";
            transform: translateX(-50%);
            border-right: 16px solid transparent;
            border-top: 0 solid #fd3e72;
            border-bottom: 16px solid #fd3e72;
            border-left: 16px solid transparent;
          }
        }
        @media #{$tablet} {
          background: #fd3e72;
          color: #ffffff;
          width: 154px;
          height: 63px;
          line-height: 63px;
          border-radius: 10px;
          position: relative;
          bottom: 0;
          order: 1;
          padding: 0;
          left: 0;
          transform: translateX(0);
          box-shadow: none;
          &:after {
            position: absolute;
            display: inline-block;
            content: " ";
            right: -26px;
            top: 50%;
            transform: translateY(-50%);
            border-right: 16px solid transparent;
            border-top: 16px solid transparent;
            border-bottom: 16px solid transparent;
            border-left: 16px solid #fd3e72;
          }
        }
        @media #{$mobile} {
          width: 150px;
          height: 36px;
          box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.06);
          border-radius: 5px;
          top: -10px;
          padding: 0;
          line-height: 36px;
        }
        &.last {
          padding: 0;
          @media (min-width: 1200px) {
            line-height: 96px;
          }
        }
      }
    }
  }
}

.press-assets-new {
  background: #fafafa;
  overflow: hidden;
  @media #{$maxTab} {
    .container {
      padding: 0;
      max-width: 100%;
    }
  }

  .press-assets-header {
    background: linear-gradient(92.7deg, #fe6556 -30.72%, #fd3e72 54.41%, #49a8ff 139.68%);
    padding: 40px 0;
    @media (max-width: 1200px) {
      padding: 32px 0;
    }
    .press-assets-header-content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media #{$tablet} {
        padding: 0 20px;
      }
      @media #{$mobile} {
        flex-direction: column;
        text-align: center;
        justify-content: center;
      }
    }

    h2 {
      color: #fff;
      @media #{$mobile} {
        margin-bottom: 18px;
      }
    }
    .press-assets-btn {
      padding: 24px 40px;
      border: 0;
      @media #{$betweentabdesk} {
        padding: 18px 27px;
      }
      @media #{$tablet} {
        padding: 15px 30px;
        font-size: 16px;
      }
      @media #{$mobile} {
        padding: 10px 23px;
        font-size: 14px;
      }

      img {
        margin-left: 8px;
        position: relative;
        top: -1px;
      }
    }
  }
  .press-assets-content {
    padding: 60px 0;
    @media #{$tablet} {
      padding: 50px 0;
    }
    @media #{$mobile} {
      padding: 32px 0 50px;
    }
    .logo-section,
    .media-section {
      display: flex;
      @media #{$maxTab} {
        display: block;
      }
    }
    .press-assets-logo {
      border-right: 1px solid #ece4e4;
      min-width: 270px;
      margin-right: 30px;
      @media #{$maxTab} {
        min-width: unset;
        border: 0;
        margin-right: 0;
        padding: 0 20px;
      }
      &.media-assets {
        .dropdown {
          .btn {
            width: 150px !important;
          }
        }
      }
      .logo-assets-header {
        @media #{$maxTab} {
          display: flex;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid #eeeeee;
          padding-bottom: 12px;
        }
        h3 {
          font-size: 28px;
          line-height: 35px;
          margin-bottom: 22px;
          @media #{$maxTab} {
            font-size: 20px;
            line-height: 30px;
            margin-bottom: 0;
          }
          @media #{$tablet} {
            font-size: 24px;
          }
        }
        ul {
          @media (min-width: 768px) and (max-width: 1200px) {
            margin-top: 30px;
          }
          @media #{$mobile} {
          }
          li {
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            transition: 0.3s;
            border: 2px solid transparent;
            cursor: pointer;
            padding: 8px 5px;
            @media #{$tablet} {
              margin-right: 15px;
              font-size: 15px;
            }
            cursor: pointer;
            &.active {
              position: relative;
              color: $primaryColor;
              border-bottom: 2px solid #fd3e72;
            }
          }
        }
        .dropdown {
          .dropdown-menu {
            min-width: 130px;
            padding: 0;
            background-color: #fd3e72;
            color: #fff;
            border-top: 0;
            margin: 0;
            &.last {
              min-width: 150px;
            }
            li {
              font-size: 14px;
              padding: 5px 20px;
              color: #fff;
              padding: 0.5rem 1rem;
              opacity: 1;
              margin-right: 0;
              display: block;
              &:not(:last-of-type) {
                border-bottom: 1px solid #f80d4c;
              }
              &.active {
                background-color: #f80d4c;
                &:after {
                  display: none;
                }
              }
            }
          }
          .btn {
            background-color: $primaryColor;
            color: #fff;
            border-radius: 5px;
            font-size: 14px;
            box-shadow: none;
            outline: none;
            padding: 7px 15px;
            width: 130px;
            position: relative;
            text-align: left;
            &:after {
              position: absolute;
              right: 10px;
              top: 50%;
              transform: translateY(-50%);
            }
          }
        }
      }
      .white_btn {
        font-size: 16px;
        padding: 14px 18px;
        border: 1px solid #fd3e72;
        margin-top: 55px;
        width: 240px;
        text-align: center;
      }
    }
    .press-assets-logo-content {
      padding-bottom: 50px;
      @media #{$maxTab} {
        margin-top: 20px;
        padding-bottom: 30px;
      }
      .logo-wordmark,
      .logo-symbol,
      .box {
        @media #{$maxTab} {
          .row.sm-scrolled,
          .row.box-sm-scrolled {
            width: auto;
            margin-left: 0;
            margin-right: 0px;
            padding: 0 40px;
            flex-wrap: nowrap;
            overflow: auto;
            .col-4 ,.col-md-6 {
              padding: 0;
              max-width: 100%;
              .item {
                margin-right: 25px;
              }
            }
          }
        }
        @media #{$betweentabdesk} {
          .row.sm-scrolled,
          .row.box-sm-scrolled {
            .col-4,.col-6 {
              max-width: 100%;
            }
          }
        }

        @media #{$maxTab} {
          .row.sm-scrolled,
          .row.box-sm-scrolled {
            padding: 0 20px;
          }
        }
      }
      > div:not(:first-child) {
        display: none;
      }
      &.media-assets {
        padding-bottom: 0;
        width: 100%;
        .h-180 {
          height: 180px;
        }
        .w-180 {
          width: 180px;
        }
        @media #{$maxTab} {
          padding-bottom: 0;
        }
        > div:not(:first-child) {
          display: none;
          width: 100%;
        }
      }
      .item {
        background-color: #fff;
        width: 270px !important;
        box-shadow: 0px 3px 17px rgba(77, 77, 77, 0.08);
        border-radius: 10px;
        padding: 10px;
        margin-bottom: 30px;
        &-top {
          background-image: url(../imgs/pattern.jpg);
          background-repeat: repeat;
          height: 220px;
          border-radius: 6px 6px 0 0;
          display: flex;
          padding: 0 10px;
          img {
            margin: auto;
            width: auto;
            max-width: 100%;
            &.white_logo {
              filter: drop-shadow(0px 14px 74px rgba(0, 0, 0, 0.35));
            }
          }
        }
        &-bottom {
          h4 {
            font-weight: bold;
            font-size: 16px;
            line-height: 20px;
            padding: 17px 0 17px 2px;
          }
          &-download {
            padding: 12px 0px 2px;
            border-top: 1px solid #ebe8df;
            display: flex;
            justify-content: space-between;
            align-items: center;
            span {
              font-size: 16px;
              line-height: 28px;
              opacity: 0.7;
            }
            a {
              font-weight: bold;
              font-size: 14px;
              line-height: 16px;
              border-radius: 100px;
              padding: 9px 17px;
              display: inline-block;
              color: #fd3e72;
              border: 1px solid #fd3e72;
            }
          }
        }
      }
    }
  }
}


