      }
      @media #{$tablet} {
        font-size: 30px;
      }
      &:before {
        content: "";
        width: 6px;
        height: 40px;
        background-color: $primaryColor;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        @media #{$maxTab} {
          width: 4px;
          height: 27px;
        }
      }
    }
  }
  .team-member {
    background: #FFFFFF;
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    padding: 20px 5px 20px 20px;
    @media #{$mobile} {
      border-radius: 10px;
    }
    &:not(:last-child) {
      margin-bottom: 15px;
    }
    h3 {
      font-size: 32px;
      font-weight: bold;
      line-height: 140%;
      @media (max-width: 1199px) {
        font-size: 24px;
      }
      @media #{$mobile} {
        font-size: 20px;
      }
      span {
        font-size: 24px;
        font-weight: normal;
        @media (max-width: 1199px) {
          font-size: 19px;
        }
        @media #{$mobile} {
          font-size: 18px;
          display: block;
          font-weight: 500;
        }
      }
      .divider {
        margin: 0 6px;
        display: inline-block;
        @media #{$mobile} {
          display: none;
        }
      }
    }
    .exp {
      display: flex;
      margin-top: 10px;
      flex-wrap: wrap;
      @media (max-width: 1199px) {
        margin-top: 5px;
      }
      p {
        padding-left: 15px;
        position: relative;
        flex: 1 1 50%;
        @media #{$betweentabdesk} {
          flex: 1 1 100%;
        }
        @media (max-width: 1199px) {
          font-size: 16px;
        }
        @media #{$mobile} {
          flex: 1 1 100%;
          font-size: 14px;
          &:not(:last-child) {
            margin-bottom: 5px;
          }
        }
        &::before {
          position: absolute;
          content: "";
          width: 7px;
          height: 7px;
          border-radius: 50%;
          background-color: $primaryColor;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          @media #{$mobile} {
            width: 5px;
            height: 5px;
          }
        }
      }
    }
  }
  img {
    @media #{$maxTab} {
      margin-bottom: 30px;
    }
  }
}

