header.wedns-page {
  background-color: #faf0fe;
}

.hero-img.wedns-page {
  background-color: #faf0fe;
  padding: 120px 0 33px;
  @media #{$tablet} {
    padding: 120px 0 55px;
  }
  .hero-img-content {
    @media #{$tablet} {
      text-align: center;
    }
    .hero-img-title {
      @media (min-width: 1024px) {
        margin: 0;
        font-size: 48px;
        line-height: 48px;
        margin-bottom: 32px;
      }
      @media #{$mobile} {
        margin: 30px auto 17px;
        width: 100%;
      }
    }
    .hero-img-text {
      font-size: 18px;
      line-height: 28px;
      @media #{$mobile} {
        font-size: 16px;
        line-height: 24px;
      }
    }
    .hero-img-btn {
      @media (min-width: 1024px) {
        margin-top: 40px;
      }
    }
  }
}

// Start Wedns Security
.security-center {
  background-color: #fff;
  padding: 120px 0 50px;
  @media #{$betweentabdesk} {
    padding: 80px 0 50px;
  }
  @media #{$maxTab} {
    padding: 60px 0;
  }
  .image {
    &.first {
      margin-bottom: 50px;
      @media #{$betweentabdesk} {
        margin-top: 20px;
      }
      @media #{$maxTab} {
        margin-bottom: 0;
      }
    }
    &.second {
      @media #{$tablet} {
        margin-top: 50px;
      }
      @media #{$mobile} {
        margin-top: 30px;
      }
    }
  }
  .text {
    h3 {
      margin-bottom: 26px;
      @media #{$betweentabdesk} {
        font-size: 30px;
        margin-bottom: 20px;
      }
    }
    p {
      font-size: 18px;
      line-height: 28px;
      width: 85%;
      @media #{$betweentabdesk} {
        font-size: 16px;
        width: 100%;
      }
      @media #{$tablet} {
        font-size: 17px;
        width: 100%;
      }
      @media #{$mobile} {
        font-size: 16px;
        width: 100%;
      }
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
    a.security-btn {
      padding: 21px 41px;
      @media #{$mobile} {
        display: block;
      }
    }
  }
}
