.sst-hero-img {
  padding: 145px 0 90px;
  @media #{$maxTab} {
    padding: 115px 0 55px;
  }
  .sst-hero-text {
    margin: 20px 0 60px;
    font-size: 20px;
    @media #{$mobile} {
      font-size: 16px;
      margin: 20px 0 35px;
      padding: 0 10px;
    }
  }
  .sst-hero-btn {
    max-width: 260px;
    line-height: 64px;
    @media #{$mobile} {
      max-width: none;
    }
  }
  @media #{$maxTab} {
    .hero-img-image {
      margin-bottom: 45px;
    }
  }
}

.sst-ram-disk {
  padding: 100px 0;
  @media #{$maxTab} {
    padding: 60px 0;
  }
  .text {
    @media #{$maxTab} {
      margin-top: 40px;
    }
    @media (min-width: 1024px) {
      padding-left: 30px;
    }
    h2 {
      margin-bottom: 25px;
    }
    p {
      &:not(:last-child) {
        margin-bottom: 25px;
      }
    }
  }
}

.sst-server-automation {
  .server-automation-content {
    padding: 60px;
    box-shadow: 0px 4px 28px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    @media #{$betweentabdesk} {
      padding: 60px 40px;
    }
    @media #{$maxTab} {
      padding: 60px 20px;
    }
  }
  .server-automation-text {
    @media #{$maxTab} {
      margin-top: 40px;
    }
    @media #{$tablet} {
      text-align: center;
    }
    h2 {
      margin-bottom: 20px;
    }
    p {
      &:not(:last-child) {
        margin-bottom: 30px;
        @media #{$maxTab} {
          margin-bottom: 20px;
        }
      }
    }
  }
}

.sst-linux-encryption {
  padding: 300px 0 100px;
  margin-top: -200px;
  @media #{$maxTab} {
    margin-top: -240px;
    padding: 300px 0 60px;
  }
  .text {
    @media (min-width: 1024px) {
      padding-left: 30px;
    }
    @media #{$maxTab} {
      margin-top: 40px;
    }
    p {
      margin: 30px 0 60px;
      @media #{$maxTab} {
        margin: 20px 0 40px;
      }
    }
  }
  .sst-btn {
    line-height: 64px;
    max-width: 260px;
    @media #{$mobile} {
      max-width: none;
    }
  }
}