/* Start Hero Image */

.hero-img {
  @media #{$mobile} {
    text-align: center;
  }

  &-content {
    position: relative;
    .hero-img-title {
      font-size: 80px;
      line-height: 88px;
      margin: 100px 0 20px;

      @media #{$mobile} {
        font-size: 36px;
        line-height: 44px;
        width: 85%;
        margin: 20px auto 17px;
      }

      @media #{$tablet} {
        font-size: 45px;
        line-height: 1.3;
        margin: 50px auto 17px;
      }

      @media #{$betweentabdesk} {
        font-size: 55px;
        line-height: 65px;
        margin: 70px 0 20px;
      }
    }

    .hero-img-txt {
      font-size: 22px;
      line-height: 33px;

      @media #{$maxTab} {
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 0;
      }

      @media #{$betweentabdesk} {
        font-size: 24px;
        line-height: 24px;
      }
    }
    .hero-button {
      display: block;
      text-align: center;
      @media (min-width: 992px) {
        display: inline-block;
      }
      > span {
        display: block;
        font-size: 14px;
        line-height: 16px;
        color: #33303f;
        opacity: 0.6;
        margin-top: 10px;

        @media #{$mobile} {
          i {
            font-size: 10px;
          }
        }
      }
    }
    .hero-button {
      @media #{$mobile} {
        width: 100%;
