/* End Hero Image */

/* Start Features */
.features {
  position: relative;

  @media #{$mobile} {
    padding-top: 35px;
    padding-bottom: 60px;
  }

  .features-content {
    position: absolute;
    right: 15px;
    left: 15px;
    top: -221px;
    @media #{$mobile} {
      position: static;
    }
    @media #{$tablet} {
      top: -140px;
    }
    @media #{$betweentabdesk} {
      right: 30px;
      left: 30px;
      top: -190px;
    }

    .features-items {
      background: #ffffff;
      box-shadow: 0px 80px 99px rgba(235, 233, 228, 0.8), 0px 6px 9px #e6e4df;
      border-radius: 30px;
      display: flex;
      flex-wrap: wrap;
    }

    .feat {
      padding: 85px;
      transition: all 0.3s ease-in-out;
      flex-basis: 33.33%;
      @media #{$mobile} {
        flex-basis: 100%;
      }

      &:not(:last-child) {
        border-right: 1px solid #ebe8df;
        @media #{$mobile} {
          border-right: 0;
          border-bottom: 1px solid #ebe8df;
        }
      }

      @media #{$maxTab} {
        padding: 33px;
      }

      @media #{$betweentabdesk} {
        padding: 60px 40px;
      }

      &-title {
        font-size: 20px;
        line-height: 20px;
        margin-bottom: 20px;
        margin-top: 40px;

        @media #{$maxTab} {
          font-size: 16px;
          line-height: 16px;
          margin-bottom: 12px;
          margin-top: 30px;
        }
      }

      &-txt {
        font-size: 16px;
        line-height: 28px;

        @media #{$maxTab} {
          font-size: 14px;
          line-height: 24px;
        }
      }
    }
  }
}

/* End Features */

.press-advantages.homepage {
  .feat-icon {
    width: 64px;
    height: 64px;
    margin: auto;
    line-height: 64px;
  }
  .advantage .advantage-text p {
    width: 73%;
    @media #{$tablet} {
      width: 84%;
    }
  }
  .advantage .advantage-text h4 {
    @media #{$tablet} {
      font-size: 16px;
    }
  }
}

/* Start advantages */
.advantages {
  padding: 100px 0;
  &.homepage-advantages {
    box-shadow: inset 0px -1px 0px #eae8e0;
    padding-bottom: 200px;
    @media #{$tablet} {
      padding-bottom: 140px;
    }
  }
  @media #{$maxTab} {
    padding: 60px 0;
  }

  .section-one,
  .section-two,
  .section-three {
    padding-right: 15px;
    padding-left: 15px;

    @media #{$mobile} {
      padding-left: 0;
      padding-right: 0;
    }

    .advantages-content {
      @media #{$mobile} {
        padding-left: 10px;
      }

      .advantages-title {
        font-size: 36px;
        line-height: 44px;
        width: 85%;

        @media #{$mobile} {
          font-size: 24px;
          line-height: 28px;
          width: 80%;
          margin-bottom: 0;
        }

        @media #{$tablet} {
          font-size: 30px;
          line-height: 36px;
          width: 100%;
        }
        @media #{$betweentabdesk} {
          width: 85%;
        }
      }

      .advantages-desc {
        font-size: 18px;
        line-height: 28px;
        margin-top: 26px;
        margin-bottom: 40px;
        width: 100%;

        @media #{$maxTab} {
          font-size: 16px;
          line-height: 28px;
          width: 100%;
          margin-top: 24px;
          margin-bottom: 21px;
        }

        @media #{$tablet} {
          width: 100%;
        }

        @media #{$betweentabdesk} {
          width: 90%;
        }
      }

      .advantages-btn {
        padding: 21px 43px;
        @media #{$tablet} {
          padding: 15px 43px;
          font-size: 18px;
        }
      }
    }

    .advantages-img {
      @media #{$mobile} {
        margin-bottom: 30px;
      }
      img {
        max-width: 100%;
      }
    }
  }

  .section-two {
    padding-top: 50px;
    .advantages-content {
      .advantages-title {
        width: 85%;

        @media #{$mobile} {
          width: 90%;
        }

        @media #{$tablet} {
          width: 100%;
        }
      }
      .advantages-desc {
        margin-bottom: 0;
      }
    }

    .advantages-img {
      text-align: center;

      img {
        max-width: 100%;
      }
    }
  }

  .section-three {
    padding-top: 100px;

    @media #{$maxTab} {
      padding-top: 50px;
    }

    .advantages-content {
      .advantages-title {
        width: 80%;

        @media #{$tablet} {
          width: 90%;
        }
      }

      .advantages-desc {
        @media #{$mobile} {
          margin-bottom: 26px;
        }
      }
    }

    .advantages-img {
      img {
        max-width: 100%;
      }
    }

    .advantages-icons {
      margin-bottom: 40px;

      @media #{$maxTab} {
        margin-bottom: 24px;
      }

      li {
        display: inline-block;
        margin: 0 3px;
        width: 28px;
        height: 28px;

        @media #{$mobile} {
          margin: 0;
          margin-right: 9px;
          width: 25px;
          height: 25px;
        }

        @media #{$tablet} {
          margin: 0 2px;
        }

        a {
          display: block;

          &:hover {
            svg {
              path {
                fill: $primaryColor;
              }
            }

            .firefox {
              .line {
                stroke: $primaryColor;
                fill: none;
              }

              .fox {
                fill: $primaryColor;
              }
            }
          }

          svg {
            transform: scale(0.6);

            path {
              transition: all 0.3s ease-in-out;
            }
          }
        }
      }
    }
  }
}

/* End advantages */

.discord {
  &__widget {
    background-color: #697fc9;
    position: absolute;
    top: -100px;
    border-radius: 30px;
    display: flex;
    left: 15px;
    right: 15px;
    @media (min-width: 768px) and (max-width: 1199px) {
      top: -68px;
    }
    @media #{$mobile} {
      flex-direction: column;
      top: -122px;
    }
    &__text {
      flex-basis: 50%;
      background: #7289da;
      border-radius: 30px 0px 97px 30px;
      padding: 55px 75px;
      font-size: 32px;
      line-height: 44px;
      color: #f9f8f4;
      font-weight: 500;
      @media (min-width: 768px) and (max-width: 1199px) {
        padding: 40px 50px;
        font-size: 24px;
        line-height: 28px;
      }
      @media #{$mobile} {
        padding: 33px 40px;
        border-radius: 30px;
        font-size: 24px;
        line-height: 28px;
        text-align: center;
      }
    }
    &__link {
      flex-basis: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      a {
        transition: all 0.3s ease-in-out;
        &:hover {
          box-shadow: 0px 6px 9px #5769a6;
        }
      }

      @media (min-width: 768px) and (max-width: 1199px) {
        img {
          width: 230px;
        }
      }
      @media #{$mobile} {
        padding: 17px 0;
        img {
          transform: scale(0.72);
        }
      }
    }
  }
}

.trustbox {
  padding: 50px 0;
  @media #{$mobile} {
    background-color: #fff;
  }
}

/* Start Customers Features */
.customers-features {
  background-color: #f9f8f4;
  padding: 0;

  @media #{$tablet} {
    padding: 0;
  }

  @media #{$mobile} {
    padding-bottom: 40px;
  }
  .container {
    position: relative;
  }

  .customers-features-items {
    position: absolute;
    top: -390px;
    left: 15px;
    right: 15px;
    .row {
      > div {
        display: flex;
      }
    }
    @media #{$mobile} {
      position: static;
    }
    @media #{$tablet} {
      top: -260px;
      .row {
        .col-md-6 {
          &:first-child {
            padding-right: 10px;
          }
          &:last-child {
            padding-left: 10px;
          }
        }
      }
    }
  }

  .customer-feat {
    background-color: #fff;
    padding: 130px 60px;
    position: relative;
    box-shadow: 0px 20px 29px rgba(35, 35, 35, 0.2);
    border-radius: 20px;

    @media #{$mobile} {
      padding: 40px 32px 60px;
      margin-bottom: 20px;
      box-shadow: 0px 4px 14px rgba(186, 185, 181, 0.2);
    }

    @media #{$tablet} {
      padding: 32px;
      padding-top: 57px;
    }
    @media #{$betweentabdesk} {
      padding: 70px 60px;
    }

    img {
      @media #{$mobile} {
        transform: scale(0.63);
      }

      @media #{$tablet} {
        transform: scale(0.8);
      }
    }

    h2 {
      margin: 58px 0 26px;
      font-size: 32px;
      line-height: 42px;

      @media #{$mobile} {
        font-size: 24px;
        line-height: 28px;
        margin: 12px 0 16px;
      }

      @media #{$tablet} {
        font-size: 19px;
        line-height: 28px;
        margin: 20px 0;
      }
    }

    p {
      font-size: 16px;
      line-height: 28px;
      margin-bottom: 36px;

      @media #{$mobile} {
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 20px;
      }
    }

    a {
      padding: 21px 43px;

      &.secondary__btn {
        padding: 21px 41px;
      }

      @media #{$maxTab} {
        display: block;
        font-size: 16px;
      }
      @media #{$tablet} {
        padding: 21px 0 !important;
      }

      @media (max-width: 350px) {
        padding: 24px 20px;
      }
    }

    &.one {
      margin-right: 5px;

      @media #{$maxTab} {
        margin-right: 0;
      }
    }

    &.two {
      padding-top: 118px;
      margin-left: 5px;

      @media #{$betweentabdesk} {
        padding-top: 59px;
      }

      @media #{$mobile} {
        padding: 30px 32px 60px;
        margin-left: 0;
        margin-bottom: 0;
      }

      @media #{$tablet} {
        padding: 32px;
        margin-left: 0;
      }

      h2 {
        margin-top: 38px;

        @media #{$mobile} {
          margin-top: -5px;
        }

        @media #{$tablet} {
          margin-top: 13px;
        }
      }

      a {
        background-color: $primaryColor;
        color: #fff;

        &:hover {
          background-color: #e33866;
        }

        &:active {
          background-color: #ff5784;
        }
      }
    }
  }
}

/* End Customers Features */

.get-started-login {
  p {
    font-size: 14px;
    line-height: 17px;
    color: #5b5863;
    margin-bottom: 0;
    a:first-child {
      transition: all 0.3s ease-in-out;
      font-size: 16px;
      font-weight: 500;
      @media #{$maxTab} {
        font-size: 18px;
      }
      &:hover {
        color: $primaryColor;
      }
    }
    .my-account {
      background: #fefefe;
      border: 1px solid #e3e0d2;
      display: inline-block;
      border-radius: 5px;
      padding: 10px 15px;
      color: #fd3e72;
      font-weight: 500;
      margin-left: 12px;
      transition: all 0.3s ease-in-out;
      @media #{$maxTab} {
        display: block;
        margin-left: 0;
        padding: 13.5px;
        background-color: #fd3e72;
        color: #fff;
        text-align: center;
        svg {
          path {
            fill: #fff;
          }
        }
      }

      svg {
        transform: translate(-2px, -2px);

        path {
          transition: all 0.3s ease-in-out;
        }
      }
      &:hover {
        background-color: #fd3e72;
        color: #fff;
        border-color: #fd3e72;
        svg {
          path {
            fill: #fff;
          }
        }
      }
    }
  }
}

.get-started-login.color-bg-pages {
  p {
    a:first-child {
      color: #fff;
      @media #{$maxTab} {
        color: #5b5863;
        margin-top: 10px;
        display: inline-block;
      }

      &:hover {
        color: $primaryColor;
      }
    }
  }
}

