// transparency-report new page
.transparency-report-hero {
  padding: 150px 0;
  @media #{$maxTab} {
    padding: 50px 0 100px;
  }
  .report-content {
    h1 {
      @media #{$betweentabdesk} {
        font-size: 45px;
      }
    }
    h3 {
      @media #{$maxTab} {
        font-size: 18px;
        line-height: 160%;
      }
    }
  }
}

.report-update-info {
  margin-top: -60px;
  margin-bottom: 35px;
  @media #{$mobile} {
    margin-bottom: 25px;
  }
  .report-update-content {
    padding: 30px 40px;
    @media #{$maxTab} {
      padding: 25px;
    }
    .date {
      @media #{$mobile} {
        &.first {
          border-bottom: 1px solid rgba(#E3D9DC, 0.4);
          padding-bottom: 20px;
          margin-bottom: 20px;
        }
        .icon {
          svg {
            width: 36px;
          }
        }
        p {
          font-size: 14px;
        }
      }
      @media(max-width: 1199px) {
        h3 {
          font-size: 18px;
        }
      }
    }
  }

}

.transparency-report-data {
  padding-bottom: 60px;
  .transparency-table {
    padding: 24px 15px;
    background: #FFFFFF;
    box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
  }
  .table {
    td,th {
      border: 0;
      vertical-align: middle;
    }
    thead {
      tr {
        th {
          border: 0;
          font-size: 20px;
          text-transform: capitalize;
          vertical-align: middle;
          height: 60px;
          width: 24%;
          padding-left: 25px;
          @media #{$tablet} {
            font-size: 14px;
          }
          &:first-child {
            width: 28%;
            color: #5b5863;
            border-right: 1px solid rgba(#E3D9DC, 0.4);
          }
        }
      }
    }
    tbody {
      tr {
        &:nth-child(odd) {
          background: #FAFAFA;
        }
        td,th {
          vertical-align: middle;
          font-size: 18px;
          line-height: 20px;
          font-weight: 500;
          color: #5b5863;
          height: 60px;
          padding-left: 25px;
          @media #{$tablet} {
            font-size: 14px;
          }
          &:first-child {
            border-right: 1px solid rgba(#E3D9DC, 0.4);
          }
        }
      }
    }
  }
  
  .invoices {
    background: #FFFFFF;
    box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    h3 {
      padding: 15px;
      border-bottom: 1px solid rgba(#e3e0d3, 0.4);
    }
    > .cell {
      position: relative;
      line-height: 60px;
      padding: 0 18px;
      &:not(:last-child) {
        border-bottom: 1px solid rgba(#e3e0d3, 0.4);
      }

      > span {
        font-size: 16px;
        font-weight: 500;
      }

      .arrow {
        top: 22px;
        right: 28px;
      }

      &.active {
        border-bottom: 0;
        .arrow {
          span {
            background-color: #5b5863;

            &:first-of-type {
              transform: rotate(-45deg);
            }

            &:last-of-type {
              transform: rotate(45deg);
            }
          }
        }
      }
    }

    .toggle {
      background: #fafafa;
      box-shadow: inset 0px 4px 4px #f6f6f6;
      padding: 0 18px;
      display: none;

      .cell {
        padding: 0 12px;
        &:not(:last-child) {
          border-bottom: 1px solid #f4f4f4;
        }

        h6 {
          font-size: 14px;
          color: $primaryColor;
          font-weight: bold;
          line-height: 52px;
          span {
            color: #5a5761;
            font-weight: 400;
            float: right;
          }
        }
      }
    }
  }
}