.ios-tutorials.mac-tuts-page {
  @media (min-width: 1200px) {
    padding-top: 100px;
  }
}

.hero-img.ios-tuts-page.windows-tuts-page.mac-tuts-page .hero-img-image {
  left: 0;
  right: -20px;
  top: 0;
}

.ios-tutorials .ios-need-help.mac-tuts-page {
  margin-bottom: 50px;
  @media #{$mobile} {
    margin-bottom: 20px;
  }
}

.hero-img.ios-tuts-page.windows-tuts-page.mac-tuts-page {
  @media #{$betweentabdesk} {
    padding: 140px 0 100px;
  }
}

.hero-img.ios-tuts-page.mac-tuts-page .hero-img-content__title {
  @media #{$maxTab} {
    width: 100%;
  }
}

.system-requirements {
  p {
    line-height: 20px;
    text-align: center;
    color: #5b5863;
    opacity: 0.7;
    font-weight: 500;
    margin-bottom: 24px;
    @media #{$mobile} {
      font-size: 15px;
    }
  }
  &__list {
    background: #ffffff;
    border: 1px solid #ebe8df;
    box-shadow: 0px 10px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    border-radius: 10px;
    padding: 25px 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 55px;
    @media #{$mobile} {
      margin-bottom: 25px;
      padding: 25px 30px;
    }
    li {
      line-height: 32px;
      width: 31%;
      @media #{$tablet} {
        width: 50%;
      }
      @media #{$mobile} {
        width: 100%;
        line-height: 35px;
      }
      strong {
        font-weight: 500;
      }
    }
  }
}
