/* start weplay info */
.weplay-info {
  padding: 100px 0;

  @media #{$mobile} {
    padding: 60px 0;
    overflow: hidden;
  }

  @media #{$tablet} {
    padding: 60px 0 100px;
  }

  @media #{$betweentabdesk} {
    padding: 80px 0;
  }

  .weplay-info-txt {
    h3 {
      font-size: 20px;
      line-height: 32px;
      margin: 30px auto 28px;
      width: 45%;

      @media #{$mobile} {
        line-height: 28px;
        margin-bottom: 25px;
        margin-top: 0;
        text-align: center;
        margin: 0 auto 25px;
        width: 85%;
      }

      @media #{$tablet} {
        width: 85%;
      }

      @media #{$betweentabdesk} {
        width: 60%;
        margin-top: 15px;
      }
    }

    p {
      line-height: 32px;
      width: 65%;
      margin: auto;

      @media #{$mobile} {
        line-height: 28px;
        text-align: center;
        width: 97%;
        margin: 0 auto;
      }

      @media #{$tablet} {
        width: 90%;
        line-height: 28px;
      }

      @media #{$betweentabdesk} {
        width: 80%;
      }
    }

    span {
      color: $primaryColor;
    }
  }

  .weplay-video {
    position: relative;
    background-image: url(../imgs/video-install-bg.png);
    background-position-y: 64%;
    background-position-x: 64%;
    box-shadow: 0px 34px 86px rgba(107, 104, 114, 0.63);
    padding: 80px 0 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;

    &:hover {
      box-shadow: 0px 8px 20px rgba(107, 104, 114, 0.63);
    }

    @media #{$mobile} {
      background-position: center;
      background-size: cover;
      padding: 47px 0;
    }

    @media #{$tablet} {
      padding: 80px 0;
      background-position: center;
      background-size: cover;
    }

    &:after {
      content: "";
      @include overlay;
      background-color: #5b5863;
      opacity: 0.9;
      z-index: 1;
    }

    .weplay-video-content {
      text-align: center;
      position: relative;
      z-index: 5;

      a {
        display: inline-block;
        width: 102px;
        height: 102px;
        line-height: 102px;
        border-radius: 50%;
        background-color: $primaryColor;
        color: #fff;
        text-align: center;
        font-size: 35px;
        transition: all 0.3s ease-in-out;

        &:hover {
          box-shadow: 0px 4px 16px #46444d;
        }

        @media #{$mobile} {
          width: 60px;
          height: 60px;
          line-height: 60px;
          font-size: 24px;
        }

        @media #{$tablet} {
          width: 90px;
          height: 90px;
          line-height: 90px;
          font-size: 28px;
        }

        @media #{$betweentabdesk} {
          width: 78px;
          height: 78px;
          line-height: 78px;
          font-size: 28px;
        }
      }

      p {
        font-size: 24px;
        line-height: 24px;
        color: #fff;
        margin: 25px auto 0;

        @media #{$mobile} {
          font-size: 14px;
          margin: 8px auto 0;
        }

        @media #{$betweentabdesk} {
          font-size: 20px;
          margin: 20px auto 0;
        }
      }
    }
  }
}
/* End weplay info */

/* Start how work weplay */
.how-work-weplay {
  padding: 100px 0 200px;
  position: relative;

  @media #{$mobile} {
    padding: 60px 0 200px;
  }
  @media #{$tablet} {
    padding: 80px 0 160px;
  }
  @media #{$betweentabdesk} {
    padding: 80px 0 160px;
  }

  .title {
    font-size: 56px;
    line-height: 56px;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 46px;
    }

    @media #{$tablet} {
      font-size: 36px;
      line-height: 44px;
    }
  }

  .desc {
    font-size: 18px;
    line-height: 28px;
    width: 65%;
    margin: 35px auto 0;

    @media #{$mobile} {
      font-size: 16px;
      width: 99%;
      margin: 20px auto 0;
    }

    @media #{$tablet} {
      width: 85%;
      margin: 20px auto 0;
    }

    strong {
      font-weight: 500;
    }
  }

  .section-line {
    border-color: #e0dfdc;
    margin: 65px 0;

    @media #{$mobile} {
      margin: 60px 0;
    }
  }

  .step {
    &.one,
    &.two {
      position: relative;

      @media #{$maxTab} {
        padding-bottom: 100px;
      }

      &:after {
        content: "";
        border-width: 19px;
        border-style: solid;
        border-color: transparent transparent transparent rgba(#afadb2, 0.4);
        position: absolute;
        display: block;
        top: 93px;
        right: 30px;

        @media #{$maxTab} {
          top: auto;
          bottom: 25px;
          right: auto;
          left: 50%;
          transform: translateX(-50%);
          border-color: rgba(#afadb2, 0.4) transparent transparent transparent;
        }
      }
    }

    .icon {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background-color: $primaryColor;
      position: relative;
      overflow: hidden;

      @media #{$mobile} {
        width: 40px;
        height: 40px;
        position: absolute;
      }

      @media #{$tablet} {
        position: absolute;
      }

      span {
        color: #fff;
        font-size: 96px;
        line-height: 120px;

        font-weight: bold;
        position: absolute;
        top: -18px;
        left: 10px;

        @media #{$mobile} {
          font-size: 60px;
          line-height: 60px;
          top: -4px;
          left: 7px;
        }
      }
    }

    .step-info {
      @media #{$mobile} {
        padding-left: 56px;
      }

      @media #{$tablet} {
        padding-left: 80px;
      }

      h3 {
        font-size: 20px;
        line-height: 20px;
        margin: 45px 0 15px;

        @media #{$mobile} {
          margin: 0 0 15px;
          font-size: 16px;
        }

        @media #{$tablet} {
          margin: 0;
        }
      }

      p {
        line-height: 28px;
        width: 69%;

        @media #{$mobile} {
          width: 100%;
          font-size: 14px;
        }
      }
    }
  }

  /* End join-to-weplay */
}
/* End how work weplay */

.weplay-page-footer {
  padding-top: 180px;

  @media #{$mobile} {
    padding-top: 170px;
  }

  @media #{$tablet} {
    padding-top: 140px;
  }

  @media #{$betweentabdesk} {
    padding-top: 150px;
  }
}
/* End Why Weplay */
.weplay-hero-section {
  h1 {
    @media #{$betweentabdesk} {
      font-size: 40px;
    }
    @media #{$tablet} {
      font-size: 30px;
    }
  }
  .weplay-subtitle {
    font-size: 30px;
    line-height: 140%;
    @media #{$tablet} {
      font-size: 22px;
    }
    @media #{$mobile} {
      font-size: 18px;
    }
  }
}

.how-weplay-work {
  .step {
    padding-bottom: 45px;
    .step-nu {
      border-radius: 10px 10px 0px 0px;
      padding: 10px;
    }
    .image {
      img {
        max-height: 240px;
        @media #{$betweentabdesk} {
          min-height: 210px;
          max-height: 210px;
        }
        @media #{$tablet} {
          min-height: 150px;
          max-height: 150px;
        }
      }
    }
    h4 {
      @media #{$maxTab} {
        font-size: 18px;
      }
    }
    p {
      padding: 0 12px;
      @media #{$tablet} {
        font-size: 14px;
      }
    }
  }
}