.wireguard-hero {
  padding: 135px 0 95px;
  @media #{$maxTab} {
    padding: 45px 0 60px;
  }
  &__text {
    h1 {
      @media #{$maxTab} {
        margin-bottom: 23px;
        text-align: center;
      }
    }
    p {
      margin: 20px 0 60px;
      @media #{$maxTab} {
        margin: 20px 0 40px;
        text-align: center
      }
    }
    .wireguard-btn {
      @media #{$maxTab} {
        text-align: center;;
      }
      a {
        max-width: 260px;
        width: 100%;
        line-height: 64px;
        @media #{$mobile} {
          max-width: unset;
        }
      }
    }
  }
}

// Start security standard
.security-standard {
  padding: 60px 0;
}
// End security standard

// Start wireguard platform
.wireguard-platform {
  padding: 60px 0;
}
// End wireguard platform

// Start wireguard system
.wireguard-system {
  padding: 60px 0;
}
// End wireguard system

.security-standard,
.wireguard-system,
.wireguard-platform {
  @media #{$mobile} {
    .image {
      margin-bottom: 50px;
      text-align: center;
    }
  }
}
