.page-404 {
  padding: 130px 0 60px;
}

.page-404 h1 {
  font-size: 100px;
  line-height: 125px;
}

.page-404 p {
  font-size: 18px;
  line-height: 160%;
  opacity: 0.8;
  margin: 30px 0 50px;
}
.page-404 .back-home {
  width: 100%;
  max-width: 200px;
  line-height: 52px;
}

@media (min-width: 768px) and (max-width: 992px) {
  .page-404 {
    padding: 60px 0;
  }
  .page-404 h1 {
    font-size: 80px;
    line-height: 100px;
  }
  .page-404 h2 {
    font-size: 30px;
    line-height: 40px;
  }
  .page-404 p {
    margin: 20px 0 30px;
  }
}

@media (max-width: 767px) {
  .page-404  {
    padding: 60px 0;
    text-align: center;
  }
  .page-404 h1 {
    margin-top: 30px;
  }
  .page-404 .back-home {
    max-width: unset;
  }
}

