.affiliates-hero {
  color: #fff;
  padding: 115px 0 70px;
  @media #{$tablet} {
    padding: 105px 0 50px;
  }
  @media #{$mobile} {
    padding: 105px 0 60px;
  }
  .affiliates-hero__text {
    @media #{$mobile} {
      text-align: center;
    }
    h1{
      @media #{$tablet} {
        font-size: 32px;
        line-height: 32px;
      }
      @media #{$mobile} {
        font-size: 28px;
        line-height: 140%;
      }
    }
    p {
      margin: 20px 0 45px;
      font-size: 20px;
      line-height: 32px;
      @media #{$maxTab} {
        font-size: 18px;
        margin: 15px 0 30px;
      }
    }
  }
  .affiliates-hero__image {
    @media #{$mobile} {
      max-width: 85%;
      margin: 0 auto 20px;
    }
  }
  .affiliates-btns {
    @media #{$mobile} {
      flex-wrap: wrap;
    }
    a {
      padding: 0;
      line-height: 60px;
      border-radius: 5px;
      width: 100%;
      display: inline-block;
      text-align: center;
      font-size: 18px;
      border: 2px solid #fff;
      font-weight: 500;
      @media #{$tablet} {
        font-size: 16px;
        line-height: 54px;
      }
      @media #{$mobile} {
        line-height: 54px;
        &:first-child {
          margin-bottom: 20px;
        }
      }
      svg {
        transition: all 0.3s ease-in-out;
        margin-left: 5px;
      }
      &:hover {
        svg {
          transform: translateX(4px);
        }
      }
    }
    .signin {
      color: #fff;
      margin-left: 20px;
      @media #{$tablet} {
        margin-left: 10px;
      }
      @media #{$mobile} {
        margin-left: 0;
      }
    }
  }
}

.join-wevpn-affiliate {
  padding: 80px 0;
  @media #{$betweentabdesk} {
    padding: 70px 0 40px;
  }
  @media #{$maxTab} {
    padding: 60px 0 30px;
  }
  h2 {
    margin-bottom: 55px;
    @media #{$mobile} {
      margin-bottom: 30px;
    } 
  }
  .box {
    background: #FFFFFF;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.07);
    border-radius: 10px;
    padding: 45px 20px;
    @media (max-width: 1199px) {
      margin-bottom: 30px;
    }
    @media #{$tablet} {
      padding: 30px 20px 35px;
    }
    .icon {
      max-width: 210px;
      height: 190px;
      margin: 0 auto 30px;
      @media #{$tablet} {
        margin: 0 auto;
        transform: scale(0.85);
      }
      @media #{$mobile} {
        height: auto;
      }
      img {
        margin: auto;
      }
    }
    h3 {
      margin-bottom: 15px;
      @media #{$maxTab} {
        font-size: 18px;
      }
    }
    p {
      line-height: 160%;
      opacity: 0.8;
      @media #{$maxTab} {
        font-size: 14px;
      }
    }
  }
}

.affiliates-account-managers,.promote-affordable-service {
  padding: 60px 0;
  .image {
    @media #{$mobile} {
      margin-bottom: 30px;
    }
  }
  .text {
    h2 {
      margin-bottom: 34px;
      @media #{$betweentabdesk} {
        font-size: 32px;
      }
      @media #{$tablet} {
        font-size: 24px;
      }
      @media #{$maxTab} {
        margin-bottom: 15px;
      }
    }
    p {
      opacity: 0.8;
      line-height: 160%;
      @media #{$maxTab} {
        font-size: 14px;
      }
      &:not(:last-child) {
        margin-bottom: 25px;
      }
    }
  }
}