.ip-address-hero {
  padding: 164px 0 0;
  @media #{$maxTab} {
    padding: 60px 0 0;
  }
  h1 {
    margin-bottom: 60px;
    @media #{$maxTab} {
      margin-bottom: 40px;
    }
  }
  .ip-address-list {
    display: flex;
    li {
      width: 33.33%;
      text-align: center;
      padding: 16px;
      @media #{$tablet} {
        padding: 10px;
      }
      a {
        color: #fff;
        opacity: 0.8;
        font-size: 20px;
        @media #{$betweentabdesk} {
          font-size: 18px;
        }
        @media #{$tablet} {
          font-size: 14px;
        }
      }
      &.active {
        background: #fafafa;
        border-radius: 10px 10px 0px 0px;
        a {
          color: $primaryColor;
          font-weight: 500;
          opacity: 1;
        }
      }
    }
  }
  .dropdown {
    button {
      background: #FAFAFA;
      border-radius: 10px 10px 0px 0px;
      padding: 10px 40px;
      min-width: 270px;
      max-width: 270px;
      &:focus {
        box-shadow: none;
      }
      &:hover {
        color: $primaryColor;
      }
    }
    .dropdown-menu {
      background: #FFFFFF;
      box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      border: 0;
      min-width: 270px;
      max-width: 270px;
      text-align: center;
      .dropdown-item {
        padding: 12px 0;
        &:not(:last-child) {
          border-bottom: 1px solid #F8F8F8;
        }
      }
    }
  }
}


.ip-address-info {
  padding: 60px 0 70px;
  @media #{$mobile} {
    padding: 35px 0 60px;
  }
  .loader-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .lds-spinner {
    color: official;
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-spinner div {
    transform-origin: 40px 40px;
    animation: lds-spinner 1.2s linear infinite;
  }
  .lds-spinner div:after {
    content: " ";
    display: block;
    position: absolute;
    top: 3px;
    left: 37px;
    width: 6px;
    height: 18px;
    border-radius: 20%;
    background: #FD3E72;
  }
  .lds-spinner div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -1.1s;
  }
  .lds-spinner div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -1s;
  }
  .lds-spinner div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.9s;
  }
  .lds-spinner div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.8s;
  }
  .lds-spinner div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.7s;
  }
  .lds-spinner div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.6s;
  }
  .lds-spinner div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.5s;
  }
  .lds-spinner div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.4s;
  }
  .lds-spinner div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.3s;
  }
  .lds-spinner div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.2s;
  }
  .lds-spinner div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.1s;
  }
  .lds-spinner div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
  }
  @keyframes lds-spinner {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  .ip-address-info-content {
    background: #FFFFFF;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding: 30px;
    &.ip-protected {
      .protected-msg {
        display: flex;
      }
      .wevpn-button,.not-protected-msg {
        display: none;
      }
      .ip-value {
        color: #00B67A;
      }
    }
    &.dns-protected {
      .protected-msg {
        display: flex;
      }
      .wevpn-button,.not-protected-msg {
        display: none;
      }
      .dns-not-protected-text {
        display: none;
      }
      .dns-protected-text {
        display: block;
      }
      .requests .title {
        border-bottom: 1px solid #00B67A;
      }
      .requests .ip-value.active .value {
        color: #00B67A;
      }
      .requests .ip-value.active .arrow span {
        background-color: #00B67A;
      }
    }
    @media #{$mobile} {
      padding: 20px 12px;
    }
    .wevpn-button {
      @media (max-width: 1200px) {
        a {
          font-size: 16px;
        }
      }
    }
    
  }
  .webrtc-disabled {
    background: linear-gradient(91.55deg, #FE6556 -13.03%, #FD3E72 66.96%, #49A8FF 153.9%);
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    font-weight: 500;
    font-size: 32px;
    line-height: 140%;
    color: #FFFFFF;
    padding: 34px 20px;
    @media #{$tablet} {
      padding: 20px;
      font-size: 24px;
    }
    @media #{$mobile} {
      padding: 20px 30px;
      font-size: 20px;
    line-height: 160%;
    }
  }
  .dns-not-protected-text {
    display: block;
  }
  .dns-protected-text {
    display: none;
  }
  .not-protected-msg {
    background: #FFECF1;
    border-radius: 10px;
    padding: 15px;
    display: flex;
  }
  .protected-msg {
    background: rgba(#00B67A, 0.1);
    border-radius: 10px;
    padding: 15px;
    display: none;
    .text {
      p {
        color: #00B67A;
      }
    }
  }
  .item {
    margin-bottom: 30px;
    @media #{$betweentabdesk} {
      margin-bottom: 15px;
    }
    @media #{$mobile} {
      margin-bottom: 0;
      padding: 12px 0;
      border-bottom: 1px solid #F4F4F4;
    }
  }
  label {
    margin-bottom: 4px;
  }
  h4.value {
    font-size: 20px;
  }
  .ip-location {
    position: relative;
    @media (min-width: 992px) {
      &:before {
        content: "";
        position: absolute;
        border-left: 1px dashed #F4E6E6;
        width: 1px;
        height: 100%;
        top: 0;
        left: -28px;
      }
    }
  }
  .dns-requests {
    h3 {
      @media #{$mobile} {
        font-size: 18px;
      }
    }
  }
  .requests {
    display: flex;
    justify-content: space-between;
    @media #{$tablet} {
      margin-bottom: 30px;
    }
    @media #{$mobile} {
      margin-bottom: 30px;
      > div:not(.ip-address) {
        display: none;
      }
      .ip-address {
        width: 100%;
      }
      .ip-value {
        position: relative;
        border-bottom: 1px solid #eee;
        &.active {
          .value {
            color: $primaryColor;
          }
          border-bottom: 0;
          .arrow {
            span {
              background-color: $primaryColor;

              &:first-of-type {
                transform: rotate(-45deg);
              }

              &:last-of-type {
                transform: rotate(45deg);
              }
            }
          }
        }
        .arrow {
          top: 12px;
        }
      }
      .toggle {
        background: #FAFAFA;
        box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.05);
        display: none;
        .row-value {
          padding: 10px;
        }
      }
    }
    
    > div {
      width: 30%;     
      .odd {
        background-color: #fafafa;
      }
    }
    .provider,.type {
      width: 40%;
    }
    .title {
      border-bottom: 1px solid $primaryColor;
      padding: 10px 10px 5px;
    }
    .row-val {
      padding: 10px;
    }
  }
}
