.why-vilfo {
  .feat-row {
    &:not(:last-child) {
      border-bottom: 1px solid #e3ebef;
      @media #{$mobile} {
        border-bottom: 0;
      }
    }
  }
  .feat-box {
    padding: 45px 30px;
    position: relative;
    width: 100%;
    @media #{$mobile} {
      padding: 30px 0;
    }
    @media (min-width: 768px) and (max-width: 1199px) {
      padding: 30px 0;
      &.bord-right:after {
        content: "";
        position: absolute;
        height: 100%;
        width: 1px;
        background-color: #e3ebef;
        right: -15px;
        top: 0;
      }
    }
    &.bord-right {
      @media (min-width: 1200px) {
        border-right: 1px solid #e3ebef;
      }
    }
    @media #{$mobile} {
      border-bottom: 1px solid #e3ebef;
    }
    &.last {
      @media #{$mobile} {
        padding-bottom: 0;
        border-bottom: 0;
      }
    }
  }
}

.vilfo-router {
  .image {
    position: relative;
  }
  .video-btn {
    position: absolute;
    top: 47%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 110px;
    height: 110px;
    z-index: 2;
  }
}