.student-beans-hero {
  overflow: hidden;
  .student-beans-btns {
    a {
      @media (min-width: 1024px) {
        width: 48% !important;
      }
      @media #{$betweentabdesk} {
        font-size: 16px;
      }
    }
  }
  .student-beans-logo {
    svg {
      @media #{$mobile} {
        height: 25px;
      }
    }
  }
  .terms-btn {
    font-size: 20px;
    padding: 0 20px;
    border: 2px solid #fff;
    border-radius: 5px;
    @media #{$tablet} {
      margin-left: 20px;
    }
    @media #{$mobile} {
      font-size: 16px;
    }
  }
  .image {
    @media (min-width: 1024px) {
      position: relative;
      right: -50px;
    }
  }
}

.wevpn-students {
  .text {
    h2 {
      font-size: 32px;
      @media #{$tablet} {
        font-size: 24px;
      }
      @media #{$mobile} {
        font-size: 20px;
      }
    }
  }
}
