.roadmap-banner {
  padding: 50px 0;
  @media #{$maxTab} {
    padding: 30px 0;
  }
}

.roadmap-timeline {
  padding: 70px 0;
  @media #{$maxTab} {
    padding: 40px 0 0;
  }
  .title {
    margin-bottom: 100px;
    position: relative;
    @media #{$betweentabdesk} {
      h2 {
        font-size: 32px;
      }
    }
    @media #{$maxTab} {
      margin-bottom: 20px;
    }
    &:after {
      content: "";
      background: url(../imgs/roadmap/timeline.svg);
      background-repeat: no-repeat;
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 100%;
      left: 2px;
      right: 0;
      @media #{$maxTab} {
        display: none;
      }
    }
  }
  .timeline-panel {
    @media #{$maxTab} {
      position: relative;
      padding-left: 45px;
      padding-top: 25px;
      &::after {
        position: absolute;
        content: "";
        background-image: url(../imgs/roadmap/timeline-m.svg);
        top: 0;
        left: 0;
        height: 100%;
        width: 25px;
        background-repeat: no-repeat;
        background-position: top;
      }
    }
    
  }
  .timeline {
    @media (min-width: 1200px) {
      padding-left: 20px;
    }
    @media #{$maxTab} {
      margin-bottom: 50px;
    }
    &::after {
      position: absolute;
      content: "";
      background-image: url(../imgs/roadmap/timeline-mark.svg);
      top: -50px;
      left: 40px;
      height: 30px;
      width: 30px;
      z-index: 2;
      background-repeat: no-repeat;
      background-position: top;
      @media #{$betweentabdesk} {
        left: 30px;
      }
      @media #{$maxTab} {
        background-image: url(../imgs/roadmap/timeline-mark-m.svg);
        left: -33px;
        top: 4px;
      }
    }
    h3 {
      font-size: 28px;
      @media #{$betweentabdesk} {
        font-size: 24px;
      }
      @media #{$maxTab} {
        font-size: 20px;
        margin-bottom: 18px;
      }
    }
    .timeline-list {
      li {
        padding-left: 20px;
        position: relative;
        @media #{$maxTab} {
          font-size: 14px;
        }
        &:not(:last-child) {
          margin-bottom: 5px;
          @media #{$maxTab} {
            margin-bottom: 10px;
          }
        }
        &::before {
          content: "";
          position: absolute;
          top: 8px;
          left: 0;
          width: 6px;
          height: 6px;
          background-color: $primaryColor;
          border-radius: 50%;
        }
      }
    }
  }
}