.vpn-hero-img {
  padding: 150px 0 90px;
  @media #{$maxTab} {
    padding: 40px 0 60px
  }
  .vpn-hero-desc {
    p:not(:last-child) {
      margin-bottom: 30px;
      @media #{$mobile} {
        margin-bottom: 20px;
      }
    }
    .p-lg {
      @media (max-width: 1199px) {
        font-size: 16px;
      }
    }
  }
  .vpn-hero-image {
    @media #{$maxTab} {
      margin-bottom: 30px;
    }
    @media (min-width: 1024px) {
      position: relative;
      right: -50px;
    }
  }
}

.vpn-online-privacy, .vpn-internet-freedom, .vpn-cyber-security {
  padding: 60px 0;
  .h1 {
    margin-bottom: 30px;
    @media #{$betweentabdesk} {
      font-size: 42px;
    }
  }
  .title {
    margin-bottom: 20px;
  }
  .vpn-btn {
    margin-top: 40px;
    line-height: 64px;
    max-width: 260px;
    @media #{$mobile} {
      max-width: unset;
    }
  }
  .img {
    @media #{$mobile} {
      margin-bottom: 30px;
    }
  }
}

.vpn-more-benefits {
  padding: 65px 0;
  @media #{$mobile} {
    padding: 50px 0 40px;
  }
  .title {
    margin-bottom: 50px;
    @media #{$mobile} {
      margin-bottom: 35px;
    }
  }
  .item {
    padding: 25px;
    background: #FAFAFA;
    box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    @media #{$maxTab} {
      flex-direction: column;
      justify-content: center;
      padding: 30px 20px;
    }
    @media #{$tablet} {
      padding: 30px 25px;
    }
  }
  .icon {
    @media (min-width: 1024px) {
      margin-right: 30px;
    }
    @media #{$maxTab} {
      margin-bottom: 30px;
    }
  }
  .benefit-title {
    margin-bottom: 15px;
  }
}

.vpn-protocols {
  padding: 85px 0;
  @media #{$maxTab} {
    padding: 50px 0;
  }
  .vpn-protocols-img {
    @media #{$maxTab} {
      margin-bottom: 30px;
    }
  }
  .vpn-protocols-content {
    @media (min-width: 1024px) {
      padding-left: 40px;
    }
    .title {
      @media #{$betweentabdesk} {
        font-size: 34px;
      }
    }
    .text {
      margin: 30px 0;
    }
  }
}

.pricing-tables {
  padding-top: 130px;
  padding-bottom: 130px;
  @media #{$betweentabdesk} {
    padding-top: 80px;
    padding-bottom: 75px;
  }

  @media #{$tablet} {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @media #{$mobile} {
    padding-top: 45px;
    padding-bottom: 60px;
  }
}