header.homepage-2 {
  height: 70px;
  .get-started {
    padding: 10px 20px;
    display: block;
    font-size: 14px;
    text-align: center;
    transition: background-color 0.3s ease-in-out;
    background-color: #fd3e72;
    text-decoration: none;
    color: #fff;
    font-family: "Averta";
    font-weight: 500;
    border-radius: 3px;
    @media #{$maxTab} {
      padding: 8px 20px;
    }
  }
}

.hero-img.homepage-2 {
  background-image: url(../imgs/homepage/homepage-2x.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  background-color: #162932;

  .container {
    position: relative;
    z-index: 2;
  }

  @media #{$mobile} {
    padding: 60px 0;
    background-image: linear-gradient(to right, rgba(9, 32, 46, 0.7), rgba(0, 0, 0, 0.5)), url(../imgs/homepage/homepage-mobile.webp);
  }
  

  .hero-img-txt {
    color: #ffffff;
    font-size: 32px;
    line-height: 45px;
    font-weight: 500;
    @media #{$betweentabdesk} {
      font-size: 25px;
      line-height: 38px;
    }
    @media #{$tablet} {
      font-size: 22px;
      line-height: 32px;
    }
    @media #{$mobile} {
      font-size: 18px;
      line-height: 30px;
    }
  }
  .title {
    @media #{$betweentabdesk} {
      font-size: 38px;
    }
    @media #{$tablet} {
      font-size: 30px;
    }
    @media #{$mobile} {
      font-size: 26px;
    }
  }
  .hero-img-title {
    font-size: 53px;
    line-height: 60px;
    margin-bottom: 40px;
    margin-top: 0;
    color: #ffffff;
    @media #{$betweentabdesk} {
      font-size: 42px;
      line-height: 50px;
    }
    @media #{$tablet} {
      font-size: 50px;
      line-height: 55px;
      margin-bottom: 30px;
    }
    @media #{$mobile} {
      font-size: 40px;
      line-height: 50px;
      margin-bottom: 20px;
      width: 100%;
    }
  }

  .trustpilot_img {
    margin-top: 35px;
    @media (min-width: 768px) and (max-width: 1199px) {
      img {
        height: 30px;
        width: auto;
      }
    }
    @media #{$mobile} {
      margin: 30px 0;
      img {
        height: 22px;
      }
    }
  }
  .hero-img-content {
    .homepage-2-button {
      display: inline-block;
      text-align: center;
      .hero-img-btn {
        line-height: 64px;
        width: 100%;
        padding: 0 20px;
        min-width: 260px;
        @media #{$betweentabdesk} {
          font-size: 16px;
        }
        @media #{$tablet} {
          width: 260px;
          font-size: 16px;
        }
        @media #{$mobile} {
          margin: 0;
          min-height: 64px;
        }
        .arrow-triangle {
          margin-left: 5px;
        }
      }
      p {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #ffffff;
        opacity: 1;
        margin-top: 10px;
        @media #{$betweentabdesk} {
          font-size: 14px;
        }
        @media #{$maxTab} {
          font-size: 14px;
          line-height: 17px;
        }
      }
    }
  }
}

@media (min-width: 1000px) and (max-width: 1200px) {
  .hero-img.homepage-2 {
    padding: 120px 0 90px;
  }
}



// Start Homepage Testimonials
.homepage-testimonials {
  padding: 30px 0;
  background-color: #fff;
  @media #{$mobile} {
    padding: 0;
  }
  .testimonial-content {
    @media #{$tablet} {
      .left,
      .right {
        padding-right: 0;
      }
    }
    @media #{$mobile} {
      background: #ffffff;
      padding: 20px 0;
    }
  }
  .testimonial {
    display: flex;
    align-items: center;
    @media #{$maxTab} {
      align-items: flex-start;
    }
    @media #{$mobile} {
      margin: 0 14px;
      padding: 20px 0;
    }

    &.one {
      position: relative;
      @media #{$mobile} {
        border-bottom: 1px solid #ffebf0;
      }
      @media (min-width: 768px) and (max-width: 1199px) {
        .text {
          padding-right: 15px;
        }
      }
      &:after {
        content: "";
        width: 0.8px;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
        background-color: #e9edef;
        @media #{$mobile} {
          display: none;
        }
      }
    }
    .image {
      margin-right: 40px;
      @media #{$maxTab} {
        margin-right: 13px;
        img {
          width: 80px;
        }
      }
    }
    .text {
      flex: 1;
      .testimonial-text {
        font-weight: 500;
        font-size: 12px;
        line-height: 20px;
        max-width: 400px;
        @media #{$maxTab} {
          font-size: 14px;
          line-height: 140%;
        }
      }
      .author {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 10px;
        position: relative;
        .author-info {
          font-size: 12px;
          line-height: 15px;
          @media #{$maxTab} {
            font-size: 14px;
            line-height: 22px;
          }
          h3 {
            font-weight: 500;
            color: #fd3e72;
            font-size: inherit;
          }
          p {
            color: rgba(91, 88, 99, 0.6);
          }
        }
        .quote {
          position: absolute;
          right: 24px;
          top: 24px;
          font-weight: 500;
          font-size: 100px;
          line-height: 22px;
          color: rgba(253, 62, 114, 0.1);
          @media #{$mobile} {
            right: 10px;
          }
        }
      }
    }
  }
}

// Start Wevpn Comparison
.wevpn-comparison {
  background: #f2f1ed;
  padding: 140px 0;
  @media #{$betweentabdesk} {
    padding: 120px 0 100px;
  }
  @media #{$tablet} {
    padding: 100px 0 60px;
    .container {
      max-width: 100%;
    }
  }
  @media #{$mobile} {
    padding: 55px 0 50px;
    .container {
      max-width: 100%;
    }
  }
  .comparison-table {
    @media #{$mobile} {
      width: 100vw;
      margin-left: -20px;
      overflow-x: scroll;
    }
    .inner {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      background: #ebeae9;
      border-radius: 10px;
      padding: 0 10px 10px;
      @media #{$mobile} {
        width: 777px;
      }
      .cell {
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        background-color: #fff;
        font-weight: 500;
        font-size: 18px;
        color: rgba(91, 88, 99, 0.6);
        @media #{$maxTab} {
          font-size: 14px;
          height: 70px;
        }
        &:first-child {
          height: 97px;
          background: #ebeae9;
          @media #{$betweentabdesk} {
            img {
              width: 95px;
            }
          }
          @media #{$maxTab} {
            height: 80px;
            img {
              width: 95px;
            }
          }
        }
        &:nth-child(4) {
          height: 125px;
          @media #{$maxTab} {
            height: 110px !important;
          }
        }

        &:not(:first-child) {
          border-right: 1px solid #ebeae9;
          border-bottom: 1px solid #ebeae9;
        }
        &:last-child {
          border-bottom: 0;
        }
      }
      .table-col.labels {
        width: 20%;
        @media #{$betweentabdesk} {
          width: 25%;
        }
        @media #{$tablet} {
          width: 21%;
        }
        .cell {
          padding: 0 24px;
          justify-content: flex-start;
          text-align: left;
          font-weight: 500;
          color: #5b5863;
          @media #{$maxTab} {
            padding: 0 12px;
          }
          &.large {
            flex-direction: column;
            justify-content: center;
          }
          p {
            font-weight: normal;
            font-size: 12px;
            line-height: 15px;
            color: #5b5863;
            opacity: 0.7;
            margin-top: 5px;
            @media #{$maxTab} {
              font-size: 10px;
              line-height: 12px;
            }
          }
        }
      }
      .table-col.wevpn {
        width: 20%;
        position: relative;
        .main-column {
          background: #f2f1ed;
          border: 1px solid #fd3e72;
          border-radius: 10px;
          position: absolute;
          top: -60px;
          right: 0;
          left: 0;
          @media #{$tablet} {
            top: -26px;
          }
          @media #{$mobile} {
            top: 0;
          }
        }
        .cell {
          height: 95px;
          background-color: #fff;
          font-weight: 500;
          font-size: 25px;
          color: #5b5863;
          @media #{$maxTab} {
            font-size: 18px;
            height: 70px;
          }
          &:not(:first-child) {
            border-right: 0;
          }

          &:nth-child(4) {
            height: 125px;
            @media #{$maxTab} {
              height: 110px !important;
            }
          }
        }
        .cell:first-child {
          height: 129px;
          background: #f2f1ed;
          border-radius: 10px 10px 0px 0px;
          @media #{$tablet} {
            height: 105px;
          }
          @media #{$mobile} {
            height: 79px;
          }
        }

        .cell:last-child {
          height: 95px;
          margin-bottom: 0;
          background: #fd3e72;
          border-radius: 0px 0px 10px 10px;
          color: #fff;
          @media #{$tablet} {
            height: 85px;
          }
          @media #{$mobile} {
            height: 70px;
          }
        }
      }
    }

    .table-col:nth-child(3),
    .table-col:nth-child(4),
    .table-col:nth-child(5),
    .table-col:nth-child(6) {
      width: 15%;
    }
  }
}

.get-wevpn-btn {
  text-align: center;
  margin-top: 100px;
  @media #{$maxTab} {
    margin-top: 55px;
  }
  a {
    width: 270px;
    line-height: 64px;
    @media #{$maxTab} {
      font-size: 16px;
    }
  }
  p {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    margin-top: 10px;
    @media #{$maxTab} {
      font-size: 14px;
      line-height: 17px;
    }
  }
}
// Start Protect Security
.protect-security,
.internet-freedom {
  background: #fff;
  padding: 80px 0 130px;

  @media #{$tablet} {
    padding: 80px 0;
  }
  @media #{$mobile} {
    padding: 40px 0 46px;
  }
  h2 {
    font-weight: bold;
    font-size: 45px;
    line-height: 65px;
    max-width: 750px;
    margin: 0 auto 105px;
    text-align: center;
    @media #{$betweentabdesk} {
      margin: 0 auto 80px;
    }
    @media #{$tablet} {
      font-size: 40px;
      line-height: 50px;
      margin: 0 auto 40px;
    }
    @media #{$mobile} {
      font-size: 32px;
      line-height: 45px;
      margin: 0 auto 40px;
    }
  }
  &-text {
    h3 {
      font-size: 35px;
      line-height: 44px;
      margin-bottom: 30px;
      @media #{$betweentabdesk} {
        font-size: 33px;
        line-height: 40px;
      }
      @media #{$maxTab} {
        font-size: 30px;
        line-height: 30px;
      }
    }
    h4 {
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
      color: $primaryColor;
      margin-bottom: 15px;
      @media #{$betweentabdesk} {
        margin-bottom: 10px;
        font-size: 25px;
        line-height: 35px;
      }
      @media (min-width: 992px) {
        max-width: 470px;
      }
      @media #{$mobile} {
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 10px;
      }
    }
    p {
      font-size: 22px;
      line-height: 32px;
      color: rgba(91, 88, 99, 0.8);
      @media #{$betweentabdesk} {
        font-size: 17px;
        line-height: 28px;
      }
      @media #{$maxTab} {
        font-size: 14px;
        line-height: 25px;
      }
      &:not(:last-of-type) {
        margin-bottom: 50px;
        @media (max-width: 1199px) {
          margin-bottom: 30px;
        }
      }
    }
  }
  .image {
    @media #{$maxTab} {
      margin-bottom: 50px;
    }
  }
}

// Start all-apps
.all-apps {
  background: #fd3e72;
  padding: 21px 0 18px;
  @media #{$maxTab} {
    padding: 35px 0;
  }
  &-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media #{$maxTab} {
      flex-direction: column;
    }
    .apps {
      p {
        text-transform: uppercase;
        font-weight: 500;
        font-size: 18px;
        color: #ffffff;
        margin-bottom: 20px;
        @media #{$maxTab} {
          text-align: center;
          font-size: 16px;
          line-height: 30px;
          margin-bottom: 34px;
        }
      }
      ul {
        @media #{$maxTab} {
          text-align: center;
        }
        li {
          display: inline-block;
          margin-right: 7px;
          @media #{$maxTab} {
            margin-bottom: 20px;
          }
        }
      }
    }
  }
}

.get-wevpn {
  text-align: center;
  @media #{$maxTab} {
    margin-top: 25px;
  }

  a {
    width: 270px;
    height: 77px;
    line-height: 77px;
    background-color: #fff;
    display: inline-block;
    text-align: center;
    border-radius: 5px;
    font-size: 20px;
    color: #fd3e72;
    font-weight: 500;
    @media (max-width: 1199px) {
      width: 228px;
      height: 64px;
      line-height: 64px;
      font-size: 16px;
    }
  }
  p {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
    margin-top: 10px;
    @media (max-width: 1199px) {
      font-size: 14px;
    }
  }
}

// Start Internet Freedom
.internet-freedom {
  padding: 124px 0 80px;
  .wevpn_communities {
    margin-top: 100px;
    @media #{$maxTab} {
      margin-top: 50px;
    }
  }
  @media #{$betweentabdesk} {
    padding: 80px 0;
  }
  @media #{$maxTab} {
    padding: 28px 0 60px;
  }
  .internet-freedom-text h3 {
    max-width: none;
  }
}

// Start Wevpn Features
.wevpn-features {
  background-color: #fff;
  position: relative;
  padding-top: 90px;
  overflow: hidden;
  @media #{$maxTab} {
    padding-top: 0;
  }
  &:after {
    content: "";
    position: absolute;
    right: 0;
    width: 50%;
    top: 0;
    bottom: 0;
    background: linear-gradient(154.48deg, #fe6556 -9.73%, #fd3e72 66.22%, #49a8ff 163.3%);
    z-index: 0;
    @media #{$mobile} {
      display: none;
    }
  }
  .wevpn_features_content {
    max-width: 1200px;
    margin: auto;
    padding: 0 15px;
  }
  .row {
    > div {
      display: flex;
      padding: 0;
    }
  }
  .support {
    position: relative;
    z-index: 1;
    width: 100%;
    top: 1px;

    @media (min-width: 1050px) {
      max-width: 475px;
      margin: 0 auto;
    }
    @media (max-width: 1050px) {
      padding: 0 30px;
    }
    @media #{$tablet} {
      max-width: 350px;
      margin: 0 auto;
    }
    h3 {
      @media (min-width: 992px) {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 80px;
      }
      @media #{$maxTab} {
        padding-top: 65px;
      }
      @media (max-width: 550px) {
        max-width: 320px;
        margin: auto;
      }
    }
    .text {
      margin: 30px auto 50px;
      font-size: 18px;
      line-height: 160%;
      @media #{$maxTab} {
        margin: 20px auto 30px;
      }
    }
    .support-btn {
      margin-bottom: 53px;
      display: inline-block;
      text-align: center;
      a {
        width: 100%;
        min-width: 263px;
        line-height: 64px;
      }
      p {
        margin-top: 10px;
        font-weight: 500;
        @media #{$maxTab} {
          font-size: 13px;
        }
      }
    }
    .image.man-img {
      img {
        @media #{$maxTab} {
          max-height: 225px;
          width: 154px;
        }
      }
    }
  }
  .money-back {
    color: #fff;
    @media #{$mobile} {
      background: linear-gradient(154.48deg, #fe6556 -9.73%, #fd3e72 66.22%, #49a8ff 163.3%);
    }
    .text {
      @media #{$mobile} {
        max-width: 430px;
      }
    }
    .support-btn {
      a {
        &.primary__btn {
          background-color: #fff;
          color: $primaryColor;
          svg {
            transition: all 0.3s ease-in-out;
            margin-left: 5px;
          }
          &:hover {
            svg {
              transform: translateX(3px);
            }
            background-color: #ffecf1;
          }
        }
      }
    }
  }
}

// Start Deal
.start-deal.choose-steps {
  background: url(../imgs/landing/deal.png);
  background-size: 100%;
  padding: 80px 0;
  position: relative;
  background-color: #fff;
  @media #{$maxTab} {
    background-position: 48% 0;
  }
  @media #{$mobile} {
    overflow: hidden;
    background-size: 100%;
    background-repeat: repeat;
  }
  h2 {
    font-size: 55px;
    line-height: 80px;
    text-align: center;
    margin-bottom: 80px;
    @media #{$betweentabdesk} {
      font-size: 45px;
    }
    @media #{$tablet} {
      font-size: 40px;
      line-height: 45px;
    }
    @media #{$mobile} {
      font-size: 30px;
      line-height: 35px;
    }
  }
  .pricing-tables {
    padding-top: 0;
    .card-content {
      position: relative;
      &:after {
        position: absolute;
        content: "";
        background-image: url(../imgs/landing/deal-children.png);
        top: 0;
        left: -33px;
        bottom: 0;
        right: 0;
        z-index: 0;
        background-size: 100%;
        background-repeat: no-repeat;
        @media #{$betweentabdesk} {
          left: -93px;
          right: -60px;
        }
        @media #{$tablet} {
          left: -120px;
          right: -90px;
        }
        @media #{$mobile} {
          display: none;
        }
      }
    }
    .marked {
      box-shadow: 0px 4px 17px rgba(136, 136, 136, 0.38);
      max-width: 370px;
      margin: auto;
      position: relative;
      z-index: 2;
    }

    @media #{$mobile} {
      .price-card .offer {
        font-size: 14px;
      }
      .price-card.two .price-top .special-offer {
        width: 185px;
        height: 36px;
        line-height: 36px;
        .month {
          height: 36px;
          line-height: 36px;
        }
      }
      .marked .award {
        img {
          width: 53px;
        }
        p {
          font-size: 10px;
        }
      }
      .price-card .label-radio .price-bottom .every-month {
        font-size: 14px;
      }
      .price-card .label-radio .price-bottom .deleted {
        font-size: 20px;
        color: rgba(91, 88, 99, 0.7);
      }
    }
  }
  .get-wevpn-btn {
    margin-top: 46px;
    @media #{$mobile} {
      margin-top: 0;
    }
  }
}
