.security_bulletin {
  padding-top: 150px;
  @media #{$mobile} {
    padding-top: 100px;
  }
  h1 {
    text-align: center;
    @media #{$mobile} {
      font-size: 26px;
    }
  }
  p {
    font-size: 22px;
    margin: 25px auto 50px;
    text-align: center;
    max-width: 910px;
    @media #{$mobile} {
      font-size: 18px;
    }
  }
  .security_bulletin_table {
    @media #{$mobile} {
      overflow-x: auto;
    }
  }
  .table {
    width: 100%;
    max-width: 100%;
    margin-bottom: 60px;
    border-spacing: 0;
    border-collapse: collapse;
    @media #{$mobile} {
      width: 1080px;
    }
    td,th {
      vertical-align: middle;
      @media #{$maxTab} {
        font-size: 14px;
      }
    }
    thead {
      th {
        border-bottom: 2px solid #ddd;
      }
    }
    tbody {
      tr {
        &:nth-child(odd) {
          background-color: #f8f9fa;
        }
      }
      td {
        padding: 8px;
        line-height: 2;
        border-top: 1px solid #ddd;
        min-width: 180px;
        @media #{$tablet} {
          min-width: 160px;
        }
        @media #{$mobile} {
          min-width: 130px;
        }
        
        a {
          color:#0a84ae
        }
      }
    }
  }
}

.deprecation {
  padding-top: 150px;
  padding-bottom: 100px;
  @media #{$mobile} {
    padding-top: 100px;
  }
  h1 {
    @media #{$mobile} {
      font-size: 26px;
    }
  }
  .text {
    font-size: 22px;
    margin: 20px 0;
    @media #{$mobile} {
      font-size: 18px;
    }
  }
  article {
    margin-top: 50px;
    h2 {
      margin-bottom: 20px;
      font-size: 26px;
    }
    p {
      margin-bottom: 20px;
      font-size: 22px;
      line-height: 1.7;
    }
    ul {
      padding-left: 20px;
      li {
        list-style: disc;
        margin-bottom: 20px;
        font-size: 22px;
      }
    }
  }
}