.servers-search {
  padding-top: 108px;
  padding-bottom: 213px;
  background-image: url(../imgs/map-grey-dark.png);
  background-repeat: no-repeat;
  background-position: center;
  background-color: #fafafa;
  @media (min-width: 1024px) {
    margin-top: 70px;
  }
  @media (min-width: 1024px) {
    margin-top: 70px;
  }

  .dot {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #a3a198;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transition: all 0.3s ease-in-out;
    @media (max-width: 1200px) {
      display: none;
    }

    &.pulsing {
      // animation: pulse 2s infinite ease-out;
      background: $primaryColor;

      &:after {
        content: "";
        border: 1px solid $primaryColor;
        border-radius: 50%;
        height: 20px;
        width: 20px;
        position: absolute;
        top: -7px;
        left: -7px;
        animation: pulsate 2s infinite ease-out;
        opacity: 0;
      }
    }

    &-1 {
      top: 10%;
      left: 9.5%;
    }

    &-2 {
      top: 20%;
      left: 14.5%;
    }

    &-3 {
      top: 15%;
      left: 24.5%;
    }

    &-4 {
      top: 48%;
      left: 27%;
    }

    &-5 {
      top: 30%;
      left: 33%;
    }

    &-6 {
      top: 18%;
      left: 37%;
    }

    &-7 {
      top: 18%;
      left: 53%;
    }

    &-8 {
      top: 21%;
      left: 59%;
    }

    &-9 {
      top: 47%;
      left: 63%;
    }

    &-10 {
      top: 12%;
      left: 83.5%;
    }

    &-11 {
      top: 34%;
      left: 82.5%;
    }

    &-12 {
      top: 57%;
      left: 77%;
    }

    &-13 {
      top: 113%;
      left: 84.5%;
    }

    &-14 {
      top: 60%;
      left: 49%;
    }
  }

  /*******************************************************/

  @keyframes pulsate {
    0% {
      transform: scale(0.1, 0.1);
      opacity: 0;
    }

    50% {
      opacity: 1;
    }

    100% {
      transform: scale(1.2, 1.2);
      opacity: 0;
    }
  }

  /*******************************************/

  @media #{$mobile} {
    padding-top: 73px;
    padding-bottom: 40px;
    background: none;
  }

  @media #{$tablet} {
    padding-top: 88px;
    padding-bottom: 160px;
    background-position: -10px 20%;
    background-size: contain;
    margin-top: 50px;
  }
  @media #{$betweentabdesk} {
    background-size: contain;
  }

  .servers-search-content {
    @media #{$mobile} {
      background-image: url(../imgs/servers-map-small.png);
      background-size: 100% 70%;
      background-repeat: no-repeat;
      background-position: 0px 20px;
    }
  }

  h1 {
    margin: 0 auto 35px;
    position: relative;
    z-index: 2;

    @media #{$maxTab} {
      margin-bottom: 20px;
    }
    @media #{$mobile} {
      padding-top: 27px;
    }
  }

  .servers-list {
    width: 360px;
    margin: auto;
    margin-bottom: 36px;

    @media #{$mobile} {
      text-align: center;
      width: auto;
      margin-bottom: 20px;
    }

    li {
      font-size: 18px;
      line-height: 32px;

      @media #{$mobile} {
        font-size: 14px;
        line-height: 14px;
        display: inline-block;
        margin-right: 12px;

        i {
          font-size: 16px;
        }
      }

      i {
        color: #08ae7e;
        margin-right: 7px;
      }
    }
  }

  form {
    border: 1px solid #ebe8df;
    background-color: #fff;
    box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    position: relative;
    transition: all 0.3s ease-in-out;
    border-radius: 10px;

    &:hover {
      box-shadow: 0px 10px 12px rgba(240, 238, 233, 0.8), 0px 1px 3px #e6e4df;
    }

    input {
      width: 100%;
      display: block;
      padding: 29px 40px;
      border: 0;
      font-size: 20px;
      background-color: transparent;

      @media #{$mobile} {
        height: 56px;
        font-size: 14px;
        line-height: 20px;
        padding: 19px 16px;
      }

      &::placeholder {
        color: #8d8b92;
      }
    }

    button {
      background-color: #fff;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      border: 0;
      cursor: pointer;
      width: 88px;
      height: 100%;
      border-radius: 0 10px 10px 0;

      @media #{$mobile} {
        width: 56px;

        svg {
          width: 16px;
          height: 16px;
        }
      }

      svg.close-icon {
        display: none;
      }
    }
  }
}

/* Start Americas */

body.servers-page {
  background: #fff;
}

.americas {
  margin-top: -114px;
  padding-bottom: 88px;

  @media #{$mobile} {
    margin-top: 24px;
    padding-bottom: 30px;
    background-color: #f5f4f0;
    padding-top: 24px;
  }

  h2 {
    margin-bottom: 33px;
    font-size: 24px;
    line-height: 24px;
    color: #5b5863;

    @media #{$mobile} {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 16px;
    }
  }

  .table {
    background-color: #fff;
    border-color: #ebe8df;
    border-radius: 10px;
    margin-bottom: 0;

    thead {
      tr {
        background-color: #f5f5f5;

        th {
          font-size: 12px;
          line-height: 140%;
          color: #8d8b92;
          font-weight: normal;
          height: 56px;
          border: 1px solid #ebe8df;
          border-top: 0;
          border-bottom: 0;
          vertical-align: middle;
          padding: 10px 6px;
          &.tcp {
            padding-bottom: 14px;
          }

          &:first-child {
            padding-left: 32px;
            border-left: 0;
            min-width: 230px;
            @media #{$betweentabdesk} {
              min-width: 200px;
              padding-left: 20px;
            }
            @media #{$tablet} {
              padding-left: 20px;
            }
          }

          &:last-child {
            border-right: 0;
          }

          &:not(:first-child) {
            width: 110px;
            vertical-align: middle;

            @media #{$tablet} {
              font-size: 10px;
              padding: 5px;
            }

            @media #{$betweentabdesk} {
              width: 85px;
              font-size: 11px;
            }
          }
        }
      }
    }

    tbody {
      tr:first-child {
        td,
        th {
          border-top: 0;
        }
      }
      tr.parent {
        cursor: pointer;
      }

      tr.child {
        background-color: #fbfaf9;
        display: none;

        td {
          height: 44px;
        }

        th {
          color: #5b5863;
          padding-left: 67px;
          height: 44px;
          @media (min-width: 768px) and (max-width: 1199px) {
            padding-left: 55px;
          }
        }
      }

      tr.parent.active {
        .arrow {
          span {
            background-color: #5b5863;

            &:first-of-type {
              transform: rotate(-45deg);
            }

            &:last-of-type {
              transform: rotate(45deg);
            }
          }
        }
      }
      tr.child {
        th > span {
          padding-left: 0;
        }
      }

      th {
        position: relative;
        .arrow {
          cursor: pointer;
          top: 22px;
          right: 25px;
        }

        &:first-child {
          font-size: 14px;
          vertical-align: middle;
          color: #5b5863;
          height: 64px;
          padding: 0;
          padding-left: 32px;
          border: 1px solid #ebe8df;
          border-bottom: 0;
          @media #{$betweentabdesk} {
            padding-left: 20px;
          }
          @media #{$tablet} {
            height: 58px;
            padding-left: 20px;
          }
          .via {
            opacity: 0.7;
            font-weight: normal;
            display: block;
            @media #{$tablet} {
              display: block;
            }
            @media #{$mobile} {
              display: inline-block;
            }
          }

          img {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
          }

          > span {
            padding-left: 35px;
            display: inline-block;
          }
        }
      }

      td {
        padding: 0;
        vertical-align: middle;
        border-top: 1px solid #ebe8df;

        i {
          color: #08ae7e;
        }
      }

      tr {
        th:first-child,
        td:first-child {
          border-left: 0;
        }

        th:last-child,
        td:last-child {
          border-right: 0;
        }
      }
    }
  }
  .continent {
    .server-table {
      box-shadow: 0px 10px 62px rgba(240, 238, 233, 0.8);
      margin-bottom: 16px;
      border-radius: 10px;
      overflow: hidden;
      @media #{$tablet} {
        overflow-x: auto;
        table {
          width: 850px;
        }
      }
    }
    .geo_located {
      opacity: 0.7;
      font-size: 14px;
      margin-left: 35px;
      @media #{$mobile} {
        margin-top: 10px;
        margin-left: 15px;
      }
    }
  }

  .panel-continent {
    .panel-country {
      .panel-country-header {
        background-color: #ffffff;
        border-bottom: 1px solid #ebe8df;
        padding: 0 16px;
        position: relative;
        height: 48px;

        > span {
          font-size: 14px;
          line-height: 48px;
          font-weight: 500;
          display: flex;
          align-items: center;
          height: 48px;
          .via {
            opacity: 0.7;
            font-weight: normal;
          }

          img {
            margin-right: 8px;
          }
        }

        &.active {
          .arrow {
            span {
              background-color: #5b5863;

              &:first-of-type {
                transform: rotate(-45deg);
              }

              &:last-of-type {
                transform: rotate(45deg);
              }
            }
          }
        }
      }

      .has-clusters {
        display: none;

        .panel-cluster {
          background-color: #fbfaf9;

          .panel-cluster-header {
            position: relative;
            border-bottom: 1px solid #ebe8df;
            padding: 0 47px;
            height: 36px;

            span {
              font-size: 14px;
              line-height: 36px;
            }

            .arrow {
              top: 10px;
            }

            &.active {
              .arrow {
                span {
                  background-color: #5b5863;

                  &:first-of-type {
                    transform: rotate(-45deg);
                  }

                  &:last-of-type {
                    transform: rotate(45deg);
                  }
                }
              }
            }
          }

          .panel-cluster-body {
            padding: 9px 15px 9px 47px;
            border-bottom: 1px solid #ebe8df;
            display: none;

            .panel-protocol {
              p {
                font-size: 14px;
                line-height: 32px;
                color: #8d8b92;
                display: flex;
                justify-content: space-between;
                align-items: center;

                i {
                  color: #62d5b4;
                }
              }
            }
          }
        }
      }

      .panel-country-body {
        padding: 14px 16px;
        border-bottom: 1px solid #ebe8df;
        background-color: #fbfaf9;
        display: none;

        .panel-protocol {
          p {
            font-size: 14px;
            line-height: 32px;
            color: #8d8b92;
            display: flex;
            justify-content: space-between;
            align-items: center;

            i {
              color: #62d5b4;
            }
          }
        }
      }
    }
  }

  .europe-content {
    margin-top: 57px;
    margin-bottom: 57px;

    @media #{$mobile} {
      margin-top: 30px;
      margin-bottom: 30px;
    }
  }
  .asia-content,
  .africa-content {
    margin-bottom: 57px;
    &.last {
      margin-bottom: 0;
    }
    @media #{$mobile} {
      margin-bottom: 30px;
    }
  }
}

.table-bordered thead td,
.table-bordered thead th {
  border-bottom-width: 1px;
  border-color: #ebe8df;
}

.table-bordered td,
.table-bordered th {
  border-color: #ebe8df;
}

/* End Americas */

/* Start Servers Benefits */
.servers-benefits {
  h2 {
    width: 55%;
    margin: 0 auto 60px;

    @media #{$mobile} {
      width: 100%;
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 28px;
    }

    @media #{$tablet} {
      width: 75%;
      font-size: 30px;
      margin-bottom: 30px;
    }

    @media #{$betweentabdesk} {
      width: 65%;
    }
  }

  .servers-benefits-list {
    li {
      float: left;
      font-size: 20px;
      line-height: 160%;
      margin-bottom: 25px;
      color: #5a5761;
      width: 50%;
      display: flex;
      align-items: center;
      span {
        margin-right: 10px;
        @media #{$maxTab} {
          svg {
            width: 32px;
          }
        }
      }

      @media #{$mobile} {
        font-size: 16px;
        line-height: 20px;
        width: 100%;
        margin-bottom: 5px;
      }

      @media #{$tablet} {
        font-size: 17px;
        width: 49%;
        line-height: 24px;
        margin-bottom: 20px;
      }
    }
  }

  .benefits-btn {
    text-align: center;
    margin-top: 60px;

    @media #{$mobile} {
      margin-top: 25px;
    }

    @media #{$tablet} {
      margin-top: 30px;
    }

    a {
      padding: 21px 91px;

      @media #{$mobile} {
        display: block;
        padding: 21px 0;
      }
    }
  }
}

/* End Servers Benefits */
