.wedns-wrapper {
  background: #262f3e;
}

body.wedns_page {
  #zdbutton {
    display: none;
  }
}

.wedns-hero-section {
  background: linear-gradient(117.38deg, #fe7e56 -5.95%, #fd3e72 44.95%, #49a8ff 148.61%);
  position: relative;

  @media (min-width: 992px) {
    &:after {
      content: "";
      display: block;
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 583px;
      height: 422px;
      background-image: url(../imgs/wedns/hero-shape.svg);
      z-index: 0;
    }
  }
  #wednsModal {
    .modal-dialog {
      @media (min-width: 768px) {
        max-width: 635px;
      }
    }
    .modal-content {
      background: #252f3d;
      border-radius: 20px;
      color: #ffffff;
      overflow: hidden;
      @media #{$mobile} {
        margin: 0 5px;
      }
      .modal-header {
        text-align: center;
        position: relative;
        flex-direction: column;
        align-items: center;
        border: 0;
        padding: 15px;
        @media #{$mobile} {
          padding: 35px 15px 25px;
        }
        h5 {
          font-size: 22px;
          line-height: 120%;
        }
        p {
          font-weight: 500;
          font-size: 12px;
          line-height: 120%;
          margin-top: 5px;
          @media #{$mobile} {
            font-weight: 400;
            font-size: 14px;
          }
        }
        .close {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          border: 1px solid transparent;
          padding: 0 !important;
          opacity: 1;
          position: absolute;
          top: 25px;
          right: 25px;
          transition: all 0.3s ease-in-out;
          display: flex;
          @media #{$mobile} {
            top: 18px;
          }
          svg {
            transition: all 0.3s ease-in-out;
            margin: auto;
          }
          &:hover {
            border-color: #1c2532;
            svg {
              transform: rotate(180deg);
            }
          }
        }
      }
      .modal-body {
        padding: 0;
        .step {
          .step-header {
            background: #2b3748;
            box-shadow: 0px 4px 6px #19212e;
            cursor: pointer;
            &#headingTwo,
            &#headingThree {
              h2.folded {
                opacity: 0.5;
              }
            }
            h2 {
              padding: 10px 15px;
              font-weight: 500;
              font-size: 16px;
              line-height: 120%;
              display: flex;
              justify-content: space-between;
              align-items: center;
              border-bottom: 1px solid #344255;
              @media #{$mobile} {
                > span:last-child {
                  max-width: 150px;
                  text-align: right;
                }
              }
              .text {
                display: flex;
                align-items: center;
              }
              .step_num {
                text-transform: uppercase;
                color: $primaryColor;
              }
              .true_icon,
              .true_icon_one,
              .true_icon_two,
              .true_icon_three,
              .true_icon_four {
                display: none;
              }
              .app_selected,
              .os_selected,
              .protocol_selected,
              .firewall_selected,
              .country_selected {
                font-size: 10px;
                font-weight: normal;
              }
              .divider {
                display: inline-block;
                margin: 0 10px;
                background-color: #3e4d62;
                height: 15px;
                width: 2px;
              }
            }
          }
          .step-body {
            background: #1c2532;
            border-top: 2px solid $primaryColor;
            padding: 20px 15px;
            max-height: 315px;
            overflow: auto;
            &.step1_body {
              padding: 20px 15px 8px;
            }
            &.step-scroll {
              &::-webkit-scrollbar {
                width: 2px;
                height: 30px;
              }
              &::-webkit-scrollbar-button {
                display: none;
              }
              &::-webkit-scrollbar-thumb {
                background: #767676;
                border-radius: 5px;
              }
              &::-webkit-scrollbar-track {
                background-color: transparent;
              }
            }
            ul.apps {
              display: flex;
              align-items: center;
              justify-content: space-between;
              flex-wrap: wrap;
              li {
                width: 32%;
                margin-bottom: 17px;
                @media (max-width: 499px) {
                  width: 100%;
                }
                @media (min-width: 500px) and (max-width: 767px) {
                  width: 50%;
                }
                input {
                  &:checked + label:before {
                    background-color: $primaryColor;
                    border-color: $primaryColor;
                  }
                  &:checked + label:after {
                    background-color: #fff;
                  }
                }
                label {
                  position: relative;
                  display: flex;
                  align-items: center;
                  margin-bottom: 0;
                  cursor: pointer;
                  padding-left: 26px;
                  &:before {
                    content: "";
                    position: absolute;
                    background: transparent;
                    border: 2px solid #5b697c;
                    width: 16px;
                    height: 16px;
                    left: 0;
                    border-radius: 50%;
                    top: 50%;
                    transform: translateY(-50%);
                  }
                  &:after {
                    content: "";
                    position: absolute;
                    background-color: transparent;
                    width: 9px;
                    height: 9px;
                    border-radius: 50%;
                    left: 3px;
                    top: 50%;
                    transform: translateY(-50%);
                  }
                  .icon {
                    width: 33px;
                    height: 33px;
                    display: flex;
                    background-color: #fff;
                    border-radius: 5px;
                    img {
                      margin: auto;
                    }
                  }
                  .os_info {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    margin-left: 10px;
                    .os_name {
                      display: block;
                      font-weight: 500;
                      font-size: 14px;
                    }
                    .os_version {
                      font-size: 12px;
                      opacity: 0.7;
                    }
                  }
                }
              }
            }

            .wedns-firewall {
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin-bottom: 15px;
              @media #{$mobile} {
                align-items: flex-start;
              }
              .wedns-title {
                font-weight: 500;
                font-size: 16px;
                line-height: 120%;
                margin-bottom: 3px;
              }
              .wedns-txt {
                font-size: 12px;
                line-height: 120%;
                color: #ffffff;
                opacity: 0.7;
                @media #{$mobile} {
                  max-width: 200px;
                }
              }
              .wedns-checker {
                position: relative;
                float: right;
                height: 24px;

                input {
                  -webkit-appearance: none;
                  border-radius: 50px;
                  display: inline-block;
                  background: #afadb2;
                  width: 54px;
                  height: 24px;
                  position: relative;
                  cursor: pointer;
                  transition: all 0.3s ease-in-out;

                  &:checked {
                    background: $primaryColor;

                    &:after {
                      content: "ON";
                      left: 7px;
                      right: inherit;
                    }

                    & + label {
                      right: 4px;
                    }
                  }

                  &:after {
                    content: "OFF";
                    color: #fff;
                    font-size: 12px;
                    position: absolute;
                    top: 3px;
                    right: 5px;
                    transition: all 0.3s ease-in-out;
                  }
                }

                label {
                  cursor: pointer;
                  position: absolute;
                  top: 3px;
                  width: 18px;
                  height: 18px;
                  border-radius: 50%;
                  background: #fff;
                  right: 32px;
                  transition: all 0.3s ease-in-out;
                }
              }
            }

            .select_os_protocol {
              &.tvos_selected {
                .select-os {
                  display: none;
                }
              }
            }

            .select-os {
              .ios_version,
              .mac_version,
              .android_version {
                overflow: hidden;
                .os {
                  width: 48%;
                  float: left;
                  &:last-of-type {
                    float: right;
                  }
                  @media #{$mobile} {
                    width: 100%;
                    float: none;
                  }
                }
              }
              .mac_version,
              .android_version {
                display: none;
              }
            }

            .select-protocol {
              .protocol.anonymous_protocol,
              .protocol.doh_protocol {
                display: none;
              }
              .other-protocol {
                text-decoration-line: underline;
                color: $primaryColor;
                cursor: pointer;
                display: inline-block;
                font-size: 14px;
                svg {
                  margin-left: 5px;
                }
              }
            }

            .select-os,
            .select-protocol,
            .select-location {
              @media #{$mobile} {
                width: 100%;
              }
              p.text {
                font-size: 14px;
                line-height: 120%;
                margin-bottom: 6px;
                @media #{$mobile} {
                  margin-top: 6px;
                }
              }

              .os,
              .protocol,
              .country {
                border: 2px solid #252f3d;
                border-radius: 10px;
                position: relative;
                margin-bottom: 10px;
                padding: 10px 15px;
                input {
                  &:checked + label:before {
                    background-color: $primaryColor;
                    border-color: $primaryColor;
                  }
                  &:checked + label:after {
                    background-color: #fff;
                  }
                }
                label {
                  position: relative;
                  margin-bottom: 0;
                  cursor: pointer;
                  padding-left: 26px;
                  font-weight: 500;
                  width: 100%;
                  font-size: 14px;
                  .recommended {
                    background: $primaryColor;
                    border-radius: 2px;
                    display: inline-block;
                    margin-left: 5px;
                    font-weight: 500;
                    font-size: 12px;
                    padding: 2px 6px;
                  }
                  &:before {
                    content: "";
                    position: absolute;
                    background: transparent;
                    border: 2px solid #5b697c;
                    width: 16px;
                    height: 16px;
                    left: 0;
                    border-radius: 50%;
                    top: 2px;
                  }
                  &:after {
                    content: "";
                    position: absolute;
                    background-color: transparent;
                    width: 9px;
                    height: 9px;
                    border-radius: 50%;
                    left: 3px;
                    top: 5px;
                  }
                  .desc {
                    font-size: 12px;
                    line-height: 16px;
                    opacity: 0.8;
                    margin-top: 5px;
                    display: block;
                    font-weight: 400;
                  }
                }
              }
            }

            .select-location {
              display: none;
              .countries {
                display: flex;
                justify-content: space-between;
                margin-bottom: 5px;
                @media #{$mobile} {
                  flex-wrap: wrap;
                }
                .country {
                  width: 23%;
                  @media #{$mobile} {
                    width: 48%;
                  }
                }
              }
            }

            .tutorial {
              .title {
                font-size: 16px;
                line-height: 120%;
                margin-bottom: 10px;
              }
              .tutorial_text {
                font-size: 12px;
                line-height: 140%;
                opacity: 0.8;
                max-width: 515px;
                margin-bottom: 15px;
              }

              h4 {
                font-weight: 500;
                font-size: 12px;
                line-height: 140%;
                margin-bottom: 18px;
                color: $primaryColor;
                @media (max-width: 499px) {
                  max-width: 250px;
                }
              }
              .download-button {
                width: 132px;
                height: 34px;
                line-height: 34px;
                min-height: 34px;
                margin-bottom: 20px;
                font-size: 14px;
                @media #{$mobile} {
                  width: 100%;
                }
              }
              ul {
                li {
                  display: flex;
                  align-items: center;
                  &:not(:last-child) {
                    margin-bottom: 10px;
                  }
                  > span {
                    width: 16px;
                    min-width: 16px;
                    height: 16px;
                    border-radius: 50%;
                    line-height: 16px;
                    text-align: center;
                    color: #fff;
                    background-color: $primaryColor;
                    font-weight: 500;
                    font-size: 10px;
                    margin-right: 8px;
                  }
                  p {
                    font-size: 12px;
                    line-height: 120%;
                    span {
                      font-weight: 500;
                    }
                    .dns_url {
                      &:hover {
                        .tooltiptext {
                          visibility: visible;
                          opacity: 1;
                        }
                      }
                      .dns_url_input {
                        border: 0;
                        background: none;
                        color: #fd3e72 !important;
                        font-weight: 500;
                        cursor: pointer;
                        min-width: 187px;
                      }
                    }
                    .tooltiptext {
                      visibility: hidden;
                      background-color: $primaryColor;
                      font-size: 12px;
                      color: #fff;
                      text-align: center;
                      border-radius: 5px;
                      padding: 3px 11px;
                      position: absolute;
                      z-index: 1;
                      bottom: 130%;
                      left: 50%;
                      transform: translatex(-50%);
                      opacity: 0;
                      transition: opacity 0.3s;
                      font-weight: 500;
                      text-transform: none;
                      line-height: 1.3;
                      @media #{$mobile} {
                        padding: 4px 8px;
                      }

                      &.changed {
                        background-color: #08ae7e;
                        &:after {
                          border-color: #08ae7e transparent transparent transparent;
                        }
                      }
                      &:after {
                        content: "";
                        position: absolute;
                        top: 100%;
                        left: 50%;
                        margin-left: -5px;
                        border-width: 5px;
                        border-style: solid;
                        border-color: $primaryColor transparent transparent transparent;
                      }
                    }
                  }
                }
              }
              &.ios_tutorial_lowerthan14,
              &.macos_bigSur_tutorial,
              &.macos_tutorial_lowerthan_bigSur,
              &.tvos_tutorial,
              &.android_tutorial_lowerthan9 &.windows_tutorial,
              &.linux_tutorial,
              &.chrome_tutorial,
              &.firefox_tutorial,
              &.brave_tutorial,
              &.edge_tutorial {
                display: none;
              }
            }
          }
          .step_body_footer {
            background: #1c2532;
            padding-bottom: 25px;
            position: relative;
            @media #{$mobile} {
              padding: 10px 0;
              &::before {
                content: "";
                position: absolute;
                top: -9px;
                left: 0;
                right: 0;
                height: 9px;
                background: linear-gradient(180deg, rgba(22, 32, 45, 0) 0%, #16202d 100%);
              }
            }
            .next-step2 {
              width: 132px;
              height: 34px;
              min-height: 34px;
              font-size: 14px;
              display: flex;
              justify-content: center;
              align-items: center;
              margin: auto;
              &:disabled {
                opacity: 0.5;
              }
            }
            .step2_btns {
              text-align: center;
              display: flex;
              align-items: center;
              justify-content: center;
              button {
                width: 132px;
                height: 34px;
                line-height: 34px;
                min-height: 34px;
                margin: 0 10px;
                font-weight: 600;
                font-size: 14px;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                &.back_step1 {
                  border: 1px solid $primaryColor;
                  border-radius: 5px;
                  color: $primaryColor;
                  background: none;
                  &:hover {
                    svg {
                      transform: translateX(-3px);
                    }
                  }
                  svg {
                    margin-right: 10px;
                    transition: all 0.3s ease-in-out;
                    margin-left: unset;
                  }
                }
              }
            }
            .back_step2 {
              width: 132px;
              height: 34px;
              line-height: 34px;
              margin: auto;
              font-weight: 500;
              font-size: 14px;
              display: flex;
              align-items: center;
              justify-content: center;
              border: 1px solid $primaryColor;
              border-radius: 5px;
              color: $primaryColor;
              background: none;
              &:hover {
                svg {
                  transform: translateX(-3px);
                }
              }
              svg {
                margin-right: 10px;
                transition: all 0.3s ease-in-out;
              }
            }
          }
        }
      }
    }
  }
}

