.wedns-hero-img {
  padding: 100px 0 80px;
  color: #ffffff;
  text-align: center;
  position: relative;
  z-index: 2;
  @media (min-width: 1200px) and (max-width: 1600px) {
    height: calc(100vh - 96px);
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 1;
  }
  @media #{$betweentabdesk} {
    padding: 60px 0;
  }
  @media #{$maxTab} {
    padding: 50px 0;
  }
  h1 {
    font-size: 60px;
    line-height: 120%;
    margin-bottom: 30px;
    @media #{$betweentabdesk} {
      font-size: 50px;
    }
    @media #{$tablet} {
      font-size: 44px;
      text-align: center;
      margin-bottom: 45px;
    }
    @media #{$mobile} {
      font-size: 35px;
      line-height: 120%;
      margin-bottom: 52px;
      text-align: center;
    }
  }
  .feats {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex-wrap: wrap;
    @media #{$mobile} {
      text-align: left;
      justify-content: flex-start;
      padding-left: 30px;
    }
    .feat {
      display: flex;
      align-items: center;
      flex-direction: column;
      margin: 0 15px;
      max-width: 200px;
      @media (min-width: 768px) and (max-width: 1200px) {
        margin: 0;
        max-width: unset;
      }
      @media #{$mobile} {
        flex-direction: row;
        margin: 0 0 22px;
        max-width: unset;
      }
      .icon {
        margin-bottom: 30px;
        box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2);
        border-radius: 50%;
        @media (max-width: 1600px) {
          margin-bottom: 10px;
          img {
            width: 70px;
          }
        }
        @media #{$mobile} {
          margin-bottom: 0;
          margin-right: 15px;
          img {
            width: 32px;
          }
        }
      }
      p {
        font-weight: bold;
        line-height: 32px;
        font-size: 24px;
        max-width: 180px;
        @media (min-width: 768px) and (max-width: 1200px) {
          font-size: 20px;
          max-width: 150px;
          line-height: 28px;
        }
        @media #{$mobile} {
          font-size: 20px;
          line-height: 120%;
          max-width: none;
        }
      }
    }
  }
  .wedns-hero-text {
    font-weight: 500;
    font-size: 28px;
    line-height: 140%;
    margin: 50px 0;
    @media (max-width: 1600px) {
      margin: 40px 0;
    }
    @media #{$mobile} {
      font-size: 20px;
      margin: 35px 0 50px;
    }
  }
  .setup-wedns-btn {
    width: 244px;
    height: 64px;
    line-height: 64px;
    padding: 0;
    border: 0;
    font-weight: bold;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2);
    @media #{$maxTab} {
      width: 100%;
      font-size: 18px;
    }
  }
  .setup-wedns {
    background: rgba(#313d50, 0.2);
    border-radius: 10px;
    max-width: 560px;
    padding: 19px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 22px auto 0;
    li {
      a {
        display: block;
      }
    }
    @media #{$tablet} {
      margin-top: 20px;
      justify-content: center;
      max-width: unset;
      li {
        margin: 0 15px;
      }
    }
    @media #{$mobile} {
      flex-wrap: wrap;
      justify-content: center;
      padding: 8px 20px;
      margin: 20px 0 0;
      box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.25);

      li {
        margin: 10px 12px;
        img {
          height: 30px;
        }
      }
    }
  }
}

.wedns-feat-row {
  @media #{$tablet} {
    text-align: center;
  }
  .wedns-feat-img {
    @media #{$mobile} {
      text-align: center;
      img {
        width: 90%;
      }
    }
  }
  h3.subtitle {
    font-size: 24px;
    line-height: 120%;
    color: #ffffff;
    @media #{$tablet} {
      margin-top: 40px;
    }
    @media #{$mobile} {
      margin-top: 30px;
      font-size: 20px;
    }
  }
  h2.title {
    font-weight: 500;
    font-size: 36px;
    line-height: 120%;
    margin: 10px 0 30px;
    color: $primaryColor;
    max-width: 500px;
    @media #{$tablet} {
      margin: 10px 0 15px;
      max-width: unset;
    }
    @media #{$mobile} {
      font-size: 28px;
      margin: 10px 0 25px;
      padding-right: 40px;
    }
  }
  p.txt {
    font-size: 20px;
    line-height: 140%;
    color: #ffffff;
    opacity: 0.8;
    max-width: 530px;
    @media #{$tablet} {
      max-width: unset;
    }
    @media #{$mobile} {
      font-size: 16px;
      padding-right: 20px;
    }
  }
}

