.wedns-freedom {
  padding-bottom: 100px;
  @media #{$mobile} {
    padding-bottom: 80px;
  }
}

.wedns-feaures {
  background: #293343;
  padding: 70px 0 50px;
  text-align: center;
  color: #ffffff;
  @media #{$mobile} {
    padding: 60px 0 50px;
  }
  .wedns-features-title {
    font-size: 36px;
    line-height: 120%;
    @media #{$mobile} {
      font-size: 28px;
    }
    span {
      color: $primaryColor;
    }
  }
  .wedns-features-txt {
    font-weight: 500;
    font-size: 24px;
    line-height: 120%;
    margin: 8px 0 60px;
    @media #{$mobile} {
      font-size: 20px;
      margin: 8px 0 45px;
      padding: 0 20px;
    }
  }
  .row {
    > div {
      display: flex;
    }
  }
  .feature-box {
    background: #313d50;
    box-shadow: 0px 4px 18px #232c3c;
    border-radius: 10px;
    margin-bottom: 30px;
    padding: 30px 27px 35px;
    width: 100%;
    @media #{$mobile} {
      margin-bottom: 20px;
    }
    .icon {
      width: 96px;
      height: 94px;
      display: flex;
      margin: auto;
      img {
        margin: auto;
      }
    }
    .title {
      font-size: 20px;
      line-height: 120%;
      margin: 20px 0;
      @media #{$mobile} {
        margin: 30px 0 15px;
      }
    }
    .txt {
      font-size: 16px;
      line-height: 140%;
      color: rgba(#fff, 0.8);
      @media #{$mobile} {
        font-size: 14px;
      }
      span {
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        font-weight: 500;
      }
    }
  }
}

.wedns-faq {
  padding: 85px 0;
  @media #{$mobile} {
    padding: 55px 0 60px;
  }
  .faq-title {
    font-size: 36px;
    line-height: 120%;
    margin-bottom: 65px;
    color: #ffffff;
    @media #{$mobile} {
      font-size: 28px;
      margin-bottom: 45px;
    }
  }
  .accordion {
    .question {
      &:not(:last-child) {
        border-bottom: 1px solid #333d4e;
      }
      .question-header {
        cursor: pointer;
        h2 {
          font-weight: 500;
          font-size: 20px;
          line-height: 140%;
          color: $primaryColor;
          padding: 15px 8px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          @media #{$mobile} {
            font-size: 16px;
          }
          .q_title {
            align-items: center;
            display: inline-flex;
            @media #{$mobile} {
              align-items: flex-start;
              span {
                padding-right: 30px;
              }
            }
            .info-icon {
              margin-right: 15px;
              min-width: 24px;
              @media #{$mobile} {
                margin-right: 10px;
                width: 20px;
                min-width: 20px;
              }
              path {
                fill: $primaryColor;
              }
            }
          }
          .q-arrow {
            transform: rotate(180deg);
            transition: all 0.2s ease-in-out;
            path {
              stroke: $primaryColor;
            }
          }
          &.collapsed {
            color: rgba(#fff, 0.8);
            .q_title {
              .info-icon {
                path {
                  fill: #fff;
                }
              }
            }
            .q-arrow {
              transform: rotate(0);
              path {
                stroke: #fff;
              }
            }
          }
        }
      }
      .question-body {
        color: #ffffff;
        .txt {
          font-size: 18px;
          line-height: 140%;
          padding: 0 50px;
          margin: 15px 0 30px;
          @media #{$mobile} {
            font-size: 14px;
            margin: 15px 0;
            padding: 0 20px 0 40px;
          }
        }
        .wedns-endpoints {
          border-bottom: 1px dashed #627087;
          padding: 25px 0;
          text-align: center;
          .title {
            font-weight: 500;
            font-size: 28px;
            line-height: 140%;
            @media #{$mobile} {
              font-size: 24px;
            }
          }
          .text {
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: $primaryColor;
            margin-top: 5px;
            @media #{$mobile} {
              font-size: 14px;
            }
          }
        }
        .dns-firewall,
        .dns-weblock {
          padding-bottom: 30px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .dns-title {
            font-weight: 500;
            font-size: 18px;
            line-height: 120%;
            margin-bottom: 5px;
            @media #{$mobile} {
              font-size: 16px;
            }
          }
          .dns-txt {
            font-size: 12px;
            line-height: 120%;
            color: #ffffff;
            opacity: 0.7;
            @media #{$mobile} {
              max-width: 200px;
            }
          }
          .dns-checker {
            position: relative;
            float: right;
            height: 24px;

            input {
              -webkit-appearance: none;
              border-radius: 50px;
              display: inline-block;
              background: #afadb2;
              width: 54px;
              height: 24px;
              position: relative;
              cursor: pointer;
              transition: all 0.3s ease-in-out;

              &:checked {
                background: $primaryColor;

                &:after {
                  content: "ON";
                  left: 7px;
                  right: inherit;
                }

                & + label {
                  right: 4px;
                }
              }

              &:after {
                content: "OFF";
                color: #fff;
                font-size: 12px;
                position: absolute;
                top: 3px;
                right: 5px;
                transition: all 0.3s ease-in-out;
              }
            }

            label {
              cursor: pointer;
              position: absolute;
              top: 3px;
              width: 18px;
              height: 18px;
              border-radius: 50%;
              background: #fff;
              right: 32px;
              transition: all 0.3s ease-in-out;
            }
          }
        }
        #protocol-accordion {
          padding-top: 35px;
          .protocol-header {
            h2 {
              display: flex;
              justify-content: space-between;
              align-items: center;
              background: #2c3648;
              padding: 14px 30px 14px 14px;
              border-bottom: 2px solid #262f3e;
              cursor: pointer;
              &:not(.collapsed) {
                border-bottom: 2px solid #fd3e72;
                svg {
                  transform: rotate(180deg);
                  path {
                    stroke: $primaryColor
                  }
                }
              }
            }
            .p_title {
              .protocol {
                font-weight: 500;
                font-size: 16px;
                line-height: 120%;
                display: block;
              }
              .desc {
                font-size: 12px;
                line-height: 140%;
                opacity: 0.8;
                font-weight: normal;
                display: block;
                margin-top: 3px;
                padding-right: 30px;
              }
            }
            .p-arrow {
              display: flex;
              align-items: center;
              text-decoration: underline;
              color: $primaryColor;
              span {
                font-weight: 500;
                font-size: 12px;
                line-height: 120%;
                color: $primaryColor;
                margin-right: 10px;
              }
              svg {
                transition: all 0.3s ease;
                path {
                  transition: all 0.3s ease;
                }
              }
            }
          }
          .protocol-body {
            padding: 30px 45px;
            background: #1f2634;
            box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.06);
            @media #{$mobile} {
              padding: 25px 15px;
            }
          }
        }
        .dns-table {
          @media #{$mobile} {
            overflow-x: scroll;
          }
          .dot-table,.doh-table {
            .dot-hostname {
              width: 90%;
            }
          }
          .doh-table {       
            .doh-hostname {
              width: 90%;
            }
          }
          .anonymyzed-table {
            @media #{$mobile} {
              width: 500px;
            }
          }
          
          table {
            width: 100%;
            
          }
          thead {
            th {
              font-weight: 500;
              font-size: 14px;
              line-height: 120%;
              padding: 10px;
              @media #{$mobile} {
                font-size: 14px;
              }
            }
          }
          tbody {
            tr:nth-child(odd) {
              background: #262e3f;
            }
            td {
              font-size: 14px;
              height: 40px;
              line-height: 40px;
              padding: 0 10px;
              opacity: 0.7;
              &.dot-hostname,
              &.doh-hostname,
              &.anonymyzed-hostname {

                position: relative;
                opacity: 1;
                .hostname-url {
                  display: inline-block;
                  position: relative;
                }
                input {
                  background: none;
                  border: 0;
                  color: #fd3e72 !important;
                  text-decoration: underline;
                  display: inline-block;
                  overflow: hidden;
                }              
              }
              &.anonymyzed-hostname, &.doh-hostname, &.dot-hostname {
                .copy-ip {
                  width: 80px;
                  text-align: right;
                  position: absolute;
                  right: 0;
                  background: linear-gradient(90deg, rgba(38, 46, 63, 0) 0%, #262e3f 56.25%);
                  height: 20px;
                  top: 50%;
                  transform: translateY(-50%);
                  > span {
                    position: absolute;
                    display: inline-block;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 0;
                    line-height: normal;
                  }
                }
              }
              &.anonymyzed-hostname {
                input {
                  width: 300px;
                }
              }
              @media #{$mobile} {
                font-size: 12px;
              }
            }
          }
        }
      }
    }
  }
}

.wedns-full-featured {
  padding: 65px 0 95px;
  background-color: #293343;
  position: relative;
  z-index: 1;
  overflow: hidden;

  &:before {
    content: "";
    position: absolute;
    height: 465px;
    width: 525px;
    display: block;
    left: 0;
    top: -15px;
    z-index: 0;
    background-image: url(../imgs/wedns/full-featured-up-shape-lg.svg);
    @media #{$betweentabdesk} {
      transform: scale(0.7);
      transform-origin: left top;
    }
  }
  &:after {
    content: "";
    position: absolute;
    height: 368px;
    width: 426px;
    display: block;
    right: 0;
    bottom: -15px;
    z-index: 0;
    background-image: url(../imgs/wedns/full-featured-down-shape-lg.svg);
    @media #{$betweentabdesk} {
      transform: scale(0.7);
      transform-origin: right bottom;
    }
  }

  @media #{$maxTab} {
    padding: 95px 0 60px;
    &:before {
      content: "";
      position: absolute;
      height: 200px;
      width: 375px;
      display: block;
      left: 0;
      top: 0;
      z-index: 0;
      background-image: url(../imgs/wedns/full-featured-up-shape.svg);
    }
    &:after {
      content: "";
      position: absolute;
      height: 200px;
      width: 375px;
      display: block;
      right: 0;
      bottom: 0;
      z-index: 0;
      background-image: url(../imgs/wedns/full-featured-down-shape.svg);
    }
  }
  @media #{$tablet} {
    padding: 65px 0 90px;
  }

  .content {
    color: #fff;
    position: relative;
    z-index: 1;
    .full-featured-title {
      font-size: 55px;
      line-height: 120%;
      text-align: center;
      @media #{$betweentabdesk} {
        font-size: 50px;
      }
      @media #{$maxTab} {
        font-size: 35px;
      }
    }
    .full-featured-txt {
      font-size: 27px;
      line-height: 120%;
      text-align: center;
      color: #ffffff;
      margin: 15px auto 55px;
      max-width: 635px;
      @media #{$maxTab} {
        font-size: 20px;
      }
      @media #{$mobile} {
        margin: 30px auto 60px;
      }
    }
    .comparison-boxes {
      .comparison-box {
        display: flex;
      }
    }

    ul {
      li {
        display: flex;
        align-items: flex-start;
        font-weight: 500;
        font-size: 20px;
        line-height: 140%;
        @media #{$betweentabdesk} {
          font-size: 16px;
        }

        @media #{$maxTab} {
          font-size: 14px;
        }
        &:not(:last-child) {
          margin-bottom: 15px;
        }
        &.opacity-6 {
          opacity: 0.6;
        }
        img {
          margin-right: 20px;
          @media #{$maxTab} {
            width: 20px;
            margin-right: 12px;
          }
        }
      }
    }

    .wedns-feat-box {
      padding: 30px;
      background: #313d50;
      box-shadow: 0px 4px 12px #242e40;
      border-radius: 10px;
      width: 100%;
      @media #{$betweentabdesk} {
        padding: 30px 20px;
      }
      @media #{$maxTab} {
        padding: 30px 22px;
      }
      .wedns-logo {
        text-align: center;
        img {
          height: 74px;
          @media #{$maxTab} {
            height: 40px;
          }
        }
      }
      .wedns-divider {
        border-bottom: 1px dashed #49566a;
        margin: 44px 0 57px;
        @media #{$tablet} {
          margin: 32px 0 43px;
        }
        @media #{$mobile} {
          margin: 26px 0 33px;
        }
      }
    }
    .wevpn-feat-box {
      background: linear-gradient(144.61deg, #fd3e72 22.48%, #ff81a3 82.6%);
      box-shadow: 0px 4px 42px rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      padding: 8px;
      position: relative;
      width: 100%;
      .moneyBack {
        position: absolute;
        top: -40px;
        right: -40px;
        transform: matrix(0.97, 0.27, -0.25, 0.96, 0, 0);
        @media #{$betweentabdesk} {
          top: -30px;
          right: -30px;
          img {
            width: 100px;
          }
        }
        @media #{$maxTab} {
          top: -20px;
          right: -15px;
          img {
            width: 60px;
          }
        }
      }
      .wevpn-logo {
        background-color: #fff;
        padding: 22px 0;
        text-align: center;
        border-radius: 10px 10px 0 0;
        @media #{$maxTab} {
          padding: 18px 0;
          img {
            height: 40px;
          }
        }
      }
      p.premium {
        background: transparent;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 30px;
        line-height: 51px;
        color: #ffffff;
        text-align: center;
        @media #{$betweentabdesk} {
          font-size: 24px;
        }
        @media #{$maxTab} {
          font-size: 20px;
          line-height: 38px;
        }
      }
      ul {
        background-color: #fff;
        padding: 30px;
        border-radius: 0 0 10px 10px;
        @media #{$betweentabdesk} {
          padding: 30px 15px;
        }
        @media #{$maxTab} {
          padding: 25px 15px 30px;
        }
        li {
          color: #293343;
        }
      }
    }

    .wevpn-button {
      text-align: center;
      @media #{$mobile} {
        margin-bottom: 50px;
      }
      a {
        width: 100%;
        margin: 35px 0 15px;
        height: 75px;
        line-height: 75px;
        display: flex;
        align-items: center;
        justify-content: center;
        @media #{$maxTab} {
          font-size: 20px;
          margin: 20px 0 10px;
        }
      }
    }
    .wedns-button {
      a {
        font-weight: 500;
        font-size: 20px;
        background-color: #3e4c63;
        border-radius: 5px;
        color: #ffffff;
        height: 75px;
        line-height: 75px;
        text-align: center;
        margin: 35px 0 15px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        @media #{$maxTab} {
          margin: 20px 0 0;
        }
        svg {
          transition: all 0.3s ease-in-out;
          margin-left: 10px;
        }
        &:hover {
          svg {
            transform: translateX(3px);
          }
        }
      }
    }
  }
}

.wedns-footer {
  background: rgba(38, 47, 62, 0.6);
  padding: 22px 0;
  @media #{$maxTab} {
    padding: 25px 0;
    text-align: center;
    .logo {
      img {
        height: 33px;
      }
    }
  }
}
