@media (min-width: 992px) {
  .navbar-expand-lg .container {
    flex-wrap: nowrap;
  }
}

header.wevpn-birthday .dropdown-toggle::after {
  @media(min-width: 992px) {
    background-image: url(../imgs/arrow-w.png);
  }
}
header.wevpn-birthday .navbar-light .navbar-nav .nav-link:hover {
  @media(min-width: 992px) {
    color: #fff !important;
    opacity: 0.8;
  }
}
header.wevpn-birthday .navbar.nav-fixed .navbar-nav .nav-link:hover {
  color: $primaryColor !important;
}

.hero-img.wevpn-birthday {
  background: linear-gradient(109.86deg, #fe6556 -5.54%, #fd3e72 43.58%, #49a8ff 118.56%);
  padding: 65px 0 45px;
  overflow: hidden;
  &.landingpage {
    @media #{$betweentabdesk} {
      padding-top: 30px;
    }
    @media #{$tablet} {
      padding-top: 70px;
    }
    @media #{$mobile} {
      padding-top: 60px;
    }
  }
  @media #{$betweentabdesk} {
    padding-top: 90px;
  }
  @media #{$tablet} {
    text-align: center;
    padding: 25px 0 45px;
  }
  @media #{$mobile} {
    background: linear-gradient(167.16deg, #FE6556 -28.69%, #FD3E72 52.57%, #49A8FF 176.61%);
    text-align: center;
    padding-top: 20px;
  }

  .hero-img-title {
    font-size: 47px;
    line-height: 59px;
    margin-bottom: 20px;
    @media #{$betweentabdesk} {
      font-size: 40px;
      line-height: 45px;
    }
    @media #{$tablet} {
      font-size: 42px;
      line-height: 45px;
    }
    @media #{$mobile} {
      font-size: 28px;
      line-height: 35px;
    }
  }
  .hero-img-txt {
    font-weight: 500;
    font-size: 26px;
    line-height: 140%;
    @media (min-width: 768px) and (max-width: 1199px) {
      font-size: 22px;
    }
    @media #{$mobile} {
      font-size: 18px;
    }
  }
  .image {
    @media (max-width: 1199px) {
      img {
        max-width: 100%;
        display: block;
      }
    }
  }
  .hero-img-content .homepage-2-button {
    @media #{$mobile} {
      width: 100%;
      display: block;
      .hero-img-btn {
        width: 100%;
      }
    }
  }
}

.wevpn-feats {
  background: #f4f6f7;
  padding: 60px 0 75px;
  .feat-icon {
    width: 112px;
    height: 98px;
    margin: auto;
    display: flex;
    img {
      margin: auto;
    }
  }
  .advantage {
    @media #{$mobile} {
      margin-bottom: 50px;
      &.last {
        margin-bottom: 0;
      }
    }
  }
  .advantage-text {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      top: 0;
      right: -15px;
      height: 100%;
      width: 1px;
      background-color: #ebe8df;
      @media #{$mobile} {
        display: none;
      }
    }
    &.last {
      &:after {
        display: none;
      }
    }
    h4 {
      margin: 40px 0 20px;
      font-size: 20px;
      line-height: 20px;
      @media #{$tablet} {
        margin: 25px 0 10px;
        font-size: 14px;
      }
      @media #{$mobile} {
        font-size: 18px;
        margin: 35px 0 18px;
      }
    }
    p {
      font-size: 16px;
      line-height: 28px;
      width: 90%;
      margin: auto;
      @media #{$maxTab} {
        font-size: 14px;
        line-height: 22px;
        width: 100%;
      }
      @media #{$mobile} {
        width: 90%;
      }
    }
  }
}

.homepage-buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media #{$tablet} {
    justify-content: center;
  }
  @media #{$mobile} {
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
  }
}

.get-wevpn-button {
  a {
    width: 260px;
    line-height: 64px;
    @media #{$maxTab} {
      font-size: 16px;
    }
    @media #{$mobile} {
      width: 100%;
    }
  }
  p {
    font-size: 14px;
    line-height: 20px;
    margin-top: 10px;
  }
}

.unblock-content {
  background: #f4f6f7;
  padding: 100px 0;
  position: relative;
  @media #{$tablet} {
    padding: 80px 0;
  }
  @media #{$mobile} {
    padding: 60px 0;
    text-align: center;
    .image {
      img {
        margin-bottom: 40px;
      }
    }
  }

  .unblock {
    max-width: 400px;
    margin-left: auto;
    @media #{$maxTab} {
      max-width: none;
    }
    &-title {
      font-size: 40px;
      line-height: 44px;
      @media #{$maxTab} {
        font-size: 32px;
        line-height: 42px;
      }
      .span {
        color: $primaryColor;
      }
    }
    &-text {
      font-size: 22px;
      line-height: 32px;
      max-width: 380px;
      margin: 40px 0;
      @media #{$maxTab} {
        font-size: 18px;
        line-height: 28px;
        max-width: none;
      }
    }
    .get-wevpn-button {
      display: inline-block;
      text-align: center;
      a {
        width: 100%;
        min-width: 260px;
      }
    }
  }
}

.anonymous {
  background-color: #fff;
  padding: 120px 0;
  @media #{$tablet} {
    padding: 80px 0;
  }
  @media #{$mobile} {
    padding: 60px 0;
    text-align: center;
    .image {
      img {
        margin-bottom: 40px;
      }
    }
  }
  &-title {
    font-size: 40px;
    line-height: 50px;
    @media #{$maxTab} {
      font-size: 32px;
      line-height: 42px;
    }
    .span {
      color: $primaryColor;
    }
  }
  &-desc {
    font-size: 22px;
    line-height: 32px;
    max-width: 390px;
    margin-top: 40px;
    @media #{$maxTab} {
      font-size: 18px;
      line-height: 28px;
      max-width: none;
    }
  }
}

.wevpn-devices {
  background: linear-gradient(115.13deg, #fe6556 -8.42%, #fd3e72 51.42%, #49a8ff 112.52%);
  padding: 100px 0;
  @media #{$tablet} {
    padding: 80px 0 130px;
    text-align: center;
  }
  @media #{$mobile} {
    padding: 65px 0 185px;
    text-align: center;
    background: linear-gradient(154.48deg, #fe6556 -9.73%, #fd3e72 66.22%, #49a8ff 163.3%);
  }
  .image {
    @media #{$maxTab} {
      margin-bottom: 40px;
    }
  }
  &-content {
    color: #fff;
    @media #{$tablet} {
      text-align: center;
      img {
        width: auto;
      }
    }
    .get-wevpn-button {
      a {
        background-color: #fff;
        color: $primaryColor;
      }
    }
  }
  &-desc {
    margin: 40px 0;
    @media #{$maxTab} {
      margin: 30px 0;
    }
  }
  &-icons {
    max-width: 355px;
    margin-bottom: 25px;
    @media #{$maxTab} {
      text-align: center;
      max-width: unset;
    }
    li {
      display: inline-block;
      margin-right: 5px;
      margin-bottom: 15px;
    }
  }
}

.wevpn_communities {
  position: relative;
  .content {
    position: absolute;
    top: -51px;
    left: 15px;
    right: 15px;
    z-index: 1;
    @media #{$tablet} {
      top: -85px;
    }
    @media #{$mobile} {
      top: -135px;
    }
  }
  .widget {
    border-radius: 10px;
    display: flex;
    @media #{$tablet} {
      flex-direction: column;
      text-align: center;
    }
    @media #{$mobile} {
      margin-bottom: 20px;
      filter: drop-shadow(0px 4px 9px rgba(0, 0, 0, 0.15));
    }
    &.twitter {
      background: #16addf;
      .widget__text {
        background: #33ccff;
      }
      .widget__link {
        a:hover {
          box-shadow: 0px 6px 9px darken(#16addf, 5);
        }
      }
    }
    &.discord {
      background: #5870c5;
      .widget__text {
        background: #7289da;
      }
      .widget__link {
        a:hover {
          box-shadow: 0px 6px 9px darken(#5870c5, 5);
        }
      }
    }
    &.reddit {
      background: #e94204;
      .widget__text {
        background: #ff4500;
      }
      .widget__link {
        a:hover {
          box-shadow: 0px 6px 9px darken(#e94204, 5);
        }
      }
    }
    &__text {
      flex-basis: 51%;
      border-radius: 10px 0px 30px 10px;
      padding: 29px 18px;
      color: #f9f8f4;
      font-weight: 500;
      font-size: 16px;
      line-height: 140%;
      @media #{$tablet} {
        border-radius: 10px 10px 20px 20px;
        padding: 20px 30px;
      }
      @media #{$mobile} {
        font-size: 14px;
        padding: 19px 15px;
      }
    }
    &__link {
      flex-basis: 49%;
      display: flex;
      align-items: center;
      justify-content: center;
      @media #{$tablet} {
        padding: 17px;
      }
      a {
        transition: all 0.3s ease-in-out;
        width: 134px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background-color: #fff;
        border-radius: 5px;
        @media #{$mobile} {
          height: 44px;
          line-height: 44px;
        }
      }
    }
  }
}

.servers.homepage-1 {
  padding: 120px 0 95px;
  @media #{$betweentabdesk} {
    padding: 150px 0 95px;
  }
  @media #{$tablet} {
    padding: 140px 0 80px;
  }
  @media #{$mobile} {
    padding: 225px 0 60px;
  }
  .row {
    > div {
      display: flex;
    }
  }
  .server-features {
    margin-bottom: 70px;
    @media #{$tablet} {
      margin-bottom: 50px;
    }
    @media #{$mobile} {
      max-width: 215px;
      margin: 0 auto 45px;
      li {
        margin-right: 0;
      }
    }
    @media #{$maxTab} {
      svg {
        width: 30px;
        height: 30px;
      }
    }
  }
  .servers-title {
    @media #{$maxTab} {
      font-size: 28px;
      line-height: 38px;
      width: 100%;
    }
    @media #{$tablet} {
      width: 70%;
    }
  }
  .server-feat {
    background-color: #fff;
    box-shadow: 0px 4px 20px #e1e9ed;
    border-radius: 10px;
    text-align: center;
    padding: 33px;
    width: 100%;
    max-width: 370px;
    margin: 0 auto 64px;
    @media #{$tablet} {
      padding: 15px;
      margin-bottom: 50px;
    }
    @media #{$mobile} {
      margin-bottom: 35px;
    }
    .image {
      width: 250px;
      height: 240px;
      margin: auto;
      display: flex;
      padding-bottom: 20px;
      box-sizing: content-box;
      border-bottom: 1px solid #fcefef;
      @media #{$tablet} {
        width: 160px;
        height: 170px;
        img {
          display: block;
          max-width: 100%;
        }
      }
      img {
        margin: auto;
      }
    }
    &-info {
      padding-top: 22px;
      h2 {
        font-size: 56px;
        line-height: 56px;
        margin-bottom: 22px;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        letter-spacing: -3px;
        .span {
          font-size: 20px;
          line-height: 46px;
          color: #a29cb0;
        }
        @media #{$tablet} {
          font-size: 45px;
          line-height: 46px;
          margin-bottom: 10px;
        }
        @media #{$mobile} {
          margin-bottom: 10px;
        }
      }

      .span {
        font-size: 20px;
        line-height: 20px;
        @media #{$tablet} {
          font-size: 18px;
        }
      }
    }
    &.three {
      h2 {
        font-size: 110px;
        position: relative;
        top: -10px;
        font-weight: normal;
      }
    }
  }
  .server-btn {
    padding-bottom: 0;
    @media #{$maxTab} {
      a {
        display: inline-block;
        width: 228px;
        height: 64px;
        line-height: 64px;
        min-height: 64px;
        font-size: 16px;
        padding: 0;
      }
    }
  }
}

.home-trustbox {
  background: #fafafa;
  padding: 50px 0;
  iframe {
    @media #{$mobile} {
      height: 140px !important;
    }
  }
}
