.press-banner {
  background-color: #fff;
  padding: 160px 0 75px;
  position: relative;
  @media #{$betweentabdesk} {
    padding-top: 130px;
  }
  @media #{$tablet} {
    padding-top: 140px;
  }
  @media #{$mobile} {
    padding: 130px 0 55px;
    overflow: hidden;
    text-align: center;
  }
  .shape {
    position: absolute;
    right: 0;
    bottom: 0;
    @media #{$betweentabdesk} {
      width: 1150px;
    }
    @media #{$tablet} {
      width: 850px;
    }
    @media #{$mobile} {
      width: 1000px;
    }
  }
  h1 {
    font-size: 56px;
    line-height: 66px;
    margin-bottom: 22px;
    @media #{$betweentabdesk} {
      font-size: 50px;
      margin-bottom: 20px;
      line-height: 60px;
    }
    @media #{$maxTab} {
      font-size: 50px;
      line-height: 45px;
      margin-bottom: 20px;
    }
    @media #{$mobile} {
      font-size: 38px;
    }
  }
  p {
    font-size: 22px;
    line-height: 34px;
    opacity: 0.8;
    max-width: 485px;
    @media #{$betweentabdesk} {
      line-height: 30px;
    }
    @media #{$maxTab} {
      font-size: 18px;
      line-height: 28px;
    }
    @media #{$mobile} {
      width: 97%;
    }
  }
  .image {
    position: relative;
    z-index: 1;
    text-align: right;
    @media #{$mobile} {
      margin-top: 80px;
    }
  }
}

.press-advantages{
  background: #fafafa;
  padding: 80px 0;
  @media #{$mobile} {
    padding: 60px 0;
  }
  h2 {
    margin-bottom: 65px;
    @media #{$tablet} {
      margin-bottom: 50px;
    }
    @media #{$mobile} {
      margin-bottom: 50px;
      font-size: 23px;
    }
  }
  .advantage {
    &.last {
      @media #{$tablet} {
        margin-top: 50px;
      }
    }
    .image {
      width: 125px;
      height: 90px;
      line-height: 90px;
      text-align: center;
      margin: auto;
    }
    .advantage-text {
      @media #{$mobile} {
        margin-bottom: 40px;
        &.last {
          margin-bottom: 0;
        }
      }
      h4 {
        margin: 40px 0 20px;
        font-weight: bold;
        font-size: 20px;
        line-height: 20px;
        @media #{$tablet} {
          margin: 20px 0 15px;
          font-size: 18px;
        }
        @media #{$mobile} {
          margin: 20px 0 12px;
        }
        &.last {
          margin: 50px 0 20px;
          @media #{$tablet} {
            margin: 30px 0 15px;
          }
          @media #{$mobile} {
            margin: 20px 0 12px;
          }
        }
      }
      p {
        max-width: 323px;
        font-size: 16px;
        line-height: 28px;
        margin: auto;
        @media #{$tablet} {
          max-width: 340px;
          font-size: 15px;
        }
        @media #{$mobile} {
          width: 95%;
        }
        @media #{$betweentabdesk} {
          width: 86%;
        }
      }
    }
  }
}

// Start Wevpn Ethics
.wevpn-ethics {
  padding: 45px 0 70px;
  background-color: $primaryColor;
  color: #fff;
  @media #{$mobile} {
    padding: 60px 0 35px;
  }
  h2 {
    font-size: 36px;
    line-height: 44px;
    @media #{$tablet} {
      font-size: 30px;
      line-height: 40px;
    }
    @media #{$mobile} {
      font-size: 24px;
      line-height: 34px;
    }
  }
  .ethics-text {
    font-size: 20px;
    line-height: 23px;
    max-width: 620px;
    margin: 16px auto 46px;
    @media #{$tablet} {
      font-size: 18px;
    }
    @media #{$mobile} {
      font-size: 16px;
      line-height: 20px;
    }
  }
  > .row {
    > div {
      display: flex;
    }
  }
  .box {
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    flex: 1;
    @media #{$mobile} {
      margin-bottom: 25px;
      padding: 25px 20px;
    }

    .image {
      max-width: 220px;
      height: 95px;
      line-height: 95px;
      margin: 0 auto 25px;
      text-align: center;
      display: flex;
      justify-content: center;
    }
    .text {
      font-size: 16px;
      line-height: 26px;
      max-width: 420px;
      color: #5b5863;
      @media #{$maxTab} {
        font-size: 14px;
        line-height: 26px;
      }
    }
  }
}

.press-roadmap.press-new {
  background: #fafafa;
  padding: 100px 0 230px;
  @media #{$tablet} {
    padding: 80px 0;
  }
  @media #{$mobile} {
    padding: 60px 0 30px;
  }

  .press-roadmap-title {
    @media #{$mobile} {
      margin-bottom: 40px;
    }
  }
  /* Timeline */

  .timeline {
    position: relative;
    &:before {
      content: " ";
      position: absolute;
      background: #ebebeb;
      height: 4px;
      bottom: 27px;
      left: 150px;
      width: 76%;
      @media #{$betweentabdesk} {
        left: 100px;
        width: 78%;
      }
      @media #{$maxTab} {
        width: 4px;
        height: 75%;
        left: 5px;
        top: 40px;
      }
      @media #{$tablet} {
        left: 200px;
        top: 105px;
      }
    }
    .timeline-item {
      display: table-cell;
      min-width: 300px;
      padding-right: 30px;
      max-width: 300px;
      position: relative;
      @media #{$betweentabdesk} {
        min-width: 240px;
        padding-right: 20px;
      }
      @media #{$tablet} {
        display: flex;
        max-width: none;
        min-width: auto;
        padding-left: 0;
        padding-right: 0;
        justify-content: space-between;
        align-items: center;
      }
      @media #{$mobile} {
        display: block;
        max-width: none;
        min-width: auto;
        padding-left: 40px;
        padding-right: 0;
      }
      .timeline-badge {
        width: 32px;
        height: 32px;
        line-height: 32px;
        text-align: center;
        position: absolute;
        left: 50%;
        margin-left: -30px;
        border-radius: 50%;
        bottom: 11px;
        background: #f4f4f4;
        border: 2px solid $primaryColor;
        @media #{$tablet} {
          margin-left: 0;
          position: relative;
          left: 0;
          bottom: 0;
          order: 2;
        }
        @media #{$mobile} {
          left: 20px;
          bottom: 0;
          top: 30px;
        }
        &:after {
          content: "";
          position: absolute;
          width: 18px;
          height: 18px;
          background-color: $primaryColor;
          border-radius: 50%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }

      .timeline-panel {
        position: relative;
        background: #ffffff;
        border-radius: 10px;
        padding: 15px;
        bottom: 0;
        display: inline-block;
        width: 100%;
        margin-bottom: 20px;
        box-shadow: 0px 4px 20px rgba(234, 228, 228, 0.5);
        @media #{$tablet} {
          max-width: 440px;
          order: 3;
        }
        @media #{$mobile} {
          margin-bottom: 30px;
          padding: 20px 15px 15px 30px;
        }
        &:before {
          position: absolute;
          bottom: -12px;
          left: 50%;
          display: inline-block;
          content: " ";
          transform: translateX(-50%);
          border-right: 16px solid transparent;
          border-top: 16px solid #fff;
          border-bottom: 0 solid #fff;
          border-left: 16px solid transparent;
          @media #{$maxTab} {
            bottom: auto;
            left: -14px;
            border-right: 16px solid #fff;
            border-top: 16px solid transparent;
            border-bottom: 16px solid transparent;
            border-left: 16px solid transparent;
            top: 30px;
          }
          @media #{$tablet} {
            left: -30px;
            top: 55%;
            transform: translateY(-50%);
          }
        }
        li {
          line-height: 24px;
          position: relative;
          padding-left: 20px;
          margin-bottom: 5px;
          @media #{$mobile} {
            font-size: 14px;
            line-height: 20px;
            padding-left: 13px;
          }

          &:first-child {
            color: #fd3e72;
            font-size: 18px;
            line-height: 28px;
            font-weight: 500;
            margin-left: -15px;
            margin-bottom: 12px;
            &:before {
              display: none;
            }
            .bull {
              color: #5b5863;
            }
            span:last-child {
              font-size: 16px;
              font-weight: 400;
            }
          }
          &:before {
            content: "";
            position: absolute;
            top: 10px;
            left: 0;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: $primaryColor;
            @media #{$mobile} {
              top: 7px;
            }
          }
        }
      }

      .time {
        background: $primaryColor;
        color: #ffffff;
        width: 154px;
        height: 63px;
        line-height: 63px;
        font-weight: bold;
        font-size: 18px;
        text-align: center;
        border-radius: 10px;
        margin: auto;
        bottom: -90px;
        position: relative;
        @media #{$tablet} {
          position: relative;
          bottom: 0;
          order: 1;
          margin: 0;
        }
        @media #{$mobile} {
          display: none;
        }
        &:before {
          position: absolute;
          top: -12px;
          left: 50%;
          display: inline-block;
          content: " ";
          transform: translateX(-50%);
          border-right: 16px solid transparent;
          border-top: 0 solid $primaryColor;
          border-bottom: 16px solid $primaryColor;
          border-left: 16px solid transparent;
          @media #{$tablet} {
            right: -26px;
            top: 50%;
            left: auto;
            transform: translateY(-50%);
            border-right: 16px solid transparent;
            border-top: 16px solid transparent;
            border-bottom: 16px solid transparent;
            border-left: 16px solid $primaryColor;
          }
        }
        
      }
    }
  }
}

.press-assets.press-new {
  background: #f4f4f4;
  .press-assets-header {
    background: $primaryColor;
    .press-assets-btn {
      background: #fff;
      color: $primaryColor;
      &:hover {
        svg {
          path {
            fill: $primaryColor;
          }
        }
      }
    }
  }

  .press-assets-content {
    padding-bottom: 0;
    @media #{$maxTab} {
      padding-bottom: 60px;
    }
    .press-assets-logo.media-assets {
      margin-top: 0;
    }
    .press-assets-logo .download-icon {
      border: 1px solid rgba(91, 88, 99, 0.3);
      &:hover {
        border-color: $primaryColor;
      }
      svg {
        path {
          transition: all 0.3s ease-in-out;
        }
      }
    }

    .press-assets-logo .logo-assets-header .dropdown .btn {
      background-color: $primaryColor;
      color: #fff;
      border-color: $primaryColor;
      &:after {
        background-image: url(../imgs/press/arrow-w-down.svg);
      }
    }
    .press-assets-logo .logo-assets-header .dropdown .dropdown-menu {
      background-color: $primaryColor;
    }
  }
  .press-assets-content .press-assets-logo .logo-assets-header {
    color: #5b5863;
    ul li {
      padding: 10px 12px;
      border-radius: 10px;
      margin-right: 0;
      @media #{$tablet} {
        padding: 7px 10px;
        border-radius: 5px;
      }
      &:after {
        display: none;
      }
      &.active {
        background-color: $primaryColor;
        color: #fff;
      }
    }
  }
  .press-assets-content .press-assets-logo-content .item {
    box-shadow: none;
  }
  .press-assets-content .press-assets-logo-content .box .slider .owl-carousel {
    width: auto;
    position: relative;
  }
}
.wevpn-inquiries {
  display: flex;
  color: #fff;
  @media #{$maxTab} {
    flex-direction: column;
    text-align: center;
  }
  .box {
    .title {
      height: 190px;
    }
    h2.box_title {
      font-size: 36px;
      line-height: 44px;
      padding: 55px 0 15px;
      text-align: center;
      @media #{$betweentabdesk} {
        font-size: 28px;
        line-height: 38px;
      }
      @media #{$mobile} {
        font-size: 24px;
        line-height: 30px;
      }
    }
    p.box-text {
      font-size: 20px;
      line-height: 25px;
      text-align: center;
      padding: 0 20px;
      @media #{$betweentabdesk} {
        font-size: 18px;
      }
      @media #{$mobile} {
        font-size: 16px;
        line-height: 20px;
      }
    }
    .box-content {
      padding: 0 0 55px;
      margin: 0 50px;
      text-align: center;
      @media #{$betweentabdesk} {
        margin: 0 20px;
      }
      @media #{$maxTab} {
        margin: 0 auto;
        padding: 50px 30px;
      }
      @media #{$tablet} {
        max-width: 500px;
      }
      .image {
        text-align: center;
        margin-bottom: 50px;
        @media (max-width: 1200px) {
          margin-bottom: 30px;
          img {
            transform: scale(0.8);
          }
        }
      }
    }
    h4 {
      font-size: 20px;
      line-height: 20px;
      margin-bottom: 21px;
    }
    p {
      font-size: 16px;
      line-height: 28px;
      text-align: center;
      @media #{$mobile} {
        padding: 0 14px;
      }
      a {
        text-decoration: underline !important;
        font-weight: 500;
        color: $primaryColor;
        &:hover {
          color: $primaryColor;
        }
      }
    }
    &.one {
      background: #fff;
      color: #5b5863;
      flex: 1;
      .box-content {
        max-width: 375px;
        margin-left: auto;
        @media (min-width: 1200px) {
          padding-left: 50px;
        }
        @media #{$maxTab} {
          margin-right: auto;
        }
        @media #{$tablet} {
          max-width: 500px;
        }
        .email {
          svg {
            margin-right: 8px;
          }
          a {
            font-weight: bold;
            font-size: 16px;
            line-height: 28px;
            transition: all 0.3s ease;
            &:hover {
              color: $primaryColor;
            }
          }
        }
        p {
          margin: 13px 0 0;
        }
      }
    }
    &.two {
      background: linear-gradient(138.52deg, #FE6556 -5.81%, #FD3E72 46.14%, #49A8FF 184.48%);
      padding: 0 20px;
      @media #{$betweentabdesk} {
        max-width: 341px;
      }
      .social-links {
        max-width: 125px;
        margin: auto;
        text-align: left;
        li {
          margin-bottom: 12px;
          a {
            font-weight: 500;
            font-size: 16px;
            color: #fff;
            display: inline-block;
            svg {
              margin-right: 5px;
            }
          }
        }
      }
    }
    &.three {
      background: #fff;
      flex: 1;
      color: #5b5863;
      .box-content {
        max-width: 375px;
        @media #{$maxTab} {
          margin-left: auto;
        }
        @media #{$tablet} {
          max-width: 500px;
        }
      }
    }
  }
}
