.risk-free-hero {
  padding: 155px 0 100px;
  @media (min-width: 1200px) and (min-height: 601px) {
    height: calc(100vh - 32px);
    display: flex;
    align-items: center;
  }
  @media (min-height:1000px){
    height: auto;
  }
  @media (max-height:600px) {
    height: auto;
  }

  @media #{$maxTab} {
    padding: 45px 0 60px;
  }

  &__content {
    p {
      margin: 24px 0 60px;
      @media #{$maxTab} {
        margin: 24px 0 40px;
      }
    }
  }
}

.wevpn-free-vpn {
  background-image: url(../imgs/risk-free/shapes-bg.svg);
  background-position: center;
  background-size: cover;
  padding: 75px 0 100px;
  @media #{$mobile} {
    background-image: url(../imgs/risk-free/shapes-bg-m.png);
    padding: 60px 0;
  }
  .box {
    width: 100%;
  }
  .box.wevpn {
    background: linear-gradient(141.37deg, #FE6556 3.52%, #FD3E72 67%, #49A8FF 127.88%);
    box-shadow: 0px 4px 34px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 8px;
    @media #{$mobile} {
      margin-bottom: 30px;
    }
    .wevpn-logo {
      padding: 28px 30px 30px;
      @media #{$tablet} {
        padding: 20px;
      }
      @media #{$mobile} {
        padding: 20px;
        img {
          height: 36px;
        }
      }
    }
  }
  .box.free-vpn {
    background: #FAFAFA;
    box-shadow: 0px 4px 34px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    h3 {
      font-size: 32px;
      line-height: 140%;
      padding: 45px 30px 30px;
      @media #{$tablet} {
        padding: 35px 20px 20px;
      }
      @media #{$mobile} {
        padding: 20px;
        font-size: 20px;
      }
    }
    .divider {
      border-top: 1px dashed #D8D8D8;
      margin: 0 30px;
      @media #{$mobile} {
        margin: 0 20px;
      }
    }
  }
  .list {
    padding: 30px;
    @media #{$tablet} {
      padding: 25px 12px 30px;
    }
    @media #{$mobile} {
      padding: 20px;
    }
    &.wevpn-list {
      background: #FFFFFF;
      border-radius: 4px;
      li {
        color: #293343;
      }
    }
    &.free-vpn-list {
      li {
        color: #5B5863;
        opacity: 0.7;
      }
    }
    li {
      display: flex;
      align-items: flex-start;
      font-weight: 500;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      img {
        margin-right: 20px;
        @media #{$maxTab} {
          margin-right: 10px;
          height: 24px;
        }
      }
    }
  }
}

.wevpn-get-started {
  .step {
    padding-bottom: 45px;
    @media #{$mobile} {
      margin-bottom: 30px;
    }
    .step-nu {
      border-radius: 10px 10px 0px 0px;
      padding: 10px;
    }
    .image {
      img {
        max-height: 260px;
        @media #{$betweentabdesk} {
          max-height: 225px;
        }
        @media #{$tablet} {
          max-height: 150px;
        }
      }
    }
    > h3 {
      max-width: 260px;
      margin: auto;
      min-height: 66px;
      display: flex;
      align-items: center;
      justify-content: center;
      @media #{$mobile} {
        min-height: auto;
      }
    }
    p {
      padding: 0 12px;
    }
  }
}

.risk-free-wevpn-features {
  .item {
    display: flex;
    align-items: center;
    padding: 40px;
    margin-bottom: 30px;
    @media #{$maxTab} {
      padding: 30px 20px;
      flex-direction: column;
    }
    @media #{$tablet} {
      .icon {
        min-height: 115px;
        display: flex;
        align-items: center;
      }
    }
  }
}
