header.tutorial-page .navbar .login .login-item .login-link {
  @media (min-width: 992px) {
    color: #fff;
  }
}

.setup-hero {
  position: relative;
  padding-top: 100px;

  .setup-content {
    position: relative;
    z-index: 2;
    padding: 45px 0;
    @media #{$tablet} {
      padding-top: 0;
    }
    @media #{$mobile} {
      padding-top: 0;
      text-align: center;
    }
    .setup-text {
      h1 {
        margin-bottom: 30px;
        @media #{$betweentabdesk} {
          margin-bottom: 15px;
        }
        @media #{$tablet} {
          margin-bottom: 10px;
        }
        @media #{$mobile} {
          margin-bottom: 15px;
        }
      }
      p {
        @media #{$betweentabdesk} {
          width: 72%;
        }

        @media #{$mobile} {
          width: 65%;
          margin: 0 auto 25px;
        }
      }
    }
  }
}

.apps-setup {
  background: #f5f4f0;
  padding: 60px 0;
  @media #{$tablet} {
    padding-top: 70px;
    padding-bottom: 40px;
  }
  @media #{$maxTab} {
    padding-top: 40px;
    padding-bottom: 38px;
  }

  .app-item {
    background: #ffffff;
    border: 1px solid #e0ded5;
    border-radius: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    transition: 0.3s;
    justify-content: space-between;
    height: 84px;
    line-height: 84px;
    padding: 0 18px 0 32px;
    @media #{$tablet} {
      min-height: 80px;
      margin-bottom: 20px;
    }
    @media #{$mobile} {
      margin-bottom: 12px;
    }

    &:hover {
      box-shadow: 0px 20px 20px rgba(232, 230, 225, 0.9), 0px 4px 10px #e6e4df;
      color: #5b5863 !important;
      svg.icon {
        path {
          fill: #5b5863;
          &.icon-inside {
            fill: #fff;
          }
        }
      }
      .link {
        background-color: $primaryColor;
        svg {
          path {
            fill: #fff;
          }
        }
      }
    }
    .text {
      display: flex;
      align-items: center;

      svg {
        margin-right: 16px;
        &.apple {
          position: relative;
          top: -4px;
        }
        path {
          transition: all 0.3s ease-in-out;
        }
      }
      .title {
        font-size: 18px;
        line-height: 20px;
        font-weight: bold;
      }
    }
    span.link {
      display: flex;
      min-height: 48px;
      width: 48px;
      svg {
        margin: auto;
      }
    }
  }
}
