.openvpn-protocol,
.wireguard-protocol,
.ikev2-protocol,
.shadowsocks-protocol {
  padding: 65px 0;
  .text {
    h2 {
      position: relative;
      &::before {
        position: absolute;
        content: "";
        width: 6px;
        height: 40px;
        background-color: $primaryColor;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        @media #{$mobile} {
          width: 4px;
          height: 26px;
        }
      }
    }
  }
}

.protocols-hero-img {
  .txt {
    @media #{$mobile} {
      font-size: 16px;
    }
  }
}

.openvpn-udp-tcp,
.openvpn-str-weak {
  padding: 60px 0;
  @media #{$mobile} {
    font-size: 16px;
    .point {
      .icon {
        img {
          width: 38px;
        }
      }
    }
  }
}
