.jobs-page {
  .p-lg {
    @media #{$betweentabdesk} {
      font-size: 16px;
    }
  }
  .text {
    ul {
      li {
        svg {
          margin: unset;
        }
      }
    }
  }
}
.jobs-hero {
  @media #{$betweentabdesk} {
    .hero-img-content {
      h1 {
        font-size: 38px;
      }
      h4 {
        font-size: 16px;
      }
      p {
        font-size: 15px;
      }
    }
  }
}

.modal-jobs-body {
  position: relative;
  .padd-right {
    @media (min-width: 1024px) {
      padding-right: 5px;
    }
  }
  .padd-left {
    @media (min-width: 1024px) {
      padding-left: 5px;
    }
  }
  input,
  select,
  textarea {
    border: 0;
    outline: none;
    box-shadow: none;
    padding-left: 0;
    padding: 0;
    height: auto;
    font-weight: 500;
    width: 100%;
    @media #{$mobile} {
      font-size: 14px;
    }

    &::placeholder {
      font-size: 16px;
      line-height: 20px;
      font-weight: normal;
      opacity: 0.4;
      @media #{$mobile} {
        font-size: 14px;
      }
    }
  }

  .form-group {
    border: 1px solid #e0ded5;
    padding: 15px 20px;
    margin-bottom: 20px;
    transition: all 0.3s ease-in-out;
    border-radius: 5px;
    overflow: hidden;

    &:hover {
      border: 1px solid #c7c4bd;
    }

    @media #{$mobile} {
      margin-bottom: 10px;
      padding: 15px 20px;
    }

    label {
      font-size: 12px;
      font-family: "Averta";
      margin-bottom: 4px;
      font-weight: 500;
      color: #8D8B92;
      display: block;
      @media #{$mobile} {
        margin-bottom: 0;
        line-height: 1.5;
      }
    }
  }

  .form-group.file {
    height: 60px;
    padding: 0 !important;
  }

  .custom--file {
    position: relative;
    height: 100%;

    img {
      position: absolute;
      top: 16px;
      left: 15px;
      z-index: 1;
      cursor: pointer;
      @media #{$mobile} {
        left: 10px;
      }
    }

    span {
      position: absolute;
      top: 20px;
      left: 45px;
      font-size: 14px;
      color: #8d8b92;
      z-index: 1;
      cursor: pointer;
      @media #{$mobile} {
        left: 40px;
        font-size: 12px;
        top: 20px;
      }
    }

    input[type="file"] {
      background-color: transparent;
      z-index: 2;
      position: relative;
      cursor: pointer;
      height: 100%;
    }

    button {
      position: absolute;
      right: 8px;
      top: 8px;
      z-index: 1;
      background: none;
      font-size: 16px;
      text-align: center;
      color: $primaryColor;
      border: 1px solid $primaryColor;
      border-radius: 5px;
      padding: 7px 26px;
      font-weight: 500;

      @media #{$mobile} {
        font-size: 14px;
        top: 12px;
        padding: 7px 9px;
      }
    }
  }
  .submit {
    min-width: 230px;
  }
  .modal-jobs-header {
    
    .close {
      min-width: 40px;
      font-size: 36px;
      color: $primaryColor;
      position: absolute;
      right: -5px;
      top: -30px;
    }
  }
  textarea#msg {
    min-height: 100px;
  }
}