.best-ways-unblock-websites {
  .way {
    margin-bottom: 30px;
    padding: 35px;
    @media #{$maxTab} {
      flex-direction: column;
      text-align: center;
      padding: 35px 25px;
    }
    .icon {
      min-width: 96px;
      @media #{$maxTab} {
        margin-bottom: 30px;
      }
      @media #{$tablet} {
        min-height: 150px;
        display: flex;
      }
      @media (min-width: 1024px) {
        margin-right: 30px;
      }
    }
    .text {
      p {
        opacity: 0.7;
        @media #{$maxTab} {
          font-size: 14px;
        }
      }
    }
  }
}