body.windows-new {
  background-color: #fafafa;
  @media #{$mobile} {
    background-color: #f9f8f4;
  }
}

.windows-header {
  .navbar {
    .login {
      .login-item {
        .login-link {
          color: #fff;
          transition: all 0.3s ease-in-out;
          &:hover {
            opacity: 0.8;
          }

          @media #{$maxTab} {
            color: #5b5863;
            font-size: 18px;
          }

          &:hover {
            color: #fff;
          }
        }

        .get-started {
          background-color: #fff;
          color: $primaryColor;
          transition: all 0.3s ease-in-out;

          @media #{$maxTab} {
            background-color: $primaryColor;
            color: #fff;
            font-size: 16px;
          }

          &:hover {
            background: #fff;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
          }
        }
      }
    }
  }
}

.hero-img.windows {
  position: relative;
  padding: 90px 0 235px;
  @media #{$tablet} {
    text-align: center;
    padding-bottom: 60px;
  }
  @media #{$mobile} {
    text-align: left;
    padding-bottom: 265px;
  }

  .hero-img-content {
    position: relative;
    z-index: 2;
    .hero-img-title {
      font-size: 48px;
      line-height: 48px;
      width: 85%;

      @media #{$tablet} {
        font-size: 36px;
        line-height: 44px;
        width: 100%;
        margin: 60px 0 12px;
      }
      @media #{$mobile} {
        font-size: 32px;
        line-height: 40px;
        width: 90%;
        text-align: center;
        margin-bottom: 35px;
      }

      @media #{$betweentabdesk} {
        width: 90%;
        font-size: 42px;
      }
    }

    .hero-img-txt {
      font-size: 24px;
      line-height: 30px;
      margin: 32px 0 34px;

      @media #{$maxTab} {
        font-size: 20px;
        text-align: center;
        line-height: 28px;
        margin: 0 0 30px;
      }

      @media #{$betweentabdesk} {
        margin: 20px 0;
      }
    }

    .hero-img-list {
      @media #{$tablet} {
        width: 60%;
        margin: auto;
      }

      li {
        font-size: 18px;
        line-height: 25px;
        position: relative;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        text-align: left;

        @media #{$mobile} {
          font-size: 16px;
          line-height: 20px;

          &:last-child {
            margin-bottom: 0;
          }
        }
        svg {
          margin-right: 15px;
          min-width: 36px;
          @media #{$mobile} {
            width: 25px;
            height: 25px;
            min-width: 25px;
            margin-right: 10px;
          }
        }
      }
    }

    .hero-img-btn {
      @media #{$betweentabdesk} {
        margin-top: 30px;
      }
    }

    .download-btn {
      padding: 21px;
      display: inline-block;
      width: 263px;
      margin: auto;
      text-align: center;

      svg {
        position: relative;
        top: -3px;
        left: -10px;

        path {
          transition: fill 0.3s ease-in-out;
        }
      }

      a {
        color: $primaryColor;
        background-color: transparent;
        margin-left: -4px;
        text-decoration: underline !important;
      }

      @media #{$mobile} {
        width: 100%;
        display: block;
        margin-left: 0;
        padding: 0;
      }

      @media #{$tablet} {
        padding: 0;
        width: auto;
        margin-left: 30px;
      }

      @media #{$betweentabdesk} {
        padding: 0;
      }

      &:hover {
        a {
          color: #e33866;
          text-decoration: none !important;
        }

        svg {
          path {
            fill: $linkHoverColor;
          }
        }
      }

      i {
        margin-right: 7px;
        position: static;
      }
    }
  }

  .hero-img-image {
    position: absolute;
    right: -90px;
    top: 100px;
    left: -130px;
    z-index: 2;
    @media #{$maxTab} {
      top: 0;
      position: relative;
      left: 0;
      right: 0;
    }
    @media #{$tablet} {
      top: 20px;
    }
    @media #{$betweentabdesk} {
      right: -180px;
      left: -70px;
      top: 64px;
    }
    img {
      max-width: 100%;
    }
  }
  .header-shape {
    z-index: 0;
  }
  @media #{$betweentabdesk} {
    .header-shape {
      top: -490px;
    }
  }
  @media #{$tablet} {
    .header-shape {
      top: -655px;
      right: -600px;
    }
  }
  @media #{$mobile} {
    .header-shape {
      top: -710px;
    }
  }
}

/* Start Platform */
.platforms {
  overflow: hidden;
  margin-top: -123px;
  @media #{$tablet} {
    padding-top: 60px;
    background-color: #f9f8f4;
    margin-top: 0;
  }
  @media #{$mobile} {
    margin-top: -217px;
  }
  .platform-content {
    p {
      text-align: center;
      line-height: 20px;
      color: #5b5863;
      font-weight: 500;
      @media #{$mobile} {
        font-size: 14px;
      }
    }

    .platform-list {
      align-items: center;
      border-radius: 10px;

      @media #{$mobile} {
        display: block;
        overflow: hidden;
      }

      .item {
        font-size: 16px;
        position: relative;
        line-height: 20px;
        font-weight: 500;
        color: rgba(#5b5863, 0.7);
        text-align: center;
        transition: 0.3s all ease-in-out;
        height: 136px;
        background-color: #fafafa;
        box-shadow: 10px 15px 24px #e6e4df, 15px 5px 21px rgba(240, 238, 233, 0.8);
        display: flex;
        justify-content: center;
        align-items: stretch;
        @media #{$maxTab} {
          box-shadow: none;
          border-right: 1px solid #ebe8df;
          border-bottom: 1px solid #ebe8df;
          height: 105px;
        }
        @media #{$mobile} {
          height: 50px;
          &.soon {
            border-radius: 0 0 10px 10px;
          }
        }

        &.active,
        &:hover {
          background: #f7f6f0;
          a {
            position: relative;
            height: 100%;

            span {
              opacity: 1;
              color: $primaryColor;
            }

            svg {
              path {
                fill: $primaryColor;
                &.amazon-letter {
                  fill: #fff;
                }
              }
            }

            .firefox {
              .line {
                stroke: $primaryColor;
                fill: none;
              }

              .fox {
                fill: $primaryColor;
              }
            }
          }
        }

        a {
          transition: all 0.3s ease-in-out;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          flex-basis: 100%;
          padding: 0 10px;
          @media #{$maxTab} {
            text-align: left;
            flex-direction: row;
            justify-content: flex-start;
          }
          @media #{$tablet} {
            justify-content: center;
            padding: 20px 11px;
            width: 100%;
            svg {
              width: 32px;
              margin-right: 10px;
            }
          }

          @media #{$mobile} {
            padding: 0;
            padding-left: 10px;
          }

          &.apple-icon {
            @media #{$tablet} {
              svg {
                position: relative;
                top: -4px;
              }
            }
          }
        }
        svg {
          @media #{$mobile} {
            width: 16px;
            height: 16px;
            margin-right: 8px;
          }

          path {
            transition: all 0.3s ease-in-out;
          }

          & + span {
            display: block;
            font-size: 14px;
            line-height: 15px;
            opacity: 0.7;
            color: #5b5863;
            font-weight: normal;
            margin-top: 15px;
            transition: all 0.3s ease-in-out;
            @media #{$betweentabdesk} {
              font-size: 14px;
            }
            @media #{$tablet} {
              margin-top: 0;
              font-size: 14px;
            }
            @media #{$mobile} {
              margin-top: 0;
              display: inline-block;
            }
          }
        }
      }
      .item.soon {
        position: relative;
        cursor: pointer;

        &:before,
        &:after {
          position: absolute;
          visibility: hidden;
          opacity: 0;
          pointer-events: none;
          transition: transform 0.1s cubic-bezier(0.5, 1, 0.25, 1);
          z-index: 1;
        }

        &:before {
          padding: 5px;
          width: 110px;
          border-radius: 3px;
          background-color: $primaryColor;
          border-radius: 5px;
          box-shadow: 0px 6px 11px rgba(79, 79, 79, 0.3);
          border-radius: 5px;
          color: #fff;
          content: attr(data-tooltip);
          text-align: center;
          font-size: 12px;
          font-weight: 500;
        }

        &:after {
          border: 8px solid transparent;
          width: 0;
          content: "";
          font-size: 0;
          line-height: 0;
        }

        &:hover:before,
        &:hover:after {
          visibility: visible;
          opacity: 1;
        }
        &.t-top {
          &:before {
            bottom: 82%;
            left: 50%;
            margin-bottom: 5px;
            transform: translateX(-50%);
          }

          &:after {
            bottom: 82%;
            left: 50%;
            transform: translateX(-50%);
            border-top: 8px solid $primaryColor;
            border-bottom: none;
          }

          &:hover:before,
          &:hover:after {
            transform: translateX(-50%) translateY(-5px);
          }
        }
      }
    }

    .owl-carousel.platform-list {
      &.owl-drag .owl-item {
        @media (min-width: 1000px) {
          opacity: 0;
          transition: opacity 200ms;
        }
      }
      .owl-stage {
        @media #{$maxTab} {
          border: 1px solid #ebe8df;
          border-radius: 10px;
          overflow: hidden;
        }
      }
      .owl-stage-outer {
        padding: 36px 0;
        overflow: visible;
      }

      @media #{$maxTab} {
        .owl-item.cloned {
          display: none !important;
        }
        .owl-stage {
          width: auto !important;
        }
        .owl-item {
          width: 33.33% !important;
        }
      }

      @media #{$mobile} {
        .owl-item {
          width: 50% !important;
          overflow: visible;
        }
      }

      .owl-item {
        &.active {
          border-right: 1px solid #ebe8df;
          opacity: 1;
          @media #{$maxTab} {
            border-right: 0;
          }
        }
        &:first-child {
          .item {
            border-radius: 10px 0 0 10px;
            @media #{$maxTab} {
              border-radius: 0;
            }
          }
        }
        @media #{$mobile} {
          &:nth-of-type(even) {
            .item {
              border-right: 0;
            }
          }
          &:nth-of-type(15) {
            .item {
              border-bottom: 0;
              border-right: 0;
            }
            @media #{$mobile} {
              width: 100% !important;
              a {
                justify-content: center;
              }
            }
          }
        }
        @media #{$tablet} {
          &:nth-of-type(3),
          &:nth-of-type(6),
          &:nth-of-type(9),
          &:nth-of-type(12),
          &:nth-of-type(15) {
            .item {
              border-right: 0;
            }
          }
          &:nth-of-type(13),
          &:nth-of-type(14),
          &:nth-of-type(15){
            .item {
              border-bottom: 0;
            }
          }
        }
      }

      .owl-item.active.last {
        border-right: 0;
        .item {
          border-radius: 0 10px 10px 0;
          @media #{$maxTab} {
            border-radius: 0;
          }
        }
      }

      .owl-item:not(.active) + .owl-item.active {
        .item {
          border-radius: 10px 0 0 10px;
          @media #{$maxTab} {
            border-radius: 0;
          }
        }
      }
      .owl-nav {
        position: relative;
        margin-top: 0;
        .arrows {
          width: 14px;
          height: 14px;
          position: relative;
          margin: auto;
        }
        .prev,
        .next {
          border-bottom: 1.5px solid;
          border-left: 1.5px solid;
          transform: rotate(45deg);
          left: 4px;
        }
        .next {
          transform: rotate(-135deg);
          left: -4px;
        }
        .owl-prev,
        .owl-next {
          top: -140px;
          color: #000;
          position: absolute;
          background: #f0eee9;
          border-radius: 130px;
          width: 72px;
          height: 72px;
          margin: 0;
          &.disabled {
            cursor: not-allowed;
          }
          @media (min-width: 1000px) and (max-width: 1366px) {
            width: 64px;
            height: 64px;
          }
        }

        .owl-prev {
          left: -87px;
          @media (min-width: 1000px) and (max-width: 1366px) {
            left: -32px;
            border-radius: 50%;
          }
        }
        .owl-next {
          right: -87px;
          @media (min-width: 1000px) and (max-width: 1366px) {
            right: -32px;
            border-radius: 50%;
          }
        }
      }
    }
  }
}

/* End Platform */

/* Start wevpn-setup */
.wevpn-setup {
  .wevpn-setup-content {
    padding: 60px 0 100px;
    @media #{$maxTab} {
      padding: 30px 0 60px;
    }
    .wevpn-setup-img {
      @media #{$mobile} {
        padding: 10px 0 40px;
        text-align: center;
      }
    }
    .wevpn-setup-info {
      .wevpn-setup-title {
        margin-bottom: 20px;
        @media #{$tablet} {
          font-size: 32px;
        }
        @media #{$mobile} {
          text-align: center;
        }
      }
      .wevpn-setup-list {
        padding-bottom: 15px;
        li {
          font-size: 18px;
          line-height: 24px;
          position: relative;
          display: flex;
          align-items: center;
          margin-bottom: 12px;
          svg {
            margin-right: 12px;
            min-width: 37px;
            margin-left: unset;
            @media #{$maxTab} {
              margin-right: 10px;
              width: 25px;
              height: 25px;
              min-width: 25px;
            }
          }
          @media #{$maxTab} {
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 10px;
            &:last-child {
              margin-bottom: 0;
            }
          }
          a {
            font-weight: normal;
          }
        }
      }
      p {
        border-top: 1px solid #ebe8df;
        font-size: 14px;
        line-height: 24px;
        padding-top: 15px;
        color: #8d8b92;
        margin-left: 45px;
        width: 80%;
        @media (min-width: 1024px) {
          font-size: 16px;
        }
        @media #{$maxTab} {
          margin-left: 0;
          width: 100%;
        }
        a {
          font-weight: normal;
        }
      }
    }
  }
}

/* End wevpn-setup */
.how-to-use {
  background-color: #fff;
  padding: 100px 0;
  text-align: center;

  @media #{$maxTab} {
    .container {
      max-width: 100%;
      width: 100%;
      margin: 0;
      padding: 0;
    }
  }
  @media #{$tablet} {
    padding: 80px 0;
  }
  @media #{$mobile} {
    padding: 60px 0;
  }
  .how-to-use-content {
    .sm-scrolled {
      position: relative;
      &:before {
        content: "";
        height: 1px;
        background-color: #fd3e72;
        width: 66%;
        position: absolute;
        top: 85px;
        left: 50%;
        transform: translateX(-50%);
        @media #{$maxTab} {
          display: none;
        }
      }
    }
    @media #{$maxTab} {
      overflow: auto;
      /* Hide scrollbar for IE and Edge */
      -ms-overflow-style: none;
      /* Hide scrollbar for Chrome, Safari and Opera */
      &::-webkit-scrollbar {
        display: none;
      }
      .sm-scrolled {
        width: auto;
        margin-left: 0;
        margin-right: 0px;
        padding: 0 40px;
        flex-wrap: nowrap;
        overflow: auto;
        .col-4 {
          padding: 0;
          max-width: 100%;
          .step {
            width: 250px;
            margin-right: 30px;
          }
        }
      }
    }
  }
  .container {
    @media #{$tablet} {
      padding: 0 20px;
    }
  }
  .how-to-use-title {
    font-size: 36px;
    line-height: 44px;
    margin: auto;

    @media #{$mobile} {
      font-size: 24px;
      line-height: 28px;
      padding-right: 20px;
      padding-left: 20px;
    }
  }
  .step {
    margin-top: 60px;
    @media #{$mobile} {
      margin-top: 50px;
    }
    @media #{$tablet} {
      margin-top: 60px;
    }
    .step-number {
      color: #ffffff;
      width: 50px;
      height: 50px;
      line-height: 50px;
      background-color: #fd3e72;
      border-radius: 50%;
      margin: 0 auto 10px;
    }
    .step-txt {
      font-size: 18px;
      line-height: 20px;
      margin-bottom: 35px;
      @media #{$mobile} {
        font-size: 16px;
        margin-bottom: 15px;
      }
    }
  }
  .step .step-img {
    width: 100%;
    max-width: 300px;
    margin: auto;
    &:after {
      display: none;
    }
  }
}

