        svg {
          transform: scale(0.8);
        }
      }
    }
  }
  .devices-img {
    max-width: 80%;
    margin: auto;
    position: relative;
    top: 1px;
    z-index: 1;
    @media #{$maxTab} {
      max-width: 90%;
    }
  }
  .devices-shape1,
  .devices-shape2 {
    position: absolute;
    z-index: 0;
    @media #{$maxTab} {
      display: none;
    }
  }
  .devices-shape1 {
    bottom: -300px;
    right: -535px;
  }
  .devices-shape2 {
    left: -450px;
    top: -340px;
  }
}

/* Start FAQ */
.faq {
  background-color: #fafafa;
  padding: 120px 0 80px;
  @media #{$betweentabdesk} {
    padding: 100px 0 70px;
    padding-top: 100px;
  }
  @media #{$maxTab} {
    padding: 60px 0 50px;
  }
  .faq-title {
    margin-bottom: 70px;
    @media #{$tablet} {
      margin-bottom: 50px;
    }
    @media #{$mobile} {
      margin-bottom: 30px;
    }
  }
  .faq-image {
    @media #{$maxTab} {
      margin-bottom: 50px;
      img {
        max-width: 80%;
      }
    }
  }
  .accordion {
    .question {
      border-bottom: 1px solid #dfddd6;
      &:last-child {
        border: 0;
      }
      .question-header {
        h2 {
          position: relative;
          padding: 20px 35px;
          font-size: 20px;
          line-height: 30px;
          color: #5b5863;
          cursor: pointer;
          font-weight: 500;
          transition: all 0.3s ease-in-out;
          span {
            color: $primaryColor;
          }
          @media #{$mobile} {
            font-size: 16px;
            line-height: 24px;
            padding: 20px 35px;
          }
          @media #{$tablet} {
            font-size: 18px;
            line-height: 24px;
            padding: 25px 35px;
          }
          &.collapsed {
            @media (min-width: 1199px) {
              &:hover {
                span {
                  color: $primaryColor;
                }
                img {
                  filter: none;
                }
                &:after,
                &:before {
                  background: $primaryColor;
                }
              }
            }
            span {
              transition: 0.3s;
              color: #5b5863;
            }
            img {
              filter: grayscale(100%);
            }
          }
          &:before,
          &:after {
            background: $primaryColor;
            content: "";
            height: 2px;
            right: 0;
            position: absolute;
            top: 50%;
            width: 16px;
            border-radius: 40px;
            transition: transform 500ms ease;
            @media #{$mobile} {
              width: 12px;
            }
          }
          &:after {
            transform-origin: center;
          }
          &.collapsed {
            &:after,
            &:before {
              background: #878489;
            }
            &:after {
              transform: rotate(90deg);
            }
            &:before {
              transform: rotate(180deg);
            }
          }
          img {
            filter: none;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            transition: all 0.3s ease-in-out;
          }
        }
      }
      .question-body {
        padding-bottom: 42px;
        padding-left: 33px;
        font-size: 16px;
        line-height: 26px;
        width: 95%;
        @media #{$tablet} {
          width: 90%;
        }
        @media #{$betweentabdesk} {
          width: 88%;
        }
        @media #{$mobile} {
          font-size: 14px;
          line-height: 24px;
        }
      }
    }
  }
}

