.main_title {
  font-size: 38px;
  line-height: 44px;
  @media (min-width: 768px) and (max-width: 1199px) {
    font-size: 33px;
    line-height: 38px;
  }
  @media #{$mobile} {
    font-size: 24px;
    line-height: 36px;
  }
}

.hero-img.vpn_mac {
  color: #ffffff;
  background: linear-gradient(121.6deg, #fe6556 -42.53%, #fd3e72 53.1%, #49a8ff 175.35%);
  overflow: hidden;
  padding: 120px 0 145px;
  height: auto;
  z-index: 1;
  &.gaming {
    padding: 120px 0 205px;
    @media #{$betweentabdesk} {
      padding: 120px 0 135px;
    }
    @media #{$tablet} {
      padding: 110px 0 50px;
    }
    @media #{$mobile} {
      padding: 110px 0 220px;
    }
  }
  @media #{$tablet} {
    padding: 110px 0 50px;
  }
  @media #{$mobile} {
    padding: 110px 0 220px;
  }
  .trustpilot-img {
    @media #{$mobile} {
      height: 14px;
    }
  }
  &:after {
    display: none;
  }

  .hero_img_title {
    margin: 20px 0;
    @media #{$mobile} {
      margin: 20px 0 10px;
    }
  }
  .hero_img_txt {
    font-size: 20px;
    line-height: 140%;
    max-width: 460px;
    @media #{$tablet} {
      font-size: 16px;
      max-width: none;
    }
    @media #{$mobile} {
      font-size: 14px;
      max-width: none;
    }
  }
  .mac_hero_list {
    margin: 35px 0 45px;
    @media #{$tablet} {
      margin: 35px auto 45px;
      text-align: center;
      max-width: 590px;
    }
    li {
      display: flex;
      align-items: flex-start;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      &:last-child {
        max-width: 450px;
        @media #{$maxTab} {
          max-width: none;
        }
      }
      img {
        margin-right: 12px;
      }
      p {
        font-size: 16px;
        line-height: 140%;
        @media #{$mobile} {
          text-align: left;
          font-size: 14px;
        }
      }
    }
  }
  .hero_img_btn {
    text-align: center;
    display: inline-block;
    @media #{$maxTab} {
      display: block;
      a {
        width: 100%;
        padding: 23px 0px;
        font-size: 18px;
      }
    }
    p {
      margin-top: 8px;
    }
  }
  .mac_hero_img {
    position: absolute;
    right: -140px;
    top: 90px;
    left: 50px;
    &.vpn-windows {
      top: 50px;
    }
    &.gaming {
      position: absolute;
      right: -100px;
      top: -35px;
      left: 120px;
      @media #{$betweentabdesk} {
        top: 0;
      }
      @media #{$maxTab} {
        position: static;
        max-width: 90%;
        margin: 40px auto;
        &:after {
          background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0.6) 0.01%, rgba(255, 255, 255, 0.0046875) 99.99%, rgba(255, 255, 255, 0) 100%);
          opacity: 0.7;
        }
      }
    }
    @media #{$maxTab} {
      position: static;
    }
    @media #{$tablet} {
      margin: 50px 0;
      text-align: center;
      img {
        max-width: 75%;
      }
    }
    @media #{$mobile} {
      margin: 70px 0 40px;
      img {
        max-width: 85%;
      }
    }
    &:after {
      content: "";
      position: absolute;
      top: -100px;
      right: 0;
      left: 40px;
      bottom: 0;
      width: 630px;
      height: 630px;
      background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0.5) 0%, #ffffff 0.01%, rgba(255, 255, 255, 0.0046875) 99.99%, rgba(255, 255, 255, 0.1) 100%);
      border-radius: 50%;
      mix-blend-mode: multiply;
      @media #{$tablet} {
        top: 50%;
        left: 50%;
        width: 400px;
        height: 400px;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      @media #{$mobile} {
        width: 270px;
        height: 270px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

.homepage-testimonials.vpn_mac {
  background-color: transparent;
  position: relative;
  z-index: 2;
  .testimonial-content {
    background: #ffffff;
    box-shadow: 0px 4px 26px rgba(0, 0, 0, 0.08);
    border-radius: 20px;
    padding: 12px 22px;
    margin-top: -80px;
    @media #{$tablet} {
      margin-top: -65px;
    }
    @media #{$mobile} {
      margin-top: -200px;
      padding: 0;
    }
  }
}
.mac_setup {
  .mac_setup_content {
    padding: 60px 0 100px;
    &.gaming {
      padding-bottom: 0;
      @media #{$mobile} {
        padding-bottom: 60px;
      }
      @media #{$tablet} {
        .title {
          margin-bottom: 20px;
          font-size: 23px;
          line-height: 32px;
        }
      }
    }
    @media #{$mobile} {
      padding: 30px 0 60px;
    }
    @media #{$tablet} {
      padding: 60px 0 80px;
    }
  }
  .mac_setup_img {
    @media #{$mobile} {
      padding: 10px 0 40px;
      text-align: center;
    }
    @media (max-width: 1199px) {
      img {
        max-width: 85%;
      }
    }
  }
  .mac_setup_info {
    .title {
      margin-bottom: 35px;
      @media #{$betweentabdesk} {
        font-size: 31px;
      }
      @media #{$mobile} {
        text-align: center;
        padding: 0 15px;
      }
    }
    .list {
      li {
        display: flex;
        align-items: flex-start;
        margin-bottom: 20px;
        @media #{$maxTab} {
          margin-bottom: 15px;
          align-items: center;
        }
        span.num {
          border: 2px solid #44c19d;
          width: 25px;
          height: 25px;
          min-width: 25px;
          border-radius: 50%;
          font-weight: bold;
          font-size: 16px;
          color: #62d5b4;
          margin-right: 15px;
          display: inline-block;
          text-align: center;
          line-height: 22px;
          @media #{$maxTab} {
            margin-right: 8px;
            font-size: 14px;
          }
        }
        p {
          font-size: 16px;
          @media #{$maxTab} {
            font-size: 14px;
          }
        }
      }
    }
    > p {
      border-top: 1px solid #ebe8df;
      font-size: 16px;
      line-height: 24px;
      padding-top: 15px;
      color: #8d8b92;
      margin-left: 45px;
      max-width: 400px;
      @media #{$mobile} {
        margin-left: 0;
        font-size: 14px;
      }

      @media #{$tablet} {
        font-size: 14px;
        margin-left: 20px;
      }

      a {
        font-weight: normal;
      }
    }
  }
}
.how-to-use.vpn_mac {
  padding: 70px 0 85px;
  @media #{$maxTab} {
    padding-bottom: 20px;
  }
  
  .step {
    @media #{$maxTab} {
      margin-bottom: 50px;
    }
    .step-img {
      img {
        filter: drop-shadow(0px 7px 26px rgba(0, 0, 0, 0.08));
      }
    }
    
  }
}

// Start mac_devices
.mac_devices {
  padding: 85px 0;
  @media #{$mobile} {
    padding: 60px 0;
  }
  .image {
    @media #{$maxTab} {
      text-align: center;
      margin-bottom: 40px;
    }
    img {
      @media (min-width: 1200px) {
        max-width: 535px;
      }
      @media #{$maxTab} {
        max-width: 85%;
      }
    }
  }
  .mac_devices_content {
    max-width: 533px;
    margin-left: auto;
    @media #{$maxTab} {
      max-width: none;
    }
    .main_title {
      @media #{$mobile} {
        font-size: 24px;
      }
      span {
        color: $primaryColor;
      }
    }
    .main_text {
      font-size: 24px;
      line-height: 30px;
      opacity: 0.8;
      margin: 22px 0 45px;
      @media #{$mobile} {
        font-size: 18px;
        line-height: 22px;
        margin: 12px 0 30px;
      }
    }
    .mac_info {
      margin-bottom: 30px;
      h4 {
        font-size: 20px;
        line-height: 25px;
        margin-bottom: 9px;
        @media #{$mobile} {
          font-size: 16px;
          line-height: 22px;
        }
      }
      p {
        font-size: 16px;
        line-height: 20px;
        @media #{$mobile} {
          font-size: 14px;
        }
      }
    }
    .more_info {
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 40px;
      @media #{$mobile} {
        font-size: 14px;
      }
    }
    .mac_devices_btn {
      a {
        max-width: 300px;
        line-height: 64px;
        width: 100%;
        @media #{$mobile} {
          max-width: none;
          font-size: 18px;
        }
      }
    }
  }
}

// Start mac_faq
.mac_faq {
  background-color: $primaryColor;
  color: #fff;
  padding: 70px 0;
  @media #{$mobile} {
    padding: 60px 0;
  }
  .main_title {
    margin-bottom: 50px;
    @media #{$mobile} {
      font-size: 24px;
      line-height: 32px;
      padding: 0 20px;
    }
    @media (max-width: 1199px) {
      margin-bottom: 30px;
    }
  }
  .accordion {
    padding: 0 60px;

    @media #{$tablet} {
      padding: 0 30px;
    }
    @media #{$mobile} {
      padding: 0;
    }

    .question {
      border-bottom: 1px solid #ee3063;

      &:last-child {
        border: 0;
      }

      .question-header {
        h2 {
          display: flex;
          justify-content: space-between;
          padding: 16px;
          font-weight: 500;
          font-size: 20px;
          line-height: 140%;
          cursor: pointer;
          transition: all 0.3s ease-in-out;
          opacity: 1;

          @media #{$mobile} {
            font-size: 14px;
            line-height: 24px;
            padding-left: 0;
          }

          @media #{$tablet} {
            font-size: 18px;
            line-height: 24px;
          }
          .title {
            display: flex;
            align-items: center;
            img {
              margin-right: 15px;
            }
            @media #{$mobile} {
              span {
                padding-right: 20px;
              }
            }
          }
          .arrow_down {
            img {
              transform: rotate(180deg);
              transition: all 0.5s ease-in-out;
            }
          }

          &.collapsed {
            .arrow_down {
              img {
                transform: rotate(0);
              }
            }
            opacity: 0.8;
            &:hover {
              opacity: 1;
            }
          }
        }
      }

      .question-body {
        padding-bottom: 16px;
        padding-left: 57px;
        font-size: 16px;
        line-height: 26px;
        width: 90%;
        @media #{$mobile} {
          font-size: 14px;
          line-height: 24px;
          width: 79%;
          padding-left: 0;
          margin: auto;
        }
        a {
          color: #fff;
          text-decoration: underline !important;
        }

        p:first-child {
          margin-bottom: 20px;
        }
        p:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

.features-windows.vpn_mac {
  .main_title {
    margin-bottom: 60px;
    @media #{$tablet} {
      margin-bottom: 30px;
    }
  }
}

.wevpn-features.vpn_mac {
  .support {
    @media #{$mobile} {
      top: 0;
      &.one {
        background: #fafafa;
      }
    }
  }
}

.wevpn-comparison.vpn_mac {
  padding: 140px 0 60px;
  @media #{$maxTab} {
    padding: 60px 0;
  }
}

.wevpn_gaming,
.wevpn_games {
  padding: 60px 0;
  @media #{$mobile} {
    text-align: center;
  }
  .title {
    @media #{$tablet} {
      font-size: 30px;
    }
    @media #{$mobile} {
      font-size: 22px;
      line-height: 25px;
      margin-top: 50px;
    }
  }
  p.txt {
    font-size: 19px;
    line-height: 140%;
    margin-top: 25px;
    opacity: 0.8;
    @media #{$tablet} {
      font-size: 17px;
    }
    @media #{$mobile} {
      font-size: 16px;
      line-height: 140%;
    }
  }
}
