/* Start Footer */
.footer-home {
  background-color: $primaryColor;
  padding-top: 50px;
  @media #{$mobile} {
    padding-top: 35px;
  }
  &.homepage-footer {
    padding-top: 370px;
    @media #{$betweentabdesk} {
      padding-top: 280px;
    }
    @media #{$tablet} {
      padding-top: 270px;
    }
    @media #{$mobile} {
      padding-top: 35px;
    }
  }
  &.footer-pt {
    padding-top: 365px;
    margin-top: -475px;
    @media #{$betweentabdesk} {
      padding-top: 415px;
    }
    @media #{$mobile} {
      padding-top: 455px;
    }
  }

  .footer-content {
    padding-bottom: 45px;
    display: flex;
    flex-wrap: wrap;
    @media #{$mobile} {
      padding-bottom: 0;
    }

    .footer-item {
      flex: 1;
      &.sec-2,
      &.sec-4 {
        flex: 1.2;
      }
      @media (min-width: 768px) and (max-width: 1199px) {
        width: 25%;
        flex: auto !important;
        &.sec-5 {
          margin-top: 50px;
        }
        &.sec-6 {
          margin-top: 50px;
          width: 50%;
        }
      }
      @media (min-width: 768px) and (max-width: 1199px) {
        &.sec-5 {
          flex: 1 !important;
        }
      }
      @media #{$tablet} {
        margin-top: 30px;
      }
      @media #{$mobile} {
        width: 100% !important;
        flex: auto !important;
      }

      &-title {
        font-size: 15px;
        line-height: 19px;
        color: #ffffff;
        font-weight: 500;
        margin-bottom: 20px;
        text-transform: uppercase;
        position: relative;
        z-index: 2;
        
        @media #{$mobile} {
          .arrow {
            position: absolute;
            transform: translate(-6px, 0);
            margin-top: 16px;
            right: 0;
            &:before,
            &:after {
              content: "";
              position: absolute;
              background-color: #fff;
              width: 3px;
              height: 9px;
              transition: all 0.25s ease-in-out;
            }
            &:before {
              transform: translate(2px, 0) rotate(45deg);
            }
            &:after {
              transform: translate(-2px, 0) rotate(-45deg);
            }
          }
        }
        &.title-second {
          margin-top: 18px;
          @media #{$mobile} {
            margin-top: 0;
          }
        }
        @media #{$mobile} {
          margin-bottom: 0;
          padding: 25px 0;
          border-bottom: 1px solid #f3376a;
          cursor: pointer;
          &.active {
            .arrow {
              &::before {
                transform: translate(-2px, 0) rotate(45deg);
              }
              &:after {
                transform: translate(2px, 0) rotate(-45deg);
              }
              
            }
          }
        }
      }
      &-list {
        @media #{$mobile} {
          display: none;
          padding: 10px 0 20px;
        }
        li {
          font-size: 15px;
          line-height: 20px;
          color: #fff;
          display: flex;
          align-items: center;
          &:not(:last-child) {
            margin-bottom: 18px;
            @media #{$tablet} {
              margin-bottom: 15px;
            }
            @media #{$mobile} {
              margin-bottom: 0;
            }
          }

          a {
            color: #fff;
            display: inline-block;
            transition: all 0.2s ease-in-out;
            @media #{$mobile} {
              padding: 12px 0;
            }
            &:hover {
              opacity: 0.8;
            }
          }
        }
        &.social-links {
          li {
            line-height: 20px;
            a {
              display: flex;
              align-items: center;
            }
            .social-icon {
              width: 32px;
              height: 32px;
              border-radius: 50%;
              position: relative;
              background-color: rgba(#fff, 0.15);
              text-align: center;
              display: inline-flex;
              margin-right: 12px;
              @media #{$maxTab} {
                line-height: 32px;
              }
              svg {
                margin: auto;
              }
            }
          }
        }
      }
    }
  }

  .footer-wevpn-apps {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 10px;
    border-bottom: 1px solid #e72358;
    li {
      margin-bottom: 10px;
      width: 16.66%;

      @media (min-width: 768px) and (max-width: 1199px) {
        width: 25%;
        text-align: left;
      }
      @media #{$mobile} {
        width: 33.33%;
        text-align: center;
      }
      .svg-icon {
        width: 40px;
        text-align: center;
        margin-right: 10px;
        @media #{$mobile} {
          margin: 0 auto 5px;
        }
      }
      
      .beta {
        background: linear-gradient(90deg, #ffffff 39.69%, rgba(255, 255, 255, 0.8) 100%);
        border-radius: 9px 9px 9px 0px;
        font-size: 11px;
        padding: 0 7px;
        line-height: 16px;
        display: inline-block;
        text-transform: uppercase;
        color: $primaryColor;
        @media #{$tablet} {
          font-size: 9px;
        }
      }
      a {
        font-size: 16px;
        color: #fff;
        display: flex;
        align-items: center;
        padding: 15px;
        border-radius: 10px;
        transition: all 0.2s ease-in-out;
        @media #{$mobile} {
          font-size: 14px;
          padding: 10px;
          display: block;
        }
        &:hover {
          background-color: #ff376e;
        }
      }
    }
  }

  .apps-in-wevpn {
    padding-bottom: 40px;
    @media #{$mobile} {
      padding-bottom: 0;
    }
  }

  .copyright {
    padding: 25px 0;
    background-color: #ff2863;
    @media #{$tablet} {
      text-align: center;
    }

    .copyright-content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media #{$maxTab} {
        flex-direction: column-reverse;
      }
    }

    .copyright-txt {
      font-size: 14px;
      line-height: 28px;
      color: #ffffff;
      @media #{$maxTab} {
        text-align: center;
        p {
          font-size: 14px;
          line-height: 28px;
          opacity: 0.7;
        }
      }
    }

    .copyright-logos {
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      @media #{$maxTab} {
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 10px;
      }
      li {
        margin: 0 12px;
        @media #{$mobile} {
          margin: 0 12px 10px;
        }
        @media #{$mobile} {
          img {
            transform: scale(1.2);
          }
        }
      }
    }
  }
}

/* End Footer */
