.app-hero-btns {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  @media #{$tablet} {
    justify-content: center;
  }
  @media #{$mobile} {
    flex-direction: column;
    align-items: stretch;
  }
  .hero-img-btn {
    width: 220px;
    border-radius: 5px;
    @media #{$mobile} {
      width: 100%;
    }
    &.app-btn {
      background-color: #111111;
      transition: all 0.3s ease-in-out;
      @media #{$tablet} {
        margin-left: 30px;
      }
      @media #{$mobile} {
        margin-top: 10px;
      }
      &:hover {
        opacity: 0.8;
      }
    }
  }
}