// Start unblock-netflix
.nvidia-setup {
  .row {
    > div {
      display: flex;
    }
  }
  .step {
    position: relative;
    padding: 40px 30px;
    background: #ffffff;
    box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    width: 100%;
    @media #{$maxTab} {
      max-width: 370px;
      margin: 0 auto 30px;
    }
  }
}
// End unblock-netflix