/* Start Privacy Policy */
header.header-pb {
  padding-bottom: 600px;
  background: linear-gradient(106.64deg, #FE6556 -9.57%, #FD3E72 61.05%, #49A8FF 137.8%);
  @media #{$maxTab} {
    padding-bottom: 550px;
  }
}

.tos,
.pp,.law-enforcement {
  margin-top: -460px;
  position: relative;
  @media #{$maxTab} {
    padding-bottom: 40px;
  }

  .pp-content,.tos-content,.law-enforcement-content {
    background: #FFFFFF;
    box-shadow: 0px 4px 41px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 65px 75px 55px;
    @media #{$betweentabdesk} {
      padding: 45px 55px;
    }
    @media #{$tablet} {
      padding: 30px 40px;
    }
    @media #{$mobile} {
      padding: 24px 15px;
    }
    .topic {
      margin-bottom: 40px;
      @media #{$mobile} {
        margin-bottom: 30px;
      }
    }
  }

  .title {
    margin-bottom: 55px;
    @media #{$betweentabdesk} {
      margin-bottom: 35px;
      font-size: 45px;
    }
    @media #{$mobile} {
      margin-bottom: 25px;
      font-size: 28px;
      line-height: 140%;
    }

    @media #{$tablet} {
      margin-bottom: 25px;
      font-size: 40px;
    }
  }
  ul,ol {
    padding-left: 30px;
    margin-bottom: 30px;
    @media #{$mobile} {
      padding-left: 25px;
    }
    li {
      line-height: 160%;
      margin-bottom: 10px;
      font-size: 18px;
      @media #{$tablet} {
        font-size: 16px;
      }
      @media #{$mobile} {
        font-size: 14px;
      }
      
    }
  }
  ul {
    li {
      list-style: none;
      &::before {
        content: "\2022";
        color: #FD3E72;
        font-weight: bold;
        display: inline-block;
        width: 1em;
        margin-left: -1em;
        font-size: 24px;
        position: relative;
        top: 2px;
        @media #{$mobile} {
          width: 0.7em;
        }
      }
    }
  }

  h3 {
    font-size: 24px;
    line-height: 160%;
    margin-bottom: 15px;
    max-width: 700px;
    @media #{$tablet} {
      font-size: 22px;
    }
    @media #{$mobile} {
      font-size: 18px;
      margin-bottom: 10px;
    }
  }

  p {
    font-size: 18px;
    line-height: 160%;
    margin-bottom: 20px;
    strong {
      font-weight: 500;
    }
    @media #{$tablet} {
      font-size: 16px;
    }
    @media #{$mobile} {
      font-size: 14px;
      line-height: 26px;
      margin-bottom: 15px;
    }
  }
}

/* End Privacy Policy */
