.fastest-hero,.school-hero,.travel-hero,.easy-hero {
  background-image: url(../imgs/landing-pages/fastest-vpn-img.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  background-color: #162932;
  @media #{$tablet} {
    .fastest-hero-text {
      font-size: 16px;
    }
    
  }
  @media #{$mobile} {
    background-image: linear-gradient(to top, rgba(9, 32, 46, 0.8), rgba(0, 0, 0, 0.8)), url(../imgs/landing-pages/fastest-vpn-img-m.webp);
  }
}

.school-hero {
  background-image: url(../imgs/landing-pages/school-vpn-img@2x.webp);
  @media #{$mobile} {
    background-image: linear-gradient(to top, rgba(9, 32, 46, 0.8), rgba(0, 0, 0, 0.8)), url(../imgs/landing-pages/school-vpn-img-m.webp);
  }
}

.easy-hero {
  background-image: url(../imgs/landing-pages/easy-vpn-img.webp);
  @media #{$mobile} {
    background-image: linear-gradient(to top, rgba(9, 32, 46, 0.8), rgba(0, 0, 0, 0.8)), url(../imgs/landing-pages/easy-vpn-img-m.webp);
  }
}
.travel-hero {
  background-image: url(../imgs/landing-pages/travel-vpn-img.webp);
  @media #{$mobile} {
    background-image: linear-gradient(to top, rgba(9, 32, 46, 0.8), rgba(0, 0, 0, 0.8)), url(../imgs/landing-pages/travel-vpn-img-m.webp);
  }
  .title {
    font-size: 32px;
  }
}

.why-fastest-vpn {
  p {
    font-size: 22px;
    @media #{$maxTab} {
      font-size: 18px;
    }
  }
}

.wireguard-protocols {
  a {
    max-width: 325px;
    display: flex;
    justify-content: space-between;
    text-align: left;
    padding: 10px 32px !important;
    font-size: 18px;
    @media #{$maxTab} {
      font-size: 16px;
    }
    @media #{$mobile} {
      margin: auto;
    }
    span {
      line-height: normal;
    }
    svg {
      min-width: 12px;
    }
  }
}
