.dynamic-hero {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  .trustpilot_img {
    margin: 40px 0;
  }
  @media #{$tablet} {
    .p-hero {
      font-size: 16px;
    }
    .trustpilot_img {
      margin: 25px 0;
    }
  }
  &.best-vpn-hero {
    padding-top: 100px;
    @media #{$maxTab} {
      padding-top: 60px;
    }
    .hero-img-content {
      @media (min-width: 1200px) {
        padding-top: 80px;
      }
    }
  }
}

.easy-vpn-hero2 {
  background-image: url(../imgs/dynamic-lp/easy.webp);
  @media #{$mobile} {
    background-image: linear-gradient(to right, rgba(9, 32, 46, 0.5), rgba(0, 0, 0, 0.3)), url(../imgs/dynamic-lp/easy-m.png);
  }
}
.fast-vpn-hero2 {
  background-image: url(../imgs/dynamic-lp/fast.png);
  @media #{$mobile} {
    background-image: linear-gradient(to right, rgba(9, 32, 46, 0.5), rgba(0, 0, 0, 0.3)), url(../imgs/dynamic-lp/fast-m.png);
  }
}

.anonymous-vpn-hero2,
.private-vpn-hero2 {
  background-image: url(../imgs/dynamic-lp/anonymous.png);
  @media #{$mobile} {
    background-image: linear-gradient(to right, rgba(9, 32, 46, 0.5), rgba(0, 0, 0, 0.3)), url(../imgs/dynamic-lp/anonymous-m.png);
  }
}

.best-vpn-hero2,
.buy-vpn-hero2,
.cheap-vpn-hero2,
.get-vpn-hero2,
.vpn-download-hero2,
.top-vpn-hero2 {
  background-image: url(../imgs/dynamic-lp/woman-vpn.png);
  @media #{$mobile} {
    background-image: linear-gradient(to right, rgba(9, 32, 46, 0.7), rgba(0, 0, 0, 0.5)), url(../imgs/dynamic-lp/woman-vpn-m.png);
  }
}
