.expat_section {
  padding: 125px 0;
  @media #{$maxTab} {
    padding: 60px 0;
    text-align: center;
  }
  .image {
    @media #{$maxTab} {
      margin-bottom: 50px;
      img {
        max-width: 85%;
      }
    }
  }
  .expat_text {
    font-size: 20px;
    line-height: 140%;
    opacity: 0.8;
    margin: 35px 0 55px;
    @media #{$mobile} {
      font-size: 16px;
      margin: 25px 0 40px;
    }
  }
  .expat_link {
    @media #{$mobile} {
      font-size: 16px;
    }
  }
  .expat_btn {
    a {
      width: 270px;
      height: 77px;
      line-height: 77px;
      @media #{$mobile} {
        width: 100%;
        display: block;
        line-height: 64px;
        height: 64px;
      }
    }
  }
}

.vpn_work {
  padding: 100px 0;
  @media #{$maxTab} {
    padding: 60px 0;
  }
  h4 {
    font-size: 24px;
    line-height: 140%;
    color: #5b5863;
    font-weight: normal;
    margin-bottom: 35px;
    @media #{$mobile} {
      font-size: 18px;
    }
    span {
      color: $primaryColor;
    }
  }
}

.how_to_use.vpn_expat {
  padding: 100px 0;
  @media #{$mobile} {
    padding: 60px 0;
  }
  @media #{$tablet} {
    padding: 60px 0;
    .container {
      max-width: 100%;
      width: 100%;
      margin: 0;
      padding: 0;
    }
  }
  .how_to_use_content {
    @media #{$tablet} {
      overflow: auto;
      .sm-scrolled {
        width: auto;
        margin-left: 0;
        margin-right: 0px;
        padding: 0 40px;
        flex-wrap: nowrap;
        overflow: auto;
        > div {
          padding: 0;
          max-width: 100%;
        }
        .step {
          width: 270px;
          margin-right: 30px;
        }
      }
    }
  }
  .step {
    background: #ffffff;
    box-shadow: 0px 4px 28px rgba(240, 240, 240, 0.7);
    border-radius: 20px;
    padding: 60px 0 50px;
    margin-top: 50px;
    @media #{$mobile} {
      margin-top: 30px;
    }
  }
  .step_img {
    width: 230px;
    height: 230px;
    text-align: center;
    margin: auto;
    display: flex;
    img {
      margin: auto;
      height: 110%;
    }
  }
  .step_num {
    font-size: 24px;
    line-height: 140%;
    margin: 30px 0 15px;
    color: $primaryColor;
  }
  .step_txt {
    font-size: 16px;
    line-height: 140%;
    max-width: 250px;
    margin: auto;
  }
}
