.hero-img.ios-tuts-page.windows-tuts-page {
  @media #{$betweentabdesk} {
    overflow: hidden;
  }
}

.hero-img.ios-tuts-page.windows-tuts-page .hero-img-txt {
  @media #{$betweentabdesk} {
    width: 100%;
  }
}

.hero-image-btns {
  .hero--btn {
    padding: 23px 36px;
    background-color: #fff;
    color: #fd3e72;
  }
}

.ios-tutorials.windows-tuts-page {
  padding-top: 125px;
  @media #{$betweentabdesk} {
    padding-top: 80px;
  }
  @media #{$maxTab} {
    padding-top: 60px;
  }
}

.hero-img.ios-tuts-page.windows-tuts-page .hero-img-image {
  top: -20px;
  left: -130px;
  right: -110px;
  @media #{$betweentabdesk} {
    top: 0px;
    left: -40px;
    right: -90px;
  }
}

.ios-tutorials.windows-tuts-page .step__steps li .text {
  margin-bottom: 24px;
  @media #{$mobile} {
    margin-bottom: 15px;
  }
}

.ios-tutorials.windows-tuts-page .step__steps li .text p .link {
  color: $primaryColor;
  text-decoration: underline !important;
}

.ios-tutorials.windows-tuts-page .step__steps li {
  margin-bottom: 0;
}

.ios-tutorials.windows-tuts-page .step__steps li .imgs img:first-child {
  position: static;
  max-width: 100%;
  width: 100%;
}

.ios-tutorials.windows-tuts-page .step__steps li .imgs {
  margin-bottom: 52px;
  @media #{$mobile} {
    margin-bottom: 25px;
  }
}

.warning-under {
  margin-bottom: 30px;
}

.warning {
  display: flex;
  align-items: center;
  background: rgba(239, 66, 115, 0.05);
  border: 1px solid rgba(239, 66, 115, 0.7);
  box-sizing: border-box;
  border-radius: 5px;
  padding: 28px 40px;
  margin-bottom: 100px;
  @media #{$betweentabdesk} {
    margin-bottom: 30px;
  }
  @media #{$mobile} {
    padding: 20px;
    margin-bottom: 20px;
  }
  @media #{$tablet} {
    margin-bottom: 20px;
    padding: 25px 55px;
    width: 90%;
  }
  p {
    font-size: 18px;
    line-height: 24px;
    color: #ef4273;
    margin-left: 17px;
    width: 80%;
    @media #{$betweentabdesk} {
      width: 90%;
    }
    @media #{$tablet} {
      width: 90%;
    }
    @media #{$mobile} {
      font-size: 14px;
      line-height: 20px;
    }
  }
}

.ios-tutorials.windows-tuts-page .step__title.font-normal {
  font-weight: normal;
}

.share {
  position: absolute;
  left: -80px;
  top: 0;
  z-index: 10;
  &.mac-tuts {
    top: 280px;
  }
  &.fixed {
    position: fixed;
    left: initial;
    margin-left: -80px;
    top: 130px;
  }

  @media #{$mobile} {
    left: 0;
    top: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  // &__icon {
  //   &:hover {
  //     .social-icons {
  //       opacity: 1;
  //       visibility: visible;
  //       height: auto;
  //       margin-top: 10px;
  //     }
  //   }
  // }

  .icon {
    width: 32px;
    height: 32px;
    line-height: 26px;
    border: 1px solid rgba(196, 196, 196, 0.6);
    border-radius: 50%;
    text-align: center;
    cursor: pointer;
    // margin-bottom: 12px;

    svg {
      path {
        transition: all 0.3s ease-in-out;
      }
    }

    &:hover {
      svg {
        path {
          fill: #5b5863;
        }
      }
    }
  }

  .social-icons {
    // opacity: 0;
    // visibility: hidden;
    // height: 0;

    transition: all 0.3s ease-in-out;

    // @media #{$mobile} {
    //   display: block;
    //   margin-top: 0;
    //   margin-left: 24px;
    // }
  }

  li,
  .arrow-up {
    width: 32px;
    height: 32px;
    line-height: 28px;
    border: 1px solid rgba(#c4c4c4, 0.6);
    border-radius: 50%;
    text-align: center;
    cursor: pointer;
    margin: 0 auto 12px;
    transition: all 0.3s ease-in-out;
    background-color: #f9f8f4;

    @media #{$mobile} {
      margin-bottom: 0;
      margin-right: 8px;
      display: inline-block;
    }

    a {
      display: block;
      height: 100%;
    }

    svg {
      path {
        transition: all 0.3s ease-in-out;
      }
    }

    &:hover {
      border: 1px solid #c4c4c4;

      .facebook {
        path {
          fill: #3b5998;
        }
      }

      .twitter {
        path {
          fill: #1da1f2;
        }
      }

      .mail {
        path {
          fill: #fd3e72;
        }
      }
    }
  }

  .arrow-up {
    margin-top: 12px;
    display: none;
    transition: all 0.3s ease-in-out;
    line-height: 26px;
    svg {
      path {
        transition: all 0.3s ease-in-out;
      }
    }
    &:hover {
      svg {
        path {
          fill: #5b5863;
        }
      }
    }
  }
}
