.how-to-use.android-TV {
  .container {
    @media #{$maxTab} {
      padding: 0 20px;
    }
  }
  @media #{$mobile} {
    .row {
      > div:not(:last-child) {
        margin-bottom: 50px;
      }
    }
  }
  .step_info {
    h3 {
      color: $primaryColor;
      margin-bottom: 5px;
    }
    h4 {
      line-height: 160%;
      width: 90%;
      margin: auto;
      @media #{$tablet} {
        font-size: 16px;
      }
    }
  }
}
.devices-os-new.androidTV {
  .devices-content {
    @media (min-width: 1200px) {
      padding-left: 50px;
    }
    .compatability{
      display: block;
    }
    .windows-devices {
      margin-bottom: 30px;
    }
    .windows-devices,.windows-versions {
      ul {
        display: flex;
        flex-wrap: wrap;
        li {
          width: 50%;
        }
      }
    }
  }
}