.streaming-services {
  padding-top: 108px;
  background: #f2f2ed;
  position: sticky;
  top: -270px;
  z-index: 2;
  @media (min-width: 1024px) {
    margin-top: 70px;
  }
  @media #{$maxTab} {
    padding-top: 128px;
  }
  &-content {
    h1 {
      font-size: 46px;
      line-height: 56px;
      max-width: 770px;
      margin: auto;
      span {
        color: #ef4273;
      }
      @media #{$tablet} {
        font-size: 36px;
        line-height: 45px;
        max-width: 640px;
      }
      @media #{$mobile} {
        font-size: 28px;
        line-height: 36px;
      }
    }
    p {
      margin: 36px auto 17px;
      font-size: 22px;
      line-height: 32px;
      max-width: 770px;
      @media #{$tablet} {
        font-size: 18px;
        margin: 24px auto 17px;
        max-width: 640px;
      }
      @media #{$mobile} {
        font-size: 14px;
        line-height: 20px;
        margin: 24px 0 17px;
      }
    }
    form {
      border: 1px solid #ebe8df;
      background-color: #fff;
      box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.05);
      position: relative;
      transition: all 0.3s ease-in-out;
      border-radius: 10px;
      top: 45px;
      @media #{$maxTab} {
        top: 26px;
      }

      &:hover {
        box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.1);
      }

      input {
        width: 100%;
        display: block;
        padding: 29px 40px;
        border: 0;
        font-size: 20px;
        background-color: transparent;

        @media #{$maxTab} {
          height: 56px;
          font-size: 14px;
          line-height: 20px;
          padding: 19px 16px;
        }

        &::placeholder {
          color: #8d8b92;
        }
      }

      button {
        background-color: #fff;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        border: 0;
        cursor: pointer;
        width: 88px;
        height: 100%;
        border-radius: 0 10px 10px 0;

        @media #{$maxTab} {
          width: 56px;

          svg {
            width: 16px;
            height: 16px;
          }
        }

        svg.close-icon {
          display: none;
        }
      }
    }
  }
}

.streaming-countries {
  padding-bottom: 35px;
  background: #fafafa;
  .heading {
    padding-top: 120px;
    background: #fafafa;
    position: sticky;
    top: 120px;
    z-index: 1;
    @media #{$mobile} {
      display: none;
    }

  }
  h2.countries,
  p.services {
    font-size: 21.3333px;
    line-height: 32px;
    display: inline-block;
    width: 200px;
    margin-bottom: 25px;
    text-align: center;
    @media #{$tablet} {
      width: 170px;
      font-size: 20px;
    }
  }
  p.services {
    font-weight: bold;
    margin-left: 15px;
    text-align: left;
  }
  @media #{$mobile} {
    padding-top: 80px;
    padding-bottom: 45px;
  }
  .country-block {
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;
    padding-bottom: 20px;
    @media #{$mobile} {
      display: block;
      margin-bottom: 20px;
      padding-bottom: 5px;
    }
    &:not(:last-child) {
      border-bottom: 1px solid #e8e8db;
    }

    .country-name {
      display: flex;
      align-items: center;
      flex-direction: column;
      min-width: 200px;
      height: 160px;
      justify-content: center;
      text-align: center;
      max-width: 200px;
      @media #{$tablet} {
        min-width: 170px;
        max-width: 170px;
      }
      @media #{$mobile} {
        margin-bottom: 20px;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        height: auto;
        max-width: none;
      }
      img {
        margin-bottom: 25px;
        height: 60px;
        @media #{$tablet} {
          height: 45px;
          margin-bottom: 15px;
        }
        @media #{$mobile} {
          margin-right: 11px;
          margin-bottom: 0;
          height: 20px;
        }
      }
      span {
        font-weight: 500;
        font-size: 28px;
        line-height: 32px;

        @media #{$tablet} {
          font-size: 24px;
        }
        @media #{$mobile} {
          font-size: 18px;
        }
      }
    }
    .country-services {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      @media (max-width: 1199px) {
        justify-content: flex-start;
      }
      .service {
        background: #ffffff;
        box-shadow: 0px 20px 29px rgba(35, 35, 35, 0.07);
        border-radius: 10px;
        height: 160px;
        width: 170px;
        margin: 0 12px 20px;
        &.service-tooltip {
          cursor: pointer;
          border: 4px solid transparent;
          position: relative;
          transition: all 0.3s ease-in-out;
          &:hover {
            border: 4px solid $primaryColor;
            .tooltiptext {
              opacity: 1;
              visibility: visible;
              top: -30px;
            }
          }
          .tooltiptext {
            position: absolute;
            display: inline-block;
            background: #4f4f4f;
            color: #ffffff;
            padding: 7px 14px 10px;
            font-weight: 500;
            font-size: 14px;
            text-align: center;
            line-height: 17px;
            top: -35px;
            transform: translateX(-50%);
            left: 50%;
            opacity: 0;
            visibility: hidden;
            border-radius: 5px;
            transition: all 0.3s ease-in-out;
            width: 135px;
          }

          .tooltiptext:after {
            content: "";
            position: absolute;
            border-width: 8px;
            border-style: solid;
            border-color: #4f4f4f transparent transparent transparent;
            display: block;
            bottom: -16px;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1;
          }
        }
        @media #{$betweentabdesk} {
          margin: 0 6px 12px;
        }
        @media #{$tablet} {
          width: 160px;
          min-width: 160px;
          max-width: 160px;
          margin: 0 6px 12px;
        }
        @media #{$mobile} {
          height: 150px;
          width: 155px;
          min-width: 155px;
          max-width: 155px;
          margin: 0 5px 15px;
        }
        .icon {
          height: 65%;
          position: relative;
          img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            will-change: auto;
            @media #{$maxTab} {
              transform: translate(-50%, -50%) scale(0.9);
            }
          }
        }
        p {
          height: 35%;
          font-size: 14px;
          line-height: 18px;
          text-align: center;
          padding: 0 10px;
          @media #{$mobile} {
            font-size: 12px;
          }
        }
      }
    }
  }
}

.customers-features.streaming-services-page {
  background: #fafafa;
  .customers-features-subtitle {
    font-weight: 600;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 4px;
    color: #fd3e72;
    @media #{$tablet} {
      font-size: 16px;
    }
    @media #{$mobile} {
      font-size: 14px;
    }
  }
  .customers-features-title {
    font-size: 56px;
    line-height: 56px;
    margin: 40px 0 510px;
    @media #{$tablet} {
      font-size: 40px;
      line-height: 45px;
      margin: 30px 0 290px;
    }
    @media #{$mobile} {
      font-size: 28px;
      line-height: 32px;
      margin: 22px 0 40px;
    }
  }
  .customers-features-items {
    top: 265px;

    @media #{$tablet} {
      top: 195px;
    }
  }
}
