.month-free-steps {
  .row {
    > div {
      display: flex;
    }
  }
  .step {
    border: 1px solid #d6d7d8;
    border-radius: 10px;
    padding: 55px 14px;
    @media #{$tablet} {
      padding: 30px 10px 40px;
    }
    @media #{$mobile} {
      padding: 55px 24px 45px;
      margin-bottom: 20px;
    }
    .image {
      width: 260px;
      height: 235px;
      margin: auto;
      display: flex;
      margin-bottom: 50px;
      @media #{$tablet} {
        width: auto;
        height: 190px;
      }
      @media #{$maxTab} {
        margin-bottom: 40px;
      }
      img {
        margin: auto;
        @media #{$tablet} {
          width: 100%;
          display: block;
          margin: 0;
        }
      }
    }
    &-info {
      p {
        margin-top: 15px;
      }
    }
  }
}