.bg-gradient {
  background: linear-gradient(106.64deg, #fe6556 -9.57%, #fd3e72 61.05%, #49a8ff 137.8%);
}

.h-70 {
  height: 63px !important;
  @media (min-width: 1024px) {
    height: 70px !important;
  }
}

.new-green-tag {
  font-size: 11px;
  line-height: 16px;
  padding: 0 5px;
  display: inline-block;
  text-transform: uppercase;
  background: linear-gradient(88.46deg, #08ae7e 33.06%, #0bdfa2 104.26%);
  border-radius: 9px 9px 9px 0px;
  color: #fff;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.wevpn-link {
  color: $primaryColor;
  text-decoration: underline !important;
  transition: all 0.3s ease-in-out;
  font-weight: 500;

  i,
  svg {
    font-weight: bold;
    margin-left: 5px;
    transition: all 0.3s ease-in-out;
  }

  &:hover {
    color: $linkHoverColor !important;
    text-decoration: none !important;

    i,
    svg {
      transform: translateX(3px);
    }
  }
}

.mainColor {
  color: $primaryColor;
}

.copy-ip {
  background: #fd3e72;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  display: inline-block;
  &:hover {
    .tooltiptext {
      visibility: visible;
      opacity: 1;
    }
  }
  .tooltiptext {
    visibility: hidden;
    background-color: #4f4f4f;
    font-size: 12px;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 3px 11px;
    position: absolute;
    z-index: 1;
    bottom: 115%;
    left: 50%;
    transform: translatex(-50%);
    opacity: 0;
    transition: opacity 0.3s;
    font-weight: 500;
    line-height: 24px;
    @media #{$mobile} {
      font-size: 12px;
      padding: 4px 8px;
    }

    &.changed {
      background-color: #08ae7e;
      &:after {
        border-color: #08ae7e transparent transparent transparent;
      }
    }
    &:after {
      content: "";
      position: absolute;
      top: 100%;
      left: 50%;
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: #4f4f4f transparent transparent transparent;
    }
  }
}

.main-bg {
  background-color: #f9f8f4;
}

select {
  background: url(../imgs/Arrow.png) no-repeat right;
  -webkit-appearance: none;

  &:invalid {
    color: #8d8b92;
  }

  & option:first-child {
    color: #8d8b92;
  }

  &:invalid option:not(:first-child) {
    color: #5b5863;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}

.bull {
  color: #c1c0c4;
}

input[type="number"] {
  -moz-appearance: textfield;
  /* Firefox */
}

body {
  font-family: "Averta", "Segoe UI", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-style: normal;
  position: relative;
  overflow-x: hidden;
  color: #5b5863;
  background-color: #fafafa;

  &.body-dashboard {
    background-color: #f2f2ed;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0;
  font-weight: bold;
}

p {
  margin-bottom: 0;
}

::selection {
  background-color: #5b5863;
  color: #fff;
}

a {
  color: #5b5863;
  text-decoration: none !important;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
    position: relative;
  }
}

@media #{$mobile} {
  .container {
    padding: 0 20px;
    position: relative;
  }
}

.wrapper {
  overflow: hidden;
}

/* Change the white to any color */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  box-shadow: 0 0 0 30px white inset !important;
}

/* Change text in autofill textbox */
input:-webkit-autofill {
  -webkit-text-fill-color: #5b5863 !important;
}

.wrapper-header {
  overflow: hidden;
  padding-bottom: 150px;
  position: relative;
  @media #{$betweentabdesk} {
    padding-bottom: 100px;
  }
  @media #{$tablet} {
    padding-bottom: 70px;
  }
  &.is-visible {
    overflow: visible;
  }

  @media #{$mobile} {
    padding-bottom: 0;
  }
  &.homepage {
    padding-bottom: 0;
  }
}

.heading-medium {
  font-size: 36px;
  line-height: 44px;

  @media #{$tablet} {
    font-size: 30px;
    line-height: 38px;
  }
  @media #{$mobile} {
    font-size: 24px;
    line-height: 28px;
  }
}

button:focus {
  box-shadow: none;
  outline: none;
}

body.overlay {
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

body.overlay:after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(#000, 0.5);
  z-index: 1;
  margin: 0;
  overflow: hidden;
}

.arrow {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 13px;
  height: 21px;
  display: inline-block;

  span {
    top: 10px;
    position: absolute;
    width: 8px;
    height: 2px;
    background-color: #acaaae;
    display: inline-block;
    transition: all 0.4s ease;

    &:first-of-type {
      left: 0;
      transform: rotate(45deg);
    }

    &:last-of-type {
      right: 0;
      transform: rotate(-45deg);
    }
  }
}
// Typography
h1,
.h1 {
  font-size: 47px;
  line-height: 140%;
  font-weight: bold;
  @media #{$tablet} {
    font-size: 40px;
  }
  @media #{$mobile} {
    font-size: 28px;
  }
}
h2,
.h2 {
  font-size: 38px;
  line-height: 140%;
  font-weight: bold;
  @media #{$tablet} {
    font-size: 30px;
  }
  @media #{$mobile} {
    font-size: 24px;
  }
}

.h-32 {
  font-size: 32px;
  line-height: 140%;
  font-weight: bold;
  @media #{$tablet} {
    font-size: 28px;
  }
  @media #{$mobile} {
    font-size: 24px;
  }
}

h3 {
  font-size: 24px;
  line-height: 140%;
  @media #{$maxTab} {
    font-size: 20px;
  }
}
h4 {
  font-size: 20px;
  line-height: 140%;
  font-weight: 500;
  @media #{$mobile} {
    font-size: 14px;
  }
}
h5 {
  font-size: 16px;
  line-height: 140%;
  font-weight: 500;
}
h6 {
  font-size: 14px;
  line-height: 140%;
  font-weight: 500;
}

.p-hero {
  font-size: 20px;
  line-height: 160%;
  @media #{$tablet} {
    font-size: 18px;
  }
  @media #{$mobile} {
    font-size: 16px;
  }
}

.p-lg {
  font-size: 18px;
  line-height: 160%;
  @media #{$tablet} {
    font-size: 16px;
  }
  @media #{$mobile} {
    font-size: 14px;
  }
}
.p-md {
  font-size: 16px;
  line-height: 160%;
  @media #{$mobile} {
    font-size: 14px;
  }
}
.p-sm {
  font-size: 14px;
  line-height: 160%;
  @media #{$mobile} {
    font-size: 12px;
  }
}

.p-xs {
  font-size: 12px;
  line-height: 160%;
}

.fw-500 {
  font-weight: 500;
}

.p-100 {
  padding: 100px 0 !important;
  @media #{$maxTab} {
    padding: 60px 0 !important;
  }
}
.p-80 {
  padding: 80px 0 !important;
  @media #{$maxTab} {
    padding: 60px 0 !important;
  }
}

.p-60 {
  padding: 60px 0 !important;
}

.box-shadow {
  background: #ffffff;
  box-shadow: 0px 4px 41px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.opacity-8 {
  opacity: 0.8;
}

img {
  image-rendering: -webkit-optimize-contrast;
  transform: translateZ(0);
  will-change: transform;
}

svg.top-4 {
  transform: translateY(4px);
  @media #{$mobile} {
    transform: translateY(0);
  }
}
.main-btn {
  width: 100%;
  line-height: 64px;
  padding: 0 30px;
}

.lb-title {
  position: relative;
  padding-left: 25px;
  @media #{$maxTab} {
    padding-left: 20px;
    margin-bottom: 20px;
  }
  @media #{$betweentabdesk} {
    padding-left: 20px;
  }
  &::before {
    position: absolute;
    content: "";
    width: 6px;
    height: 100%;
    background-color: $primaryColor;
    left: 0;
    @media #{$maxTab} {
      width: 4px;
    }
  }
}

.m-unset {
  margin: unset;
}

.hero-section {
  padding: 150px 0 100px;
  @media #{$tablet} {
    padding: 60px 0;
  }
  @media #{$mobile} {
    padding: 50px 0 60px;
  }
  @media (min-width: 1200px) and (min-height: 601px) {
    height: calc(100vh - 32px);
    display: flex;
    align-items: center;
  }
  @media (min-height: 1000px) {
    height: auto;
    padding: 150px 0 135px;
  }
  @media (max-height: 600px) {
    height: auto;
    padding: 150px 0 135px;
  }
}

// zd widget
.widget-container.normal {
  border: 0 !important;
}

.text-underline {
  text-decoration: underline !important;
}

.primary__btn,
.secondary__btn {
  background-color: $primaryColor;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  color: #fff;
  display: inline-block;
  border: 0;
  transition: all 0.3s ease-in-out;
  border-radius: 5px;
  @media #{$tablet} {
    font-size: 18px;
  }
  @media #{$mobile} {
    font-size: 16px;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    line-height: 60px;
  }

  &:hover {
    background-color: $linkHoverColor;
    color: #fff;

    i {
      transform: translateX(3px);
    }
    .arrow-triangle {
      transform: translateX(3px);
    }
  }

  &:active {
    background-color: #ff5785;
  }

  i {
    position: relative;
    transition: transform 0.3s ease-in-out;
    top: 2px;
    margin-left: 10px;
  }
  .arrow-triangle {
    position: relative;
    transition: all 0.3s ease-in-out;
    margin-left: 5px;
    margin-right: unset;
    @media #{$mobile} {
      width: 8px;
      height: 11.2px;
    }

    path {
      transition: all 0.3s ease-in-out;
    }
  }
}

.secondary__btn {
  background-color: #ffecf1;
  color: $primaryColor;

  &:hover {
    background-color: $primaryColor;
    color: #fff;

    svg {
      path {
        fill: white;
      }
    }
  }
}

.white_btn {
  background: #FFFFFF;
  border-radius: 5px;
  font-weight: 500;
  font-size: 20px;
  line-height: 25px;
  color: $primaryColor;
  padding: 26px 19px;
  display: inline-block;
  text-align: center;
  transition: all 0.3s ease-in-out;
  @media #{$mobile} {
    font-size: 16px;
  }
  svg {
    position: relative;
    transition: all 0.3s ease-in-out;
    margin-left: 5px;
    @media #{$mobile} {
      width: 8px;
      height: 11.2px;
    }
  }
  &:hover {
    background: #EBEBEB;
    color: $primaryColor;
    svg {
      transform: translateX(3px);
    }
  }
}
