header.get-started-page.get-started-new {
  .navbar {
    background-color: #fff;
  }
  .get-started-login {
    p {
      font-size: 14px;
      line-height: 17px;
      color: #8d8b92;
      font-weight: 500;
      @media #{$mobile} {
        font-size: 12px;
        line-height: 15px;
        flex-direction: column;
        align-items: flex-end;
      }

      a {
        color: #fff;
        margin-left: 12px;
        font-size: 16px;
        padding: 12px 20px;
        &.signin {
          &:hover {
            color: #fff;
          }
        }

        i,
        svg {
          margin-left: 4px;
          top: 1px;
          transition: all 0.3s ease-in-out;
        }

        @media #{$mobile} {
          display: block;
          text-align: right;
          font-size: 14px;
          font-weight: 500;
          transition: all 0.3s ease-in-out;
          min-height: auto;
          line-height: initial;
          color: #fd3e72;
          background-color: transparent;
          margin: 0;
          padding: 0;
          svg {
            path {
              fill: $primaryColor;
            }
          }
        }

        &:hover {
          i,
          svg {
            transform: translateX(2px);
          }
        }
      }
    }
    &.sign-out {
      p .my-account svg {
        transform: translateX(0);
        margin-left: 0;
      }
      p {
        a {
          color: #5b5863;
          padding: 12px 16px;
          &.sign-out {
            padding: 0;
            &:hover {
              color: $primaryColor;
            }
          }
          &:hover {
            color: #fff;
            svg {
              transform: none;
            }
          }
        }
      }
    }
  }
}

/* Start Choose Steps */
.choose-steps.get-started-new {
  padding-top: 100px;
  padding-bottom: 80px;
  background: #f4f6f7;

  @media #{$maxTab} {
    padding-top: 115px;
    padding-bottom: 50px;
  }
  .backplans {
    &:hover {
      svg {
        transform: translateX(-3px);
      }
    }
    svg {
      margin-right: 10px;
      margin-left: unset;
      transition: all 0.3s ease-in-out;
    }
    font-weight: 500;
    font-size: 20px;
    line-height: 25px;
    color: #ef4273;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    @media #{$mobile} {
      font-size: 16px;
      line-height: 20px;
    }
  }

  .choose-steps-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media #{$mobile} {
      flex-direction: row;
    }
  }

  .trustpilot_img_one {
    @media #{$mobile} {
      margin-top: 20px;
    }
  }

  .trustpilot_order_step1 {
    width: 230px;
    @media #{$mobile} {
      width: 207px;
      margin-top: 10px;
    }
  }

  .choose-title {
    font-size: 30px;
    line-height: 50px;
    &.signup-page {
      margin-top: 10px;
      @media #{$mobile} {
        font-size: 24px;
        line-height: 36px;
        margin-bottom: 10px;
      }
    }

    @media #{$mobile} {
      font-size: 28px;
      line-height: 36px;
      width: 100%;
      padding-left: 0;
      margin-bottom: 0;
    }

    @media #{$tablet} {
      font-size: 26px;
      line-height: 36px;
    }

    small {
      font-size: 16px;
      line-height: 44px;
      color: #8d8b92;

      @media #{$mobile} {
        font-size: 14px;
        line-height: 24px;

        &.have-account {
          display: block;
        }
      }

      a {
        color: $primaryColor;
        text-decoration: underline !important;
        font-weight: 500;

        svg {
          transition: all 0.3s ease-in-out;
          margin-left: 4px;
        }

        &:hover {
          color: $linkHoverColor;
          text-decoration: none !important;

          svg {
            transform: translateX(2px);
          }
        }
      }
    }

    img {
      @media #{$mobile} {
        transform: scale(0.6);
        position: absolute;
        margin-left: -15px;
      }
    }
  }
  .choose-text {
    font-size: 18px;
    line-height: 20px;
    color: rgba(91, 88, 99, 0.7);
    margin-top: 5px;
    margin-left: 6px;
    @media #{$maxTab} {
      font-size: 15px;
    }
  }

  .money-back-icon {
    img {
      @media #{$tablet} {
        width: 90px;
      }
      @media #{$mobile} {
        width: 62px;
      }
    }
  }

  .payment-method-heading {
    display: flex;
    justify-content: space-between;
    @media #{$mobile} {
      flex-direction: column;
      .payment-method-title {
        margin-bottom: 8px;
      }
    }
    .secure-payment-logo {
      display: flex;
      @media #{$mobile} {
        padding-left: 20px;
      }
      p {
        font-size: 24px;
        line-height: 32px;
        color: #08ae7e;
        font-weight: 400;
        @media #{$mobile} {
          font-size: 14px;
          line-height: 20px;
        }
      }
      img {
        transform: scale(0.75);
        transform-origin: top;
        @media #{$mobile} {
          width: 24px;
        }
      }
    }
  }

  .choose-steps-list {
    @media #{$mobile} {
      padding-left: 20px;
    }

    li {
      display: inline-block;
      font-size: 18px;
      line-height: 32px;

      @media #{$mobile} {
        font-size: 16px;
        line-height: 32px;
        display: block;
      }

      @media #{$tablet} {
        display: block;
      }

      i {
        color: #08ae7e;
        margin-right: 8px;

        @media #{$mobile} {
          margin-right: 4px;
        }
      }
    }
  }

  .pricing-tables {
    padding-bottom: 0;
    padding-top: 45px;
    @media #{$tablet} {
      .padd-0 {
        padding: 0;
      }
    }
  }

  .create-account {
    margin-top: 10px;
    @media #{$mobile} {
      margin-top: 0;
    }
    .form-group {
      background-color: #fff;
      padding: 14px 30px 18px;
      height: 80px;
      margin-bottom: 0;
      border: 1px solid transparent;
      transition: all 0.3s ease-in-out;
      border-radius: 5px;
      @media #{$betweentabdesk} {
        margin-bottom: 15px;
        &.last {
          margin-bottom: 0;
        }
      }

      &:focus-within label {
        color: #8d8b92;
      }

      &:hover {
        border: 1px solid #e3e0d7;
      }

      @media #{$tablet} {
        margin-bottom: 12px;
        height: 64px;
        padding: 7px 20px 11px;
      }
      @media #{$mobile} {
        margin-bottom: 8px;
        height: 60px;
        padding: 4px 16px;
        &.last {
          margin-bottom: 0;
        }
      }

      label {
        font-size: 12px;
        line-height: 20px;
        font-weight: 500;
        margin-bottom: 5px;
        @media #{$maxTab} {
          margin-bottom: 2px;
        }
      }

      input {
        border: 0;
        padding-left: 0;
        padding: 0;
        background: none;
        margin-top: -5px;
        color: #5b5863;
        font-weight: 500;

        @media #{$mobile} {
          font-size: 14px;
        }

        &::placeholder {
          font-size: 16px;
          color: #8d8b92;
          font-weight: 400;

          @media #{$maxTab} {
            font-size: 14px;
          }
        }
      }
    }
    .data {
      font-size: 14px;
      line-height: 16px;
      color: #8d8b92;
      margin-top: 20px;
      margin-bottom: 72px;

      @media #{$tablet} {
        margin-top: 4px;
      }
      @media #{$mobile} {
        font-size: 12px;
        padding-left: 16px;
        margin-top: 6px;
        margin-bottom: 38px;
      }
    }
  }

  .payment {
    margin-top: 10px;
    @media #{$mobile} {
      margin-top: 0;
    }

    @media (min-width: 1600px) {
      .padd-left {
        padding-left: 25px;
      }
    }

    .back-btn {
      margin-top: 10px;
      @media #{$mobile} {
        text-align: center;
      }

      .back {
        font-size: 20px;
        line-height: 25px;
        font-weight: 500;
        color: $primaryColor;
        transition: all 0.3s ease-in-out;
        padding: 10px;
        display: inline-block;

        i {
          transition: all 0.3s ease-in-out;
        }

        @media #{$mobile} {
          font-size: 16px;
        }

        &:hover {
          color: $linkHoverColor;

          i {
            transform: translateX(-4px);
          }
        }
      }
    }
  }
}

.payment-methods.get-started-new {
  .payment--method {
    transition: all 0.3s ease-in-out;

    &:hover {
      box-shadow: 0px 6px 9px #e6e4df;
    }
  }

  .card-info {
    background-color: #fff;
    padding: 32px 32px 36px;
    border-top: 1px solid #e3e0d3;
    border-radius: 0 0 10px 10px;
    display: none;
    @media #{$mobile} {
      border-radius: 0 0 5px 5px;
    }

    @media #{$maxTab} {
      padding: 20px 20px 35px;
    }

    @media #{$betweentabdesk} {
      padding: 20px 20px 35px;
    }

    .padd {
      padding-right: 10px;
      padding-left: 10px;

      @media #{$maxTab} {
        padding-right: 15px;
        padding-left: 15px;

        &.col-6 {
          padding-left: 5px;
        }
      }

      @media #{$betweentabdesk} {
        padding-right: 7.5px;
        padding-left: 7.5px;
      }
    }

    .padd-right {
      padding-right: 10px;

      @media #{$maxTab} {
        padding-right: 15px;

        &.col-6 {
          padding-right: 5px;
        }
      }

      @media #{$betweentabdesk} {
        padding-right: 7.5px;
      }
    }

    .padd-left {
      padding-left: 10px;

      @media #{$maxTab} {
        padding-left: 15px;
      }

      @media #{$betweentabdesk} {
        padding-left: 7.5px;
      }
    }

    .form-group {
      border: 1px solid #e0ded5;
      padding: 2px 20px 7px;
      margin-bottom: 17px;
      height: 60px;
      border-radius: 5px;

      @media #{$maxTab} {
        margin-bottom: 10px;
        padding: 4px 16px;
      }

      @media #{$betweentabdesk} {
        padding: 2px 10px 7px;
        margin-bottom: 15px;
      }

      label {
        font-size: 12px;
        line-height: 20px;
        font-weight: 500;
        margin-bottom: 0;
        position: relative;

        @media #{$betweentabdesk} {
          font-size: 11px;
        }

        svg {
          cursor: pointer;
          display: inline-block;
          margin-left: 5px;
          position: relative;
          z-index: 2;

          path {
            transition: all 0.3s ease-in-out;
          }

          &:hover {
            & ~ .tooltiptext {
              opacity: 1;
              visibility: visible;
              top: -93px;
            }

            path {
              fill: #908e91;
            }
          }
        }

        .tooltiptext {
          position: absolute;
          display: inline-block;
          background: #4f4f4f;
          box-shadow: 0px 6px 11px rgba(79, 79, 79, 0.3);
          width: 200px;
          height: 86px;
          font-size: 12px;
          line-height: 14px;
          color: #ffffff;
          padding: 12px 16px;
          top: -98px;
          left: 25px;
          font-weight: 400;
          opacity: 0;
          visibility: hidden;
          border-radius: 5px;
          transition: all 0.3s ease-in-out;

          &:after {
            content: "";
            position: absolute;
            border-width: 8px;
            border-style: solid;
            border-color: #4f4f4f transparent transparent transparent;
            display: block;
            bottom: -16px;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1;
          }
        }
      }

      input {
        border: 0;
        padding: 0;
        font-weight: 500;
        font-size: 14px;

        @media #{$maxTab} {
          line-height: 20px;
        }

        &::placeholder {
          font-size: 14px;
          color: #999;
          font-weight: 400;
        }
      }

      & ~ span {
        position: relative;
        font-size: 12px;
        color: #898691;
        display: inline-block;
        top: -11px;
      }
    }

    .continue {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 0;
      @media #{$maxTab} {
        margin-bottom: 20px;
      }

      strong {
        font-weight: bold;
      }
    }

    .all-coins {
      background: #f2f0e6;
      border: 1px solid #e3e0d3;
      border-radius: 10px;
      padding: 24px 40px;
      margin-bottom: 10px;
      margin-top: 30px;

      @media #{$betweentabdesk} {
        padding: 20px 50px;
      }
      @media #{$maxTab} {
        padding: 20px 32px 18px;
        margin-top: 0;
      }

      p {
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: #8d8b92;
        margin-bottom: 20px;

        @media #{$mobile} {
          font-size: 13px;
        }
      }

      .coins {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;

        @media #{$betweentabdesk} {
          flex-wrap: nowrap;
        }

        li {
          margin: 0 10px 8px;
          @media #{$betweentabdesk} {
            margin: 0 5px;
          }
          @media #{$mobile} {
            margin: 0 5px 8px;
          }
          .icon {
            width: 35px;
            height: 35px;
            display: flex;
            img {
              margin: auto;
            }
          }
          span {
            display: block;
            font-size: 12px;
            line-height: 13px;
            font-weight: 500;
            margin-top: 10px;
          }
        }
      }
    }

    .order-info {
      border: 0;
      padding-top: 0;
      .googlePay-not-support {
        padding: 20px 40px;
        background: #faf2e8;
        border: 1px solid #faca96;
        border-radius: 10px;
        margin-bottom: 10px;
        p {
          padding-left: 35px;
          position: relative;
          font-size: 16px;
          line-height: 24px;
          color: #fa8300;
          font-size: 14px;
          &:first-child {
            font-size: 16px;
            font-weight: 500;
            padding-right: 60px;
            margin-bottom: 10px;
          }
          img {
            position: absolute;
            top: 2px;
            left: 0;
          }
        }
      }

      .order-info-content {
        .order-total {
          font-size: 18px;
          line-height: 24px;
          margin-bottom: 8px;

          .total {
            font-weight: bold;
          }
        }

        .bill {
          font-size: 14px;
          line-height: 14px;
          color: #8d8b92;

          @media #{$mobile} {
            margin-bottom: 24px;
          }
        }
      }

      .secure-payment {
        border-top: 1px dashed #e3e0d3;
        padding-top: 17px;
        margin-top: 17px;
        flex-direction: column;
        li {
          display: flex;
          align-items: center;
          font-size: 14px;
          line-height: 20px;
          font-weight: bold;
          &:not(:last-child) {
            margin-bottom: 10px;
          }

          @media #{$mobile} {
            padding-left: 0;
            font-size: 12px;
            align-items: flex-start;
          }
          p {
            span {
              width: 100%;
            }
          }

          .icon {
            width: 40px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            margin-right: 15px;
            @media #{$mobile} {
              width: 25px;
              height: 25px;
              line-height: 25px;
              margin-right: 10px;
              position: relative;
              top: 3px;
              svg {
                width: 25px;
                height: 25px;
              }
            }
            img {
              display: block;
              width: 100%;
              @media #{$mobile} {
                width: 25px;
                height: 25px;
                margin-right: 0;
              }
            }
          }
        }
      }

      .credit-card-submit,
      .paypal-btn {
        width: 100%;
        height: 56px;
        line-height: 56px;
        margin-top: 22px;
        margin-bottom: 15px;

        @media #{$maxTab} {
          font-size: 16px;
          margin-top: 0;
        }
      }
      #join-now {
        &.fade {
          opacity: 0.5;
        }
        svg {
          display: none;
        }
      }

      .paypal-btn {
        background-color: #ffc63f;
        color: #44424a;
        transition: all 0.3s ease-in-out;

        &:hover {
          background-color: #ffd163;
        }
        &.amazon {
          img {
            top: 5px;
            @media #{$mobile} {
              top: 2px;
            }
          }
        }
        &.gpay {
          background-color: $primaryColor;
          color: #fff;
          &:hover {
            background-color: $linkHoverColor;
          }
          img {
            width: 50px;
          }
        }

        img {
          position: relative;
          left: 3px;
          top: -7px;
          &.paypal-image {
            top: 0;
          }

          @media #{$maxTab} {
            transform: scale(0.67) translateX(-24px);
            &.coin {
              width: 120px;
              top: -6px;
              transform: scale(1) translateX(0);
            }
          }
        }
      }

      .google-apple-pay {
        display: flex;
        justify-content: space-between;
        @media #{$mobile} {
          flex-direction: column;
          button:last-child {
            margin-top: 0;
          }
          img {
            transform: scale(0.75) translateX(-9px);
          }
        }
        .paypal-btn {
          flex-basis: 48%;
        }
        @media #{$maxTab} {
          img {
            transform: scale(0.7) translateX(-12px);
          }
          button {
            margin-top: 15px;
            &:first-child {
              margin-bottom: 10px;
            }
          }
        }
      }

      p.complete {
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: #8d8b92;
        @media #{$mobile} {
          width: 240px;
          margin: auto;
        }

        a {
          font-weight: 500;
          color: $primaryColor;
          transition: all 0.3s ease-in-out;

          &:hover {
            color: $linkHoverColor;
            text-decoration: underline !important;
          }
        }
      }
    }
  }

  .payment--method {
    overflow: hidden;
    background-color: #fff;
    border-radius: 10px;
    @media #{$mobile} {
      border-radius: 5px;
    }
    &.no-border-radius-bottom {
      border-radius: 10px 10px 0 0;
      @media #{$mobile} {
        border-radius: 5px 5px 0 0;
      }
    }

    &:not(:first-child) {
      margin-top: 12px;

      @media #{$mobile} {
        margin-top: 8px;
        margin-bottom: 0;
      }
    }

    .pay-card {
      position: relative;

      .cards {
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        @media #{$mobile} {
          right: 0;
          &.coins {
            right: 5px;
          }
        }
        li {
          margin-left: 10px;
          display: inline-block;
          margin-right: 0;

          @media #{$mobile} {
            margin-left: -12px;
            transform: scale(0.6);
          }

          @media #{$tablet} {
            transform: scale(0.9);
          }

          &.more {
            background: #6b5b60;
            border: 1px solid #6b5b60;
            color: #fff;
            font-size: 14px;
            font-weight: 500;
            width: 28px;
            height: 28px;
            line-height: 25px;
            border-radius: 50%;
            cursor: pointer;
            text-align: center;
            @media #{$tablet} {
              display: none;
            }
          }
          &.itc,
          &.bhc,
          &.eth,
          &.zec {
            display: none;
            @media #{$tablet} {
              display: inline-block;
            }
          }
        }

        &.coins {
          li {
            @media #{$mobile} {
              margin-left: -5px;
              transform: scale(0.7);
            }
            @media #{$tablet} {
              transform: scale(0.9);
            }
          }
        }
      }

      input[type="radio"] {
        display: none;
        &:checked {
          & + label:before {
            background-color: $primaryColor;
            border-color: $primaryColor;
          }
        }
      }

      .label-radio {
        font-size: 18px;
        text-transform: capitalize;
        position: relative;
        cursor: pointer;
        height: 80px;
        display: block;
        padding-left: 80px;
        padding-right: 20px;
        line-height: 80px;
        font-weight: 500;
        margin-bottom: 0;
        z-index: 2;
        @media #{$betweentabdesk} {
          padding-left: 60px;
        }

        @media #{$maxTab} {
          font-size: 16px;
          padding-left: 53px;
          line-height: 64px;
          height: 64px;
        }
        @media #{$mobile} {
          padding-left: 43px;
          font-size: 14px;
        }

        &:before {
          content: "";
          position: absolute;
          top: 25px;
          left: 32px;
          width: 32px;
          height: 32px;
          border-radius: 50%;
          border: 1px solid #adaba5;
          @media #{$betweentabdesk} {
            left: 18px;
          }

          @media #{$mobile} {
            width: 20px;
            height: 20px;
            top: 22px;
            left: 16px;
          }

          @media #{$tablet} {
            width: 25px;
            height: 25px;
            top: 21px;
            left: 20px;
          }
        }

        &:after {
          top: 33px;
          left: 44px;
          content: "";
          display: block;
          position: absolute;
          width: 8px;
          height: 13px;
          border: solid #ffffff;
          border-width: 0 3px 3px 0;
          transform: rotate(45deg);
          transition: all 0.3s ease-in-out;

          @media #{$mobile} {
            top: 26px;
            left: 23px;
            width: 6px;
            height: 10px;
            border-width: 0 2.5px 2.5px 0;
          }

          @media #{$tablet} {
            top: 26px;
            left: 29px;
          }
          @media #{$betweentabdesk} {
            left: 30px;
          }
        }
      }

      img {
        position: relative;
        z-index: 1;

        &.paypal-img {
          position: absolute;
          right: 15px;
          top: 50%;
          transform: translateY(-50%);
          &.amazon-img {
            top: 55%;
          }
          @media #{$mobile} {
            transform: translateY(-50%) scale(0.7);
            right: 0;
          }

          @media #{$tablet} {
            transform: translateY(-50%) scale(0.8);
            transform-origin: right;
          }
          @media #{$betweentabdesk} {
            transform: translateY(-50%) scale(0.8);
          }
        }
      }
    }
    .payment-icons {
      img {
        &:first-child {
          right: 80px;
          @media #{$mobile} {
            right: 50px;
          }
          @media #{$tablet} {
            right: 60px;
          }
        }
      }
    }
  }
}

