.choose-steps.get-started-new .pricing-tables {
  .price-card {
    background-color: #fff;
    transition: all 0.3s ease-in-out;

    &.one,
    &.three,
    &.four {
      margin: 34px auto 0;
      border-radius: 10px;
      max-width: 370px;
      &:hover {
        box-shadow: 0px 6px 9px #e6e4df;
      }

      @media #{$mobile} {
        margin: 10px auto 0;
      }

      @media #{$tablet} {
        margin: 14px auto 0;
        height: 360px;
      }
    }

    input[type="radio"] {
      display: none;

      &:checked + label:before {
        background-color: $primaryColor;
        border-color: $primaryColor;
      }

      &:checked + label:after {
        border-color: #fff;
      }

      &:checked + .label-radio .price-top .bill-month .value,
      &:hover + .label-radio .price-top .bill-month .value {
        color: $primaryColor;
      }

      &:checked + .label-radio .price-bottom a {
        color: #fff;
        background-color: $primaryColor;
        &:hover {
          background-color: $linkHoverColor;
        }
        svg {
          path {
            fill: #fff;
          }
        }
      }
    }

    .label-radio {
      position: relative;
      cursor: pointer;
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 40px;
      @media (min-width: 1200px) {
        min-height: 415px;
      }
      @media #{$mobile} {
        padding: 24px 16px 35px;
      }

      @media #{$tablet} {
        display: flex;
        justify-content: space-between;
      }

      &:before {
        content: "";
        position: absolute;
        width: 48px;
        height: 48px;
        top: 40px;
        left: 40px;
        border-radius: 50%;
        border: 1px solid #e0ded5;
        text-align: center;
        line-height: 48px;
        transition: all 0.3s ease-in-out;

        @media #{$betweentabdesk} {
          width: 38px;
          height: 38px;
          top: 32px;
          left: 32px;
        }
      }

      &:after {
        content: "";
        display: block;
        position: absolute;
        top: 55px;
        left: 61px;
        width: 8px;
        height: 15px;
        border: solid #e0ded5;
        border-width: 0 3px 3px 0;
        transform: rotate(45deg);
        transition: all 0.3s ease-in-out;

        @media #{$betweentabdesk} {
          top: 43px;
          left: 48px;
          height: 13px;
        }

        @media #{$mobile} {
          top: 38px;
          left: 36px;
        }
      }

      .price-top {
        display: block;

        .month {
          @media (min-width: 768px) and (max-width: 1200px) {
            margin-bottom: 40px;
            margin-top: 20px;
            font-size: 16px;
          }
          @media #{$tablet} {
            margin-top: 40px;
            margin-bottom: 20px;
          }
        }

        .bill-month {
          display: flex;
          justify-content: center;

          .value {
            margin-left: 5px;
            transition: all 0.3s ease-in-out;

            @media #{$tablet} {
              font-size: 50px;
              line-height: 50px;
            }

            @media (min-width: 768px) and (max-width: 1200px) {
              font-size: 52px;
              line-height: 45px;
            }
          }

          .mo {
            align-self: flex-end;
          }
        }
      }

      .price-bottom {
        display: block;

        .deleted {
          line-height: 20px;
          text-decoration-line: line-through;
          color: #afadb2;
        }

        .every-month {
          display: block;
        }

        a {
          display: block;
          width: 100%;
          padding: 21px 0;
          margin-top: 30px;
          @media (max-width: 1200px) {
            padding: 17px 0;
            margin-top: 10px;
            font-size: 16px;
            svg {
              width: 8px;
            }
          }
        }
      }
    }
  }

  @media #{$tablet} {
    .price-card .label-radio .price-top .bill-month .value {
      font-size: 36px;
      line-height: 45px;
    }

    .price-card .label-radio .price-top .month {
      margin-top: 20px;
      margin-bottom: 30px;
    }

    .price-card {
      .label-radio {
        padding: 30px 8px 15px;
        flex-direction: column;

        &:after {
          top: 40px;
          left: 35px;
          height: 12px;
        }
      }
    }
  }

  .price-card .label-radio .price-top .bill-month {
    position: relative;
    width: 140px;
    margin: auto;
  }
  @media #{$mobile} {
    .row {
      > div {
        margin-bottom: 40px;
      }
    }
  }

  .price-card .label-radio .price-top .month {
    background: #fafafa;
    box-shadow: 0px 4px 10px #dbdbdb;
    border-radius: 21px;
    display: block;
    font-weight: bold;
    font-size: 17px;
    text-align: center;
    color: #5b5863;
    width: 127px;
    height: 40px;
    line-height: 40px;
    margin: 65px auto 30px;
    @media #{$maxTab} {
      height: 36px;
      line-height: 36px;
      font-size: 14px;
    }
    @media #{$tablet} {
      margin: 55px auto 20px;
    }
    @media #{$mobile} {
      margin: 75px auto 40px;
    }
  }

  .price-card .label-radio {
    @media #{$betweentabdesk} {
      padding: 24px 17px;
    }
  }

  .price-card .label-radio .price-top .bill-month .usd {
    font-weight: 500;
    font-size: 40px;
    line-height: 45px;
    color: #5b5863;
  }
  .price-card .label-radio .price-top .bill-month .value {
    font-weight: 500;
    font-size: 120px;
    line-height: 82px;
    @media #{$tablet} {
      font-size: 100px;
    }
  }
  .price-card .label-radio .price-top .bill-month .cent {
    position: absolute;
