  }

  .price-card.one .label-radio .price-bottom .every-month {
    padding-top: 80px;
    @media #{$tablet} {
      padding-top: 0;
    }
  }

  .price-card .label-radio .price-bottom .every-month {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: rgba(91, 88, 99, 0.7);
