header.sst-page {
  background-color: #f0fbf8;
}

.hero-img.sst-page {
  background-color: #f0fbf8;
  .hero-img-content .hero-img-text {
    @media #{$mobile} {
      width: 90%;
      margin: auto;
    }
  }
}

.security-center.sst-page {
  .server-automation {
    background: #f9f8f4;
    border-radius: 40px;
    padding: 65px 160px 70px;
    text-align: center;
    margin: 90px 0 100px;
    @media #{$betweentabdesk} {
      padding: 65px 90px 70px;
      margin: 70px 0;
    }
    @media #{$tablet} {
      padding: 65px 40px 70px;
      margin: 50px 0;
      border-radius: 20px;
    }
    @media #{$mobile} {
      margin: 50px 0;
      border-radius: 10px;
      padding: 60px 30px;
    }
    h3 {
      margin-bottom: 25px;
    }
    p {
      font-size: 18px;
      line-height: 28px;
      @media #{$tablet} {
        font-size: 17px;
      }
      @media #{$mobile} {
        font-size: 16px;
      }
      &:not(:last-child) {
        margin-bottom: 35px;
        @media #{$maxTab} {
          margin-bottom: 25px;
        }
      }
    }
  }
  .text {
    p {
      @media (min-width: 1200px) {
        width: 95%;
      }
    }
  }
  .linux-img {
    @media #{$maxTab} {
      margin-bottom: 30px;
    }
  }
}
