.h-m-w {
  @media #{$maxTab} {
    height: 63px;
  }
}
.alert-hero-img,
.weblock-hero-img {
  padding: 110px 0 70px;
  @media #{$maxTab} {
    padding: 50px 0;
  }
  .title {
    @media #{$betweentabdesk} {
      font-size: 44px;
    }
    @media #{$tablet} {
      font-size: 33px;
    }
  }
  .text {
    font-size: 16px;
    margin: 20px 0 40px;
    @media #{$betweentabdesk} {
      font-size: 20px;
    }
    @media (min-width: 1200px) {
      margin: 30px 0 80px;
      font-size: 20px;
    }
  }
  .hero-img-btn {
    line-height: 64px;
    padding: 0;
    display: block;
    max-width: 240px;
    text-align: center;
    @media #{$maxTab} {
      width: 100%;
    }
    @media #{$tablet} {
      font-size: 18px;
    }
    @media #{$mobile} {
      max-width: unset;
    }
  }
  .hero-img-image {
    @media #{$mobile} {
      margin: 0 auto 34px;
    }
  }
}

.how-to-use-alert {
  padding: 70px 0;
  @media #{$mobile} {
    padding: 60px 0;
  }
  .how-to-use-content {
    .sm-scrolled {
      position: relative;
      @media (min-width: 768px) {
        &:before {
          content: "";
          height: 1px;
          background-color: #fd3e72;
          width: 66%;
          position: absolute;
          top: 85px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
  .how-to-use-title {
    @media #{$mobile} {
      padding: 0 20px;
    }
  }
  .step {
    margin-top: 60px;
    @media #{$tablet} {
      padding-bottom: 20px;
    }
    .step-number {
      color: #fff;
      width: 50px;
      height: 50px;
      line-height: 50px;
      background-color: $primaryColor;
      border-radius: 50%;
      margin: auto;
      @media #{$tablet} {
        font-size: 20px;
      }
    }
    .step-txt {
      font-size: 18px;
      line-height: 140%;
      margin: 30px 0 25px;
      @media (min-width: 768px) {
        min-height: 50px;
      }
      @media (min-width: 1200px) {
        padding: 0 50px;
      }
      @media #{$tablet} {
        font-size: 16px;
      }
      @media #{$mobile} {
        padding: 0 20px;
        margin: 20px 0;
      }
    }
  }
  .step .step-img {
    width: 100%;
    img {
      box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
    }
  }
}

.alert-guard-email,
.alert-check-password,
.alert-security-reports,
.alert-secure-identification,
.what-is-weblock,
.why-need-weblock,
.how-weblock-works {
  padding: 75px 0;
  @media #{$maxTab} {
    padding: 60px 0;
  }
  .img {
    @media #{$mobile} {
      margin-bottom: 30px;
      text-align: center;
      img {
        max-width: 90%;
      }
    }
    &.security-img {
      img {
        @media #{$mobile} {
          transform: rotateY(180deg);
        }
      }
    }
  }
  .text-content {
    @media (min-width: 1024px) {
      padding-right: 50px;
    }
    h2 {
      margin-bottom: 30px;
      @media #{$mobile} {
        margin-bottom: 14px;
      }
    }
    .p-lg {
      @media #{$mobile} {
        font-size: 14px;
        padding-right: 20px;
      }
    }
  }
}
.get-alert-btn {
  line-height: 64px;
  max-width: 240px;
  width: 100%;
  margin-top: 50px;
  @media #{$maxTab} {
    margin-top: 40px;
  }
  @media #{$tablet} {
    font-size: 18px;
  }
  @media #{$mobile} {
    max-width: unset;
  }
}
