.primary__btn,
.secondary__btn {
  background-color: $primaryColor;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  color: #fff;
  display: inline-block;
  border: 0;
  transition: all 0.3s ease-in-out;
  border-radius: 5px;
  @media #{$tablet} {
    font-size: 18px;
  }
  @media #{$mobile} {
    font-size: 16px;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    line-height: 60px;
  }

  &:hover {
    background-color: $linkHoverColor;
    color: #fff;

    i {
      transform: translateX(3px);
    }
    .arrow-triangle {
      transform: translateX(3px);
    }
  }

  &:active {
    background-color: #ff5785;
  }

  i {
    position: relative;
    transition: transform 0.3s ease-in-out;
    top: 2px;
    margin-left: 10px;
  }
  .arrow-triangle {
    position: relative;
    transition: all 0.3s ease-in-out;
    margin-left: 5px;
    margin-right: unset;
    @media #{$mobile} {
      width: 8px;
      height: 11.2px;
    }

    path {
      transition: all 0.3s ease-in-out;
    }
  }
}

.secondary__btn {
  background-color: #ffecf1;
  color: $primaryColor;

  &:hover {
    background-color: $primaryColor;
    color: #fff;

    svg {
      path {
        fill: white;
      }
    }
  }
}

.white_btn {
  background: #FFFFFF;
  border-radius: 5px;
  font-weight: 500;
  font-size: 20px;
  line-height: 25px;
  color: $primaryColor;
  padding: 26px 19px;
  display: inline-block;
  text-align: center;
  transition: all 0.3s ease-in-out;
  @media #{$mobile} {
    font-size: 16px;
  }
  svg {
    position: relative;
    transition: all 0.3s ease-in-out;
    margin-left: 5px;
    @media #{$mobile} {
      width: 8px;
      height: 11.2px;
    }
  }
  &:hover {
    background: #EBEBEB;
    color: $primaryColor;
    svg {
      transform: translateX(3px);
    }
  }
}
