.smartdns-hero {
  padding-top: 90px;
  overflow: hidden;
  @media #{$maxTab} {
    padding-top: 80px;
  }

  &__content {
    position: relative;
    padding: 150px 0 250px;
    @media #{$betweentabdesk} {
      padding: 100px 0 150px;
    }

    @media #{$maxTab} {
      padding: 30px 0 60px;
    }
  }

  &__text {
    position: relative;
    z-index: 2;
    h1 {
      font-size: 57px;
      line-height: 62px;
      position: relative;
      @media #{$betweentabdesk} {
        font-size: 54px;
        line-height: 60px;
      }
      @media #{$maxTab} {
        margin-top: 80px;
        margin-bottom: 23px;
      }
      @media #{$tablet} {
        text-align: center;
        width: 80%;
        margin: 80px auto 23px;
      }
      .title-shape {
        position: absolute;
        top: -45px;
        left: -45px;
        @media #{$betweentabdesk} {
          left: -20px;
        }
        @media #{$maxTab} {
          left: 0;
        }
      }
    }
    p {
      font-size: 20px;
      line-height: 30px;
      margin: 32px 0;
      width: 80%;
      @media #{$betweentabdesk} {
        width: 100%;
      }
      @media #{$maxTab} {
        margin: 24px 0 70px;
        width: 100%;
      }
      @media #{$tablet} {
        text-align: center;
      }
    }
    .smartdns-btns {
      a {
        max-width: 274px;
        padding: 23px;
        display: block;
        @media #{$maxTab} {
          margin: auto;
          display: block;
        }
      }
    }
  }

  &__image {
    position: absolute;
    right: -120px;
    top: -125px;
    left: -75px;
    z-index: 1;

    @media #{$betweentabdesk} {
      right: -90px;
      top: -75px;
      left: -15px;
    }
    @media #{$maxTab} {
      position: static;
    }
  }
}

.smartdns-info {
  position: relative;
  padding-bottom: 180px;
  background-color: #f7f7f7;
  padding-top: 64px;
  @media #{$betweentabdesk} {
    padding-bottom: 80px;
  }
  @media #{$maxTab} {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  h2 {
    @media #{$mobile} {
      margin: 0 auto 120px;
    }
    @media #{$betweentabdesk} {
      font-size: 40px;
    }
  }

  .image {
    position: absolute;
    top: 30px;
    left: 43px;
    right: 24px;

    @media #{$betweentabdesk} {
      top: 80px;
      left: -65px;
      right: 40px;
    }
    @media #{$maxTab} {
      position: static;
    }
  }

  &__list {
    margin-top: 80px;
    @media #{$maxTab} {
      margin-top: 40px;
    }
    li {
      display: flex;
      &:not(:last-child) {
        margin-bottom: 35px;
      }
      .icon {
        margin-right: 24px;
        position: relative;
        top: 6px;
      }
      .text {
        font-size: 18px;
        line-height: 32px;
      }
    }
  }
}

.smartdns-pricing-tables {
  padding-top: 150px;
  @media #{$betweentabdesk} {
    padding-top: 80px;
  }

  @media #{$tablet} {
    padding-top: 60px;
  }

  @media #{$mobile} {
    padding-top: 45px;
    padding-bottom: 60px;
  }

  h2 {
    margin-bottom: 110px;
  }
}

.smartdns--hero {
  padding-top: 160px;
  @media #{$betweentabdesk} {
    padding-top: 120px;
  }
  @media #{$maxTab} {
    padding: 60px 0;
  }
  .text {
    @media #{$mobile} {
      margin-top: 40px;
    }
    h1 {
      @media #{$betweentabdesk} {
        font-size: 40px;
      }
      @media #{$tablet} {
        font-size: 32px;
      }
    }
    p {
      font-size: 20px;
      line-height: 160%;
      margin: 20px 0 60px;
      @media #{$betweentabdesk} {
        font-size: 18px;
        margin: 20px 0 40px;
      }
      @media #{$maxTab} {
        font-size: 16px;
        margin: 15px 0 35px;
      }
    }
    .hero-img-btn {
      line-height: 64px;
      max-width: 260px;
      width: 100%;
      @media #{$mobile} {
        max-width: unset;
      }
    }
  }
}

.smartdns--info {
  h2 {
    margin-bottom: 60px;
    @media #{$mobile} {
      margin-bottom: 40px;
    }
  }
  .image {
    @media #{$maxTab} {
      margin-bottom: 40px;
    }
  }
  .smartdns--info-list {
    li {
      display: flex;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      svg {
        margin-right: 15px;
      }
    }
  }
}
