/* Start Contact */
section.contact-us {

  .group {
    display: flex;
    @media #{$mobile} {
      display: block;
    }
    .item {
      width: 49%;
      padding: 0;
      min-height: 80px;
      @media #{$mobile} {
        width: 100%;
        min-height: 60px;
      }
      &:hover {
        border: 1px solid #e0ded5;
      }
      label {
        font-size: 12px;
        font-weight: 500;
        margin-bottom: 0;
        padding-top: 15px;
        padding-left: 30px;
        display: block;
        @media #{$mobile} {
          padding-top: 5px;
          padding-left: 16px;
        }
      }
      input[type="text"] {
        padding-left: 30px;
        @media #{$mobile} {
          padding-left: 16px;
        }
      }
      &:last-child {
        margin-left: 2%;
        @media #{$mobile} {
          margin-left: 0;
        }
      }
    }
  }

  .padd-right {
    padding-right: 5px;
    @media #{$mobile} {
      padding-right: 15px;
    }
  }
  .padd-left {
    padding-left: 5px;
    @media #{$mobile} {
      padding-left: 15px;
    }
  }

  .message-sent {
    background: #ffffff;
    box-shadow: 0px 50px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    padding: 100px 60px 60px;
    display: none;
    border-radius: 10px;
    @media #{$mobile} {
      padding: 40px 24px 24px;
    }

    &-detail {
      .icon {
        svg {
          @media #{$mobile} {
            width: 64px;
          }
        }
      }
      h3 {
        font-size: 24px;
        line-height: 32px;
        margin: 33px 0 21px;
        @media #{$mobile} {
          font-size: 16px;
          line-height: 24px;
        }
      }

      p {
        font-size: 16px;
        line-height: 28px;
        @media #{$mobile} {
          font-size: 14px;
          line-height: 24px;
        }
      }
    }

    .continue-btn {
      margin-top: 200px;
      @media #{$mobile} {
        margin-top: 80px;
      }
      a {
        background: #08ae7e;
        font-size: 20px;
        line-height: 56px;
        color: #fcf9f2;
        border: 0;
        height: 56px;
        text-align: center;
        color: #fff;
        display: block;
        border-radius: 5px;
      }
    }
  }
}

.contact-us-content {
  .custom-alert {
    margin-top: -12px;
    margin-bottom: 16px;
    font-size: 12px;
    line-height: 20px;
    color: #f04545;
    display: none;

    @media #{$mobile} {
      margin-top: -5px;
      margin-bottom: 10px;
    }

    img {
      position: relative;
      top: -2px;
      margin-right: 3px;
    }
  }

  textarea {
    min-height: 100px !important;
    overflow: hidden;

    @media #{$mobile} {
      min-height: 80px !important;
    }
  }

  .form-group.file {
    height: 60px;
    padding: 0 !important;
  }

  .custom--file {
    position: relative;
    height: 100%;

    img {
      position: absolute;
      top: 16px;
      left: 30px;
      z-index: 1;
      cursor: pointer;

      @media #{$mobile} {
        left: 10px;
      }
    }

    span {
      position: absolute;
      top: 20px;
      left: 60px;
      font-size: 14px;
      color: #8d8b92;
      z-index: 1;
      cursor: pointer;

      @media #{$mobile} {
        left: 40px;
        font-size: 12px;
        top: 20px;
      }
    }

    input[type="file"] {
      background-color: transparent;
      z-index: 2;
      position: relative;
      cursor: pointer;
      height: 100%;
    }

    button {
      position: absolute;
      right: 8px;
      top: 8px;
      z-index: 1;
      background: none;
      font-size: 16px;
      text-align: center;
      color: $primaryColor;
      border: 1px solid $primaryColor;
      border-radius: 5px;
      padding: 7px 26px;
      font-weight: 500;

      @media #{$mobile} {
        font-size: 14px;
        top: 12px;
        padding: 7px 9px;
      }
    }
  }

  .submit {
    button {
      width: 100%;
      line-height: 64px;
      position: relative;
      text-align: center;
      max-width: 230px;

      &.fade {
        opacity: 0.6;
      }

      @media #{$maxTab} {
        font-size: 16px;
        max-width: unset;
        line-height: 58px;
      }

      svg {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 30px;
      }

      span.spinner {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        animation: spinner 1.2s linear infinite;
        display: inline-block;
        position: absolute;
        right: 30px;

        &.spinner-2 {
          border: 2px solid rgba(#fff, 0.4);
          border-left: 2px solid #fff;
        }
      }

      @keyframes spinner {
        0% {
          transform: rotate(0deg);
        }

        100% {
          transform: rotate(360deg);
        }
      }
    }
  }
  form + p {
    font-size: 13px;
    line-height: 20px;
    color: #8d8b92;
    margin-top: 20px;
    text-align: center;
    a:first-child {
      font-weight: 500;
      color: #5b5863;
      transition: all 0.3s;
      &:hover {
        color: $primaryColor;
      }
    }

    @media #{$mobile} {
      span {
        display: block;
      }
    }
    a:last-child {
      color: $primaryColor;
      font-weight: 500;
      text-decoration: underline !important;
      &:hover {
        text-decoration: none !important;
      }
    }
  }
}

/* End Contact */
