// responsible Disclosure
.responsible-hero-img {
  @media (max-width: 1200px) {
    padding: 115px 0 60px;
  }
  @media #{$maxTab} {
    padding: 50px 0 60px;
  }
  .responsible-hero-content {
    p {
      @media (min-width: 1200px) {
        max-width: 530px;
      }
      @media #{$tablet} {
        font-size: 16px;
      }
    }
    h1 {
      @media #{$tablet} {
        font-size: 36px;
      }
    }
  }
}



.responsible-security {
  ul {
    li {
      display: flex;
      span {
        background-color: $primaryColor;
        min-width: 24px;
        height: 24px;
        border-radius: 50%;
        color: #fff;
        display: inline-block;
        text-align: center;
        line-height: 24px;
        margin-right: 20px;
      }
    }
  }
}

.responsible-join {
  position: relative;
  margin-bottom: -132px;
  @media #{$mobile} {
    margin-bottom: -150px;
  }
  .responsible-join-content {
    background: #ffffff;
    box-shadow: 0px 8px 31px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    padding: 40px;
    @media #{$mobile} {
      padding: 30px 20px;
    }
  }
  .secuity-mail {
    transition: all 0.3s ease-in-out;
    &:hover {
      color: $primaryColor;
    }
  }
  .contact-btn {
    line-height: 64px;
    max-width: 260px;
    width: 100%;
    @media #{$tablet} {
      font-size: 16px;
    }
    @media #{$mobile} {
      max-width: unset;
    }
  }
}

footer.responsible-footer {
  padding-top: 195px;
  @media #{$maxTab} {
    padding-top: 165px;
  }
}
