.random-password-content {
  background: #ffffff;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 30px 40px;
  @media #{$mobile} {
    padding: 20px;
  }
  .random-password {
    @media (min-width: 1200px) {
      position: relative;
      padding-right: 24px;
      &:after {
        content: "";
        border-right: 1px dashed #ededed;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
      }
    }
    .desc {
      margin: 10px 0 20px;
      @media #{$mobile} {
        margin: 10px 0 30px;
      }
      @media (min-width: 1200px) {
        width: 90%;
      }
    }
  }
  .password-info {
    background: #fafafa;
    border-radius: 5px;
    padding: 30px 24px 75px;
    
    @media #{$maxTab} {
      margin-bottom: 20px;
    }
    @media #{$mobile} {
      padding: 15px 10px 55px;
    }
    h2 {
      margin: 80px 0 30px;
      word-break: break-all;
      @media #{$mobile} {
        margin: 50px 0 20px;
      }
    }
    .copy-clipboard {
      border-color: #fd3e72;
      background: #ffffff;
      border: 1px solid #fd3e72;
      border-radius: 5px;
      padding: 15px 30px;
      color: #fd3e72;
      font-weight: bold;
      @media #{$mobile} {
        width: 100%;
      }
      span {
        margin-left: 5px;
      }
    }
  }

  .multi-passwords {
    background: #FFFFFF;
    border: 1px solid #E0DED5;
    border-radius: 5px;
    max-height: 385px;
    overflow: hidden;
    display: none;
    @media #{$maxTab} {
      margin-bottom: 20px;
    }
    .multi-passwords-content {
      max-height: 385px;
      overflow: auto;
    }
    .scrollbar {
      &::-webkit-scrollbar {
        width: 8px;
        height: 8px;
      }
      &::-webkit-scrollbar-button {
        display: none;
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 24px;
        border: 3px solid transparent;
        box-shadow: inset 0 0 8px 8px #e1e1e1;
      }
      &::-webkit-scrollbar-track {
        border: 3px solid transparent;
        box-shadow: inset 0 0 8px 8px #f9f9f8;
      }
    }
    .passowrd {
      display: flex;
      justify-content: space-between;
      align-items: center;
      line-height: 62px;
      padding: 0 30px;
      @media #{$mobile} {
        line-height: 55px;
        padding: 0 18px;
      }
      p {
        font-weight: 500;
        @media #{$mobile} {
          font-size: 14px;
        }
      }
      &:nth-of-type(even) {
        background: #F8F8F8;
      }
      .copy-icon {
        svg {
          width: 20px;
        }
      }
    }
  }

  form {
    h4 {
      @media #{$mobile} {
        font-size: 16px;
      }
    }
  }
}

.checkboxes {
  .regenerate-password {
    font-size: 16px;
  }
  input {
    padding: 0;
    height: initial;
    width: initial;
    margin-bottom: 0;
    display: none;
    cursor: pointer;
  }

  label {
    position: relative;
    cursor: pointer;
    margin-bottom: 16px;
    @media #{$mobile} {
      font-size: 14px;
    }
  }

  label:before {
    content: "";
    -webkit-appearance: none;
    background-color: transparent;
    border: 2px solid #E2E2E2;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
    padding: 6px;
    display: inline-block;
    position: relative;
    vertical-align: middle;
    cursor: pointer;
    margin-right: 10px;
    border-radius: 3px;
    position: relative;
    top: -2px;
  }

  input:checked + label::before {
    border: 2px solid #fd3e72;
  }

  input:checked + label:after {
    content: "";
    display: block;
    position: absolute;
    top: 7px;
    left: 6px;
    width: 4px;
    height: 8px;
    border: solid #fd3e72;
    border-width: 0px 2px 2px 0;
    transform: rotate(45deg);
    font-weight: bold;
    @media #{$mobile} {
      top: 5px;
    }
  }
}

.share-tool {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #E2E2E2;
  padding-top: 16px;
  margin-top: 20px;
  @media #{$maxTab} {
    flex-direction: column;
  }
  .share-on-social {
    li {
      display: inline-block;
      
      &:not(:last-child) {
        margin-right: 25px;
      }
      a {
        font-weight: 500;
        span {
          @media #{$mobile} {
            display: none;
          }
        }
        svg {
          position: relative;
          top: -2px;
        }
        &:hover {
          color: $primaryColor
        }
      }
      
    }
  }
  .offline-version {
    @media #{$maxTab} {
      padding: 20px 0 10px;
      margin-top: 20px;
      border-top: 1px solid #E2E2E2;
    }
    svg {
      position: relative;
      top: -3px;
    }
  }
}

.random-password-content {
  .password-range {
    display: none;
    input,
    output {
      display: inline-block;
      vertical-align: middle;
      font-size: 1em;
      font-family: Arial, sans-serif;
    }

  output {
    background: #fff;
    border-radius: 3px;
    color: #555;
    width: 50px;
    height: 36px;
    text-align: center;
    padding: 4px 0;
    border: 1px solid #ddd;
    font-size: 18px;
    font-weight: 500;
    font-family: "Averta";
    margin-right: 24px;
  }

  input[type="range"] {
    -webkit-appearance: none;
    margin-right: 15px;
    width: 200px;
    height: 7px;
    background: #fafafa;
    box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.1) !important;
    border-radius: 5px;
    background-image: linear-gradient($primaryColor, $primaryColor);
    background-size: 30% 100%;
    background-repeat: no-repeat;
    flex: 1;
    @media #{$tablet} {
      background-size: 26% 100%;
    }
  }
  input.multi-password-input {
    background-size: 57% 100%;
  }

  /* Input Thumb */
  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: $primaryColor;
    cursor: ew-resize;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
    transition: background 0.3s ease-in-out;
  }

  input[type="range"]::-moz-range-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: $primaryColor;
    cursor: ew-resize;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
    transition: background 0.3s ease-in-out;
  }

  input[type="range"]::-ms-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: $primaryColor;
    cursor: ew-resize;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
    transition: background 0.3s ease-in-out;
  }

  input[type="range"]::-webkit-slider-thumb:hover {
    background: $primaryColor;
  }

  input[type="range"]::-moz-range-thumb:hover {
    background: $primaryColor;
  }

  input[type="range"]::-ms-thumb:hover {
    background: $primaryColor;
  }

  /* Input Track */
  input[type="range"]::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }

  input[type="range"]::-moz-range-track {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }

  input[type="range"]::-ms-track {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }
  }
}
