.netflix--hero {
  background: linear-gradient(107.26deg, #fe6556 -22.36%, #fd3e72 60.26%, #49a8ff 150.06%);
  color: #fff;
  overflow: hidden;
  .netflix-hero__content {
    background-image: url(../imgs/netflix/map.png);
    background-repeat: no-repeat;
    background-position: 0px 100px;
    padding: 200px 0 160px;
    background-size: contain;
    @media #{$betweentabdesk} {
      padding: 140px 0 100px;
    }
    @media #{$maxTab} {
      padding: 120px 0 60px;
    }
  }
  &__text {
    position: relative;
    z-index: 1;
    h2 {
      font-size: 47px;
      line-height: 140%;
      @media #{$tablet} {
        font-size: 37px;
        margin-top: 40px;
      }
      @media #{$mobile} {
        font-size: 28px;
        line-height: 140%;
        margin-top: 40px;
      }
    }
    p {
      color: #fff;
      font-size: 20px;
      line-height: 32px;
      margin: 15px 0 40px;
      @media #{$maxTab} {
        margin: 20px 0 30px;
        font-size: 16px;
        line-height: 160%;
      }
      @media #{$tablet} {
        text-align: center;
        margin-top: 15px;
      }
    }
    .netflix-btns {
      a {
        max-width: 230px;
        padding: 20px;
        display: block;
        text-align: center;
        @media #{$maxTab} {
          padding: 18px;
          width: 100%;
          max-width: unset;
        }
      }
    }
  }

  &__image {
    position: absolute;
    right: -170px;
    top: -34px;
    left: 0;
    z-index: 1;
    img {
      max-width: 970px;
    }

    @media #{$betweentabdesk} {
      right: 0;
      top: 0;
      left: -20px;
      img {
        max-width: 800px;
      }
    }
    @media #{$maxTab} {
      position: static;
      img {
        max-width: 540px;
      }
    }
    @media #{$tablet} {
      img {
        max-width: 730px;
      }
    }
  }
}

// Start unblock-netflix
.unblock-netflix {
  padding: 70px 0;
  @media #{$maxTab} {
    padding: 50px 0 0;
  }
  @media #{$tablet} {
    padding: 50px 0 40px;
  }
  h2 {
    font-size: 38px;
    line-height: 140%;
    margin-bottom: 60px;
    @media #{$tablet} {
      font-size: 30px;
      margin-bottom: 40px;
    }
    @media #{$mobile} {
      margin-bottom: 35px;
      font-size: 24px;
      padding: 0 20px;
      line-height: 140%;
    }
  }
  .row {
    > div {
      display: flex;
    }
  }
  .step {
    position: relative;
    padding: 50px 16px 40px;
    background: #ffffff;
    box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    width: 100%;
    @media #{$betweentabdesk} {
      img {
        height: 197px;
      }
    }
    .connect-img {
      filter: drop-shadow(0px 4px 15px rgba(0, 0, 0, 0.12));
      @media #{$tablet} {
        height: 135px;
      }
    }
    @media #{$maxTab} {
      max-width: 370px;
      margin: 0 auto 30px;
      padding: 50px 20px 40px;
    }
    &__title {
      font-size: 18px;
      line-height: 160%;
      text-transform: uppercase;
      margin: 45px 0 15px;
      color: $primaryColor;
      @media #{$tablet} {
        font-size: 16px;
      }
      @media #{$maxTab} {
        margin: 30px 0 10px;
      }
    }
    &__text {
      font-size: 16px;
      line-height: 160%;
      opacity: 0.8;
      @media #{$maxTab} {
        font-size: 14px;
      }
    }
  }
}
// End unblock-netflix

// Start Choose Wevpn
.choose-wevpn {
  background-color: #121019;
  overflow: hidden;
  .choose-wevpn-content {
    background-image: url(../imgs/netflix/choose-wevpn.png);
    padding: 135px 0;
    background-size: cover;
    max-width: 1600px;
    background-repeat: no-repeat;
    margin: auto;
    position: relative;
    @media #{$betweentabdesk} {
      padding: 100px 0;
      background-size: 135%;
      background-position-x: -70px;
    }
    @media #{$tablet} {
      padding: 62px 0;
      background-size: 143%;
      background-position-x: -60px;
    }
    @media #{$mobile} {
      background-image: url(../imgs/netflix/choose-wevpn-m.png);
      padding: 460px 0 60px;
    }
    &:after {
      content: "";
      background-color: #121019;
      // background: linear-gradient(90deg, rgba(11,8,16,0.9) 0%, rgba(6,4,8,1) 50%, rgba(0,0,0,1) 100%);
      top: 0;
      left: 55%;
      position: absolute;
      width: 100%;
      bottom: 0;
      z-index: 0;
      @media #{$tablet} {
        background: linear-gradient(90deg, rgba(11,8,16,0.9) 0%, rgba(6,4,8,1) 50%, rgba(0,0,0,1) 100%);
      }
      @media #{$mobile} {
        top: 60%;
        left: 0;
        background: linear-gradient(180deg, rgba(11,8,16,0.8) 0%, rgba(6,4,8,1) 50%, rgba(0,0,0,1) 100%);
      }
    }
  }
  .text {
    color: #fff;
    position: relative;
    z-index: 2;
    h2 {
      font-size: 36px;
      line-height: 140%;
      margin-bottom: 30px;
      @media #{$betweentabdesk} {
        font-size: 33px;
      }
      @media #{$tablet} {
        font-size: 24px;
      }
      @media #{$mobile} {
        font-size: 24px;
        margin-bottom: 15px;
      }
    }
    p {
      font-size: 20px;
      line-height: 160%;
      @media #{$betweentabdesk} {
        font-size: 18px;
      }
      @media #{$tablet} {
        font-size: 16px;
      }
      @media #{$mobile} {
        font-size: 14px;
      }
    }
  }
}
// End Choose Wevpn

// Start netflix-features
.netflix-features {
  background: linear-gradient(107.36deg, #fe6556 -6.9%, #fd3e72 49.39%, #49a8ff 113.19%);
  padding: 65px 0 95px;
  @media #{$maxTab} {
    padding: 50px 0 30px;
  }
  h2 {
    font-size: 38px;
    line-height: 47px;
    color: #ffffff;
    margin-bottom: 60px;
    @media #{$maxTab} {
      font-size: 24px;
      line-height: 30px;
      margin-bottom: 40px;
      padding: 0 20px;
    }
  }
  .row {
    > div {
      display: flex;
    }
  }
  .step {
    background: #ffffff;
    box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 50px 16px 35px;
    @media #{$tablet} {
      padding: 30px 16px 35px;
    }
    @media #{$maxTab} {
      max-width: 370px;
      margin: 0 auto 30px;
    }

    .icon {
      width: 280px;
      height: 265px;
      display: flex;
      margin: 0 auto 40px;
      @media #{$betweentabdesk} {
        width: auto;
        height: 215px;
        margin: 0 auto 20px;
        padding: 0 20px;
      }
      @media #{$tablet} {
        width: auto;
        height: 180px;
        margin: 0 auto 10px;
      }
      @media #{$mobile} {
        width: auto;
        height: auto;
        margin: 0;
      }
      img {
        margin: auto;
      }
    }
    &__title {
      font-size: 24px;
      line-height: 32px;
      @media #{$maxTab} {
        margin-top: 20px;
        font-size: 18px;
      }
    }
    &__text {
      font-size: 16px;
      line-height: 140%;
      margin-top: 16px;
      @media #{$maxTab} {
        margin-top: 12px;
        font-size: 14px;
      }
    }
  }
}
// End netflix-features
