.app-hero-img {
  padding: 140px 0 180px;
  @media #{$maxTab} {
    padding: 50px 0 240px;
  }
  &.edge-app {
    @media #{$betweentabdesk} {
      .title {
        font-size: 36px;
      }
    }
  }
  .title {
    @media #{$maxTab} {
      text-align: center;
    }
  }
  .text {
    margin: 30px 0;
    font-size: 20px;
  }
  .txt {
    margin: 10px 0 30px;
    font-size: 24px;
    line-height: 160%;
    a {
      text-decoration-line: underline !important;
      color: #FFFFFF;
    }
    @media #{$betweentabdesk} {
      font-size: 20px;
    }
    @media #{$maxTab} {
      text-align: center;
    }
    @media #{$mobile} {
      font-size: 18px;
    }
  }
  .hero-img-list {
    @media #{$maxTab} {
      width: fit-content;
      margin: auto;
    }
    li {
      font-size: 18px;
      line-height: 160%;
      display: flex;
      align-items: center;
      @media (max-width: 1199px) {
        font-size: 16px;
      }
      @media #{$mobile} {
        font-size: 14px;
      }
      &:not(:last-child) {
        margin-bottom: 15px;
      }
      svg {
        margin-right: 12px;
        min-width: 36px;
        margin-left: unset;
        @media #{$mobile} {
          margin-right: 8px;
          width: 26px;
          min-width: 26px;
          height: 26px;
        }
      }
    }
  }
  .hero-button {
    display: inline-block;
    text-align: center;
    @media #{$maxTab} {
      display: block;
    }
  }
  .version {
    font-size: 14px;
    opacity: 0.8;
  }
  .hero-img-btn {
    line-height: 64px;
    padding: 0;
    display: block;
    width: 280px;
    text-align: center;
    margin: 50px 0 10px;
    
    @media #{$betweentabdesk} {
      margin: 30px 0 10px;
    }
    @media #{$maxTab} {
      width: 100%;
      margin: 40px 0 10px;
    }
  }

  .app-btn {
    background-color: #000;
    border-radius: 5px;
    transition: all 0.2s ease-in-out;
    display: block;
    &:hover {
      opacity: 0.8;
    }
  }
  .hero-img-image {
    &.windows {
      position: absolute;
      right: -100px;
      top: 50%;
      transform: translateY(-50%);
      @media #{$maxTab} {
        position: static;
        transform: translateY(0);
      }
    }
    @media #{$maxTab} {
      max-width: 95%;
      margin: 0 auto 34px;
    }
  }
}

.wevpn-platforms.wevpn-apps {
  overflow: hidden;
  margin-top: -118px;
  min-height: 215px;
  @media #{$maxTab} {
    margin-top: -190px;
  }
  .platform-content {
    .platform-text {
      color: #fff;
      font-weight: 500;
      font-size: 24px;
      line-height: 20px;
      @media #{$tablet} {
        font-size: 20px;
      }
      @media #{$mobile} {
        font-size: 16px;
      }
    }
    .platform-list {
      align-items: center;
      padding: 30px 0;
      @media #{$mobile} {
        display: block;
        overflow: hidden;
        padding: 20px 0;
      }
      .item {
        height: 136px;
        background: #FFFFFF;
        transition: all 0.3s ease-in-out;
        @media #{$maxTab} {
          border-right: 1px solid #ebe8df;
          border-bottom: 1px solid #ebe8df;
          height: 70px;
        }
        @media #{$mobile} {
          height: 50px;
        }
        &.active,
        &:hover {
          background: #f7f6f0;
          a {
            span {
              opacity: 1;
              color: $primaryColor;
            }
            svg {
              path {
                fill: $primaryColor;
                &.amazon-letter {
                  fill: #fff;
                }
              }
            }
            .firefox {
              .line {
                stroke: $primaryColor;
                fill: none;
              }
              .fox {
                fill: $primaryColor;
              }
            }
          }
        }
        a {
          transition: all 0.3s ease-in-out;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          padding: 0 10px;
          text-align: center;
          svg {
            margin: unset;
          }
          @media #{$maxTab} {
            text-align: left;
            flex-direction: row;
            justify-content: flex-start;
          }
          @media #{$tablet} {
            padding: 20px;
            width: 100%;
            svg {
              width: 32px;
              margin-right: 10px !important;
              @media #{$tablet} {
                width: 26px;
              }
            }
          }
          &.apple-icon {
            @media #{$tablet} {
              svg {
                position: relative;
                top: -4px;
              }
            }
          }
        }
        svg {
          @media #{$mobile} {
            max-width: 16px;
            height: 16px;
            margin-right: 8px !important;
          }
          path {
            transition: all 0.3s ease-in-out;
          }
          & + span {
            font-size: 14px;
            line-height: 15px;
            color: rgba(#5b5863, 0.7);
            transition: all 0.3s ease-in-out;
            @media (min-width: 1024px) {
              margin-top: 15px;
            }
          }
        }
      }
      .item.soon {
        position: relative;
        cursor: pointer;
        &:before,
        &:after {
          position: absolute;
          visibility: hidden;
          opacity: 0;
          pointer-events: none;
          transition: transform 0.1s cubic-bezier(0.5, 1, 0.25, 1);
          z-index: 1;
        }
        &:before {
          padding: 5px;
          width: 110px;
          border-radius: 3px;
          background-color: $primaryColor;
          border-radius: 5px;
          box-shadow: 0px 6px 11px rgba(79, 79, 79, 0.3);
          border-radius: 5px;
          color: #fff;
          content: attr(data-tooltip);
          text-align: center;
          font-size: 12px;
          font-weight: 500;
        }
        &:after {
          border: 8px solid transparent;
          width: 0;
          content: "";
          font-size: 0;
          line-height: 0;
        }
        &:hover:before,
        &:hover:after {
          visibility: visible;
          opacity: 1;
        }
        &.t-top {
          &:before {
            bottom: 82%;
            left: 50%;
            margin-bottom: 5px;
            transform: translateX(-50%);
          }
          &:after {
            bottom: 82%;
            left: 50%;
            transform: translateX(-50%);
            border-top: 8px solid $primaryColor;
            border-bottom: none;
          }

          &:hover:before,
          &:hover:after {
            transform: translateX(-50%) translateY(-5px);
          }
        }
      }
    }
    .owl-carousel.platform-list {
      &.owl-drag .owl-item {
        @media (min-width: 1000px) {
          opacity: 0;
          transition: opacity 200ms;
        }
      }
      .owl-stage {
        @media #{$maxTab} {
          border: 1px solid #ebe8df;
          border-radius: 10px;
          overflow: hidden;
        }
      }
      .owl-stage-outer {
        background: #F9F8F4;
        box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.06);
        border-radius: 10px;
        overflow: visible;
      }
      @media #{$maxTab} {
        .owl-item.cloned {
          display: none !important;
        }
        .owl-stage {
          width: auto !important;
        }
        .owl-item {
          width: 33.33% !important;
        }
      }

      @media #{$tablet} {
        .owl-item:nth-child(19),.owl-item:nth-child(20) {
          width: 50% !important;
        }
      }
      @media #{$mobile} {
        .owl-item {
          width: 50% !important;
          overflow: visible;
        }
      }
      .owl-item {
        &.active {
          border-right: 1px solid #ebe8df;
          opacity: 1;
          @media #{$maxTab} {
            border-right: 0;
          }
        }
        &:first-child {
          .item {
            border-radius: 10px 0 0 10px;
            @media #{$maxTab} {
              border-radius: 0;
            }
          }
        }
        @media #{$mobile} {
          &:nth-of-type(even) {
            .item {
              border-right: 0;
            }
          }
          &:nth-of-type(19),
          &:nth-of-type(20) {
            .item {
              border-bottom: 0;
            }
          }
        }
        @media #{$tablet} {
          &:nth-of-type(3),
          &:nth-of-type(6),
          &:nth-of-type(9),
          &:nth-of-type(12),
          &:nth-of-type(15),
          &:nth-of-type(18),
          &:nth-of-type(20) {
            .item {
              border-right: 0;
            }
          }
          &:nth-of-type(19),
          &:nth-of-type(20) {
            .item {
              border-bottom: 0;
            }
          }
        }
      }
      .owl-item.active.last {
        border-right: 0;
        .item {
          border-radius: 0 10px 10px 0;
          @media #{$maxTab} {
            border-radius: 0;
          }
        }
      }
      .owl-item:not(.active) + .owl-item.active {
        .item {
          border-radius: 10px 0 0 10px;
          @media #{$maxTab} {
            border-radius: 0;
          }
        }
      }
      .owl-nav {
        position: relative;
        margin-top: 0;
        .arrows {
          width: 14px;
          height: 14px;
          position: relative;
          margin: auto;
        }
        .prev,
        .next {
          border-bottom: 1.5px solid;
          border-left: 1.5px solid;
          transform: rotate(45deg);
          left: 4px;
        }
        .next {
          transform: rotate(-135deg);
          left: -4px;
        }
        .owl-prev,
        .owl-next {
          top: -104px;
          color: #000;
          position: absolute;
          background: #f0eee9;
          border-radius: 130px;
          width: 72px;
          height: 72px;
          margin: 0;
          &.disabled {
            cursor: not-allowed;
            background: lighten($color: #f0eee9, $amount: 2);
            opacity: 1;
          }
          @media (min-width: 1000px) and (max-width: 1366px) {
            width: 64px;
            height: 64px;
          }
        }
        .owl-prev {
          left: -87px;
          @media (min-width: 1000px) and (max-width: 1366px) {
            left: -32px;
            border-radius: 50%;
          }
        }
        .owl-next {
          right: -87px;
          @media (min-width: 1000px) and (max-width: 1366px) {
            right: -32px;
            border-radius: 50%;
          }
        }
      }
    }
  }
}

.devices-os-new {
  padding: 110px 0;
  &.lazyloaded {
    @media (min-width: 1024px) {
      background: center / cover no-repeat url(../imgs/wevpn_mac/mac-devices-bg.svg);
    }
  }

  @media #{$maxTab} {
    padding-top: 65px;
    padding-bottom: 60px;
    &.iphone-page-devices {
      .devices-content {
        .devices-title {
          margin: 20px auto;
          width: 100%;
        }
      }
    }
  }

  @media #{$betweentabdesk} {
    padding: 80px 0;
  }

  .devices-img {
    img {
      max-width: 100%;
    }
  }

  .devices-content {
    .devices-title {
      margin-bottom: 35px;
      @media #{$maxTab} {
        margin: 40px auto 20px;
        text-align: center;
        padding: 0 20px;
      }
      @media (min-width: 1024px) {
        width: 90%;
      }
    }
    .devices-desc {
      font-size: 18px;
      line-height: 28px;
      margin: 26px 0 40px;
      width: 80%;
      @media #{$mobile} {
        width: 100%;
        margin: 20px 0 30px;
      }
    }

    .devices-btn {
      padding: 21px 43px;
      @media #{$mobile} {
        display: block;
      }
    }

    .compatability {
      display: flex;
      @media #{$tablet} {
        justify-content: space-between;
        width: 70%;
        margin: auto;
      }
      @media #{$mobile} {
        flex-direction: column;
      }
      .windows-devices,
      .windows-versions {
        flex-basis: 50%;
        @media #{$tablet} {
          flex-basis: auto;
        }
        @media #{$mobile} {
          margin-bottom: 30px;
        }
        h3 {
          margin-bottom: 20px;
          font-weight: 500;
          @media #{$mobile} {
            margin-bottom: 15px;
            font-size: 20px;
          }
        }
        ul {
          @media #{$mobile} {
            display: flex;
            flex-wrap: wrap;
          }
          li {
            font-size: 18px;
            line-height: 160%;
            padding-left: 20px;
            position: relative;
            &::before {
              content: "";
              background-color: #fff;
              width: 9px;
              height: 9px;
              border-radius: 50%;
              position: absolute;
              left: 0;
              top: 8px;
            }
            &:not(:last-child) {
              margin-bottom: 10px;
            }
            @media #{$mobile} {
              font-size: 16px;
              line-height: 26px;
              flex: 1 1 100%;
            }
          }
        }
      }
      .windows-devices {
        @media #{$mobile} {
          ul {
            li {
              flex: 1 1 50%;
            }
          }
        }
      }
    }
  }
  &.chrome-os {
    .devices-content {
      @media (min-width: 1200px) {
        padding-left: 40px;
      }
    }
    .devices-content .compatability {
      display: block;
      .devices-support {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 30px;
        li {
          width: 50%;
        }
      }
    }
  }
}