.hero-img.ios-tuts-page {
  background-color: $primaryColor;
  padding: 140px 0 70px;
  @media #{$maxTab} {
    padding: 95px 0 40px;
  }
  .hero-img-content__title {
    font-size: 48px;
    line-height: 48px;
    color: #ffffff;
    width: 90%;
    margin-bottom: 40px;
    @media #{$betweentabdesk} {
      width: 100%;
      font-size: 44px;
    }
    @media #{$tablet} {
      font-size: 42px;
      margin: 40px auto;
    }
    @media #{$mobile} {
      font-size: 32px;
      line-height: 40px;
      width: 100%;
      margin-top: 20px;
      margin-bottom: 30px;
    }
  }
  .hero-img-txt {
    font-size: 18px;
    line-height: 26px;
    color: #fff;
    width: 80%;

    @media #{$betweentabdesk} {
      width: 85%;
    }

    @media #{$tablet} {
      margin: auto;
    }
    @media #{$mobile} {
      width: 100%;
      font-size: 16px;
      line-height: 20px;
    }
  }

  .hero-img-content .hero-image-btns {
    margin-top: 45px;
    @media #{$tablet} {
      margin-top: 40px;

      a {
        margin-top: 0;
        width: 100%;
        &.apple-store-btn {
          background-color: #000;
          border-radius: 5px;
        }
      }
    }
    @media #{$mobile} {
      margin-top: 30px;

      a {
        margin-top: 0;
        width: 100%;
      }
    }
  }
  .hero-img-image {
    position: absolute;
    top: -60px;
    @media #{$betweentabdesk} {
      top: -10px;
    }
    @media #{$maxTab} {
      position: static;
    }
  }
}

.ios-tutorials {
  padding-top: 200px;
  .steps {
    position: relative;
  }
  @media #{$betweentabdesk} {
    padding-top: 120px;
  }
  @media #{$mobile} {
    padding-top: 50px;
  }
  @media #{$tablet} {
    padding-top: 70px;
  }
  .step {
    padding-bottom: 50px;
    @media #{$maxTab} {
      padding-bottom: 20px;
    }
    &:last-child {
      padding-bottom: 100px;
      @media #{$maxTab} {
        padding-bottom: 50px;
      }
    }
    &__num {
      font-size: 18px;
      line-height: 20px;
      letter-spacing: 4px;
      color: $primaryColor;
      font-weight: 500;
      @media #{$mobile} {
        font-size: 16px;
      }
    }
    &__title {
      margin: 8px 0 30px;
      width: 80%;
      @media #{$mobile} {
        margin: 5px 0 20px;
        width: 100%;
        font-size: 20px;
      }
      @media #{$tablet} {
        width: 75%;
      }
      @media #{$betweentabdesk} {
        width: 85%;
      }
    }
    &__steps {
      li {
        font-size: 18px;
        line-height: 32px;
        margin-bottom: 40px;
        @media #{$mobile} {
          font-size: 16px;
          line-height: 30px;
        }
        .text {
          display: flex;
          @media #{$mobile} {
            margin-bottom: 10px;
          }
          .num {
            width: 20px;
            height: 20px;
            border-radius: 50%;
            font-size: 12px;
            text-align: center;
            color: #ffffff;
            background-color: #f2265e;
            display: inline-block;
            line-height: 20px;
            position: relative;
            top: 6px;
            margin-right: 5px;
          }

          p {
            width: 85%;
            .menu-icon {
              margin: 0 5px;
            }
            @media #{$mobile} {
              width: 100%;
            }
          }
          .app-link {
            text-decoration-line: underline !important;
            color: #017bff;
          }
          .highlight {
            text-transform: uppercase;
            color: #3478f6;
            background: #eeeeef;
            border-radius: 70px;
            font-weight: 500;
            padding: 4px 16px;
            font-size: 16px;
          }
        }
        .imgs {
          display: flex;
          align-items: flex-end;
          @media #{$mobile} {
            justify-content: center;
          }
          img:first-child {
            position: relative;
            top: -20px;
            @media #{$mobile} {
              top: 0;
              width: 80%;
              margin: auto;
            }
          }
        }
      }
    }
  }
  .ios-need-help {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #ffffff;
    border: 1px solid #ebe8df;
    box-shadow: 0px 10px 59px rgba(240, 238, 233, 0.8), 0px 6px 9px #e6e4df;
    border-radius: 10px;
    padding: 34px 40px;
    @media #{$betweentabdesk} {
      padding: 30px;
    }
    @media #{$mobile} {
      flex-direction: column;
      text-align: center;
      align-items: stretch;
      padding: 30px;
    }
    .text {
      h3 {
        font-size: 24px;
        line-height: 36px;
        font-weight: 500;
        @media (max-width: 1199px) {
          font-size: 22px;
          line-height: 30px;
        }
      }
      p {
        font-size: 18px;
        line-height: 32px;
        @media (max-width: 1199px) {
          font-size: 16px;
          line-height: 20px;
        }
        @media #{$mobile} {
          width: 90%;
          margin: 10px auto 20px;
        }
      }
    }
    .support-btn {
      a {
        font-size: 16px;
        padding: 20px 40px;
        @media #{$betweentabdesk} {
          padding: 20px;
        }
        @media #{$mobile} {
          display: block;
          width: 100%;
        }
      }
    }
  }
  .line-break {
    margin: 60px 0 0;
    border-color: #dedede;
  }
}
