.benefits-hero {
  padding: 150px 0 600px;
  @media #{$maxTab} {
    padding: 40px 0 600px;
  }
}

.vpn-benefits {
  .vpn-benefits-content {
    background: #FFFFFF;
    box-shadow: 0px 4px 59px rgba(0, 0, 0, 0.1);
    border-radius: 30px 30px 0 0;
    margin-top: -545px;
    @media #{$maxTab} {
      margin-top: -565px;
      border-radius: 20px 20px 0 0;
    }
  }
}

.more-benefits.benefits-page {
  padding-top: 154px;
  padding-bottom: 154px;
}

/* Start Need Help */
.need-help {
  padding-top: 134px;
  padding-bottom: 134px;

  @media #{$mobile} {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @media #{$tablet} {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @media #{$betweentabdesk} {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  .need-help-content {
    .need-help-title {
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 35px;

      @media #{$mobile} {
        font-size: 24px;
        line-height: 28px;
        margin-bottom: 24px;
        margin-top: 40px;
        padding-top: 10px;
      }

      @media #{$tablet} {
        font-size: 30px;
        line-height: 28px;
        margin-bottom: 30px;
      }
    }

    .need-help-list {
      margin-bottom: 30px;

      li {
        font-size: 18px;
        line-height: 32px;
        margin-bottom: 15px;

        @media #{$mobile} {
          font-size: 14px;
          line-height: 14px;
        }

        @media #{$tablet} {
          font-size: 16px;
          line-height: 20px;
        }

        svg {
          margin-right: 5px;
          position: relative;
          top: -2px;
        }
      }
    }

    .need-help-btn {
      padding: 21px 40px;

      @media #{$mobile} {
        padding: 16px 32px;
        font-size: 16px;
      }

      @media #{$tablet} {
        padding: 20px 30px;
        font-size: 18px;
      }
    }

    .help-center {
      margin-left: 30px;

      @media #{$mobile} {
        font-size: 14px;
        margin-left: 15px;
      }
    }
  }

  .need-help-img {
    img {
      max-width: 100%;
    }
  }
}

/* End Need Help */
