header.affiliate-first-step {
  padding-bottom: 360px;
  @media #{$mobile} {
    padding-bottom: 565px;
  }
}

.join-affiliates {
  margin-top: -240px;
  padding-bottom: 90px;
  @media #{$tablet} {
    padding-bottom: 60px;
  }
  @media #{$mobile} {
    margin-top: -475px;
    padding-bottom: 50px;
  }
}

.join-affiliate-content {
  position: relative;
  background: #FFFFFF;
  box-shadow: 0px 4px 41px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding-top: 85px;
  @media #{$tablet} {
    padding-top: 60px;
  }
  @media #{$mobile} {
    padding: 55px 20px 0;
  }
  .logos {
    max-width: 680px;
    margin: 0 auto 40px;
    @media #{$tablet} {
      margin: 0 auto 30px;
    }
    img {
      @media #{$maxTab} {
        height: 60px;
      }
      @media #{$mobile} {
        &:first-child {
          margin-bottom: 30px;
        }
      }
    }
  }
  h3 {
    font-size: 26px;
    line-height: 140%;
    @media #{$tablet} {
      font-size: 24px;
    }
    @media #{$mobile} {
      font-size: 20px;
    }
  }
  .join-affiliate-btns {
    margin: 40px 0 60px;
    @media #{$tablet} {
      margin: 30px auto 50px;
    }
    @media #{$mobile} {
      margin: 40px 0;
    }
    a {
      margin: 0 20px;
      font-size: 20px;
      line-height: 64px;
      max-width: 308px;
      transition: all 0.3s ease-in-out;
      @media #{$tablet} {
        margin: 0 15px;
        max-width: 280px;
        font-size: 18px;
        line-height: 60px;
      }
      @media #{$mobile} {
        max-width: unset;
        margin: 0;
        line-height: 58px;
        font-size: 18px;
        &:first-child {
          margin-bottom: 20px;
        }
      }
    }
    a.join-junction {
      background: #1C9958;
      border-radius: 5px;
      &:hover {
        background-color: darken($color: #1C9958, $amount: 5);
      }
    }
  }
}