/* Start Header */
.top-header {
  padding: 8px 0;
  background-color: #fff;
  border-bottom: 1px solid #ebeae6;
  position: relative;
  z-index: 10;
  &.dark {
    border: 0;
    background-color: rgba(26, 40, 48, 0.95);
    .top-header-content ul li span {
      &#location,
      &#ip,
      &#isp {
        color: #fff;
      }
    }
  }
  ul {
    li {
      font-size: 13px;
      line-height: 16px;
      color: #a29cb0;
      @media #{$mobile} {
        font-size: 12px;
      }
      .bull {
        color: #a29cb0;
        margin: 0 10px;
        @media #{$mobile} {
          margin: 0 4px;
        }
      }
    }
  }
  a {
    text-decoration-line: underline !important;
    color: $primaryColor;
    font-weight: 500;
    &:hover {
      text-decoration: none !important;
      svg {
        transform: translateX(6px);
      }
    }
    svg {
      transform: translateX(4px);
      transition: all 0.2s ease;
    }
  }
}

.h-m-w {
  @media #{$maxTab} {
    height: 63px;
  }
}

header {
  &.header-white {
    .logo-colored {
      display: none;
    }
    .logo-white {
      display: inline-block;
    }
    .navbar-nav .nav-link {
      color: #fff;
      @media #{$maxTab} {
        color: #5b5863;
      }
    }
    .navbar .dropdown {
      #languageButton {
        color: #fff;
      }
    }
    .navbar-brand {
      @media #{$maxTab} {
        .logo-colored {
          display: inline-block;
        }
        .logo-white {
          display: none;
        }
      }
    }
    .navbar {
      @media #{$maxTab} {
        background-color: #fff;
      }
      .login {
        .login-item {
          .login-link {
            color: #fff;
            &:hover {
              opacity: 0.8;
              color: #fff;
            }
            @media #{$maxTab} {
              color: #5b5863;
              font-size: 18px;
              padding-left: 0;
              display: block;
            }
          }

          .get-started {
            @media (min-width: 992px) {
              background-color: #fff;
              color: $primaryColor;
              &:hover {
                background: #fff;
              }
            }
          }
        }
      }
    }
    #nav-icon {
      background-color: #fff;
    }
    .navbar-light .navbar-nav .nav-link:hover {
      color: #fff !important;
      opacity: 0.8;
      @media #{$maxTab} {
        color: #5b5863 !important;
        opacity: 1;
      }
    }
  }
  .navbar {
    padding: 13px 0;
    position: absolute;
    width: 100%;
    z-index: 5;
    display: block;
    border-bottom: 1px solid transparent;
    .container {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      width: 100%;
    }

    &.nav-fixed {
      position: fixed;
      top: 0;
      background-color: #fff;
      border-bottom: 1px solid #ebe8df;
      animation: q 1s cubic-bezier(0.23, 1, 0.32, 1) both;

      @keyframes q {
        0% {
          transform: translateY(-100%);
        }
        100% {
          transform: translateY(0);
        }
      }
      .login .login-item .login-link,
      .navbar-nav .nav-link,
      .lang-button {
        color: #5b5863 !important;
        &:hover {
          color: $primaryColor !important;
          opacity: 1 !important;
        }
      }
      .login .login-item .get-started {
        background-color: $primaryColor !important;
        color: #fff !important;
        &:hover {
          background-color: $linkHoverColor !important;
        }
      }

      .navbar-brand img.logo-white {
        display: none !important;
      }
      .navbar-brand img.logo-colored {
        display: block !important;
      }

      .get-started-login.color-bg-pages {
        p a:first-child {
          color: #5b5863;
          @media #{$maxTab} {
            padding: 12px 14px 12px 0;
            display: inline-block;
          }
        }
      }

      #nav-icon span {
        background-color: $primaryColor;
      }

      .navbar-nav .active > .nav-link {
        color: $primaryColor !important;
      }

      .container:after {
        display: none;
      }
    }

    .get-started-login.color-bg-pages {
      p {
        a:first-child {
          color: #fff;
          @media #{$maxTab} {
            color: #5b5863;
          }
          &:hover {
            color: #fff;
            opacity: 0.8;
          }
        }
      }
    }

    .get-started-login {
      display: none;
      p {
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 17px;
        color: #5b5863;
        a:first-child {
          transition: all 0.2s ease-in-out;
          font-weight: 500;
          margin: 0 15px;
          @media #{$maxTab} {
            font-size: 18px;
            padding: 10px 0px;
            margin: 0;
          }
          &:hover {
            color: $primaryColor;
          }
        }
        .my-account {
          background: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 5px;
          padding: 10px;
          color: #fd3e72;
          font-weight: 500;
          transition: all 0.2s ease-in-out;
          @media #{$maxTab} {
            position: absolute;
            bottom: 0;
            left: 15px;
            right: 15px;
            z-index: 2;
            box-shadow: 0 -1px 0 rgba(34, 46, 58, 0.1);
            padding: 13.5px;
            background-color: #fd3e72;
            color: #fff;
            svg {
              path {
                fill: #fff;
              }
            }
          }

          svg {
            margin-right: 5px;

            path {
              transition: all 0.3s ease-in-out;
            }
          }
          &:hover {
            background-color: #fd3e72;
            color: #fff;
            border-color: #fd3e72;
            svg {
              path {
                fill: #fff;
              }
            }
          }
        }
      }
    }

    .nav-item {
      &.dropdown {
        > a:hover {
          cursor: text;
        }
      }
      li {
        .badge {
          text-transform: uppercase;
          font-size: 10px;
          padding: 0 7px;
          border-radius: 9px 9px 9px 0px;
          line-height: 16px;
          margin-left: 5px;
          color: #fff;
          &-danger {
            background-color: $primaryColor;
          }
        }
      }
      @media (min-width: 992px) {
        &.dropdown {
          &:hover {
            .dropdown--menu {
              opacity: 1;
              visibility: visible;
              transform: translateY(0);
            }
          }

          .dropdown--menu {
            position: absolute;
            border-radius: 5px;
            opacity: 0;
            visibility: hidden;
            transition: opacity, transform 0.7s cubic-bezier(0.4, 0, 0.2, 1);
            background: #ffffff;
            box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.3);
            transform: translateY(10px);
            z-index: 9999;
            display: flex;
            left: -158px;
            width: 985px;

            &:before {
              border-left: 12px solid transparent;
              border-right: 12px solid transparent;
              border-bottom: 12px solid #fff;
              top: -8px;
              left: 175px;
              content: "";
              position: absolute;
            }

            .vpn-apps {
              width: 60%;
              border-right: 1px solid #e8e8e8;
              margin: 20px 0;
              padding-right: 20px;
              ul {
                float: left;
                width: 25%;
              }
            }
            .smartdns-apps {
              border-right: 1px solid #e8e8e8;
            }
            .smartdns-apps,
            .tools-pages {
              margin: 20px 0;
              width: 20%;
              padding-right: 20px;
            }

            ul {
              li {
                a {
                  padding: 5px 0;
                  transition: color 0.2s ease-in;
                  font-weight: 500;
                  font-size: 12px;
                  display: flex;
                  align-items: center;

                  &:hover {
                    color: $primaryColor;

                    svg {
                      path {
                        fill: $primaryColor;
                        &.line {
                          stroke: #fd3e72;
                          fill: none;
                        }
                      }
                      .dns-path {
                        fill: #fff;
                      }
                      .dns-rect,
                      .weblock-circle {
                        fill: $primaryColor;
                      }
                    }
                  }

                  svg {
                    margin-right: 8px;
                    margin-left: unset;
                    path,
                    rect,
                    circle {
                      transition: all 0.2s ease-in;
                    }
                  }
                }
              }
            }

            &.first {
              h6 {
                padding: 0 20px;
                @media #{$mobile} {
                  margin-bottom: 20px;
                }
              }
              ul {
                padding: 10px 0 0 20px;
                position: relative;
                li {
                  margin-bottom: 5px;
                  &.sec-title {
                    color: #8d8b92;
                    border-bottom: 1px solid #f2f2f2;
                    margin-bottom: 10px;
                    display: flex;
                    align-items: center;
                    min-height: 45px;
                    font-size: 12px;
                  }
                }
              }
            }

            .tools-pages {
              ul {
                li:first-child {
                  border-top: 1px solid #f2f2f2;
                  padding-top: 10px;
                  margin-top: 10px;
                }
              }
            }

            &.second {
              width: 520px;
              left: 0;
              &::before {
                left: 45px;
              }
              > div {
                width: 50%;
                padding: 20px;
                h6 {
                  border-bottom: 1px solid #e8e8e8;
                  padding-bottom: 5px;
                }
              }
              .benefits-col {
                border-right: 1px solid #e8e8e8;
              }
              ul {
                padding-top: 15px;
                li {
                  &:not(:last-child) {
                    margin-bottom: 5px;
                  }
                }
              }
            }

            &.third {
              width: 160px;
              left: 0;

              &:before {
                left: 24px;
              }

              ul {
                padding: 20px;
                li {
                  &:not(:last-child) {
                    margin-bottom: 5px;
                  }
                }
              }
            }
          }
        }
      }

      @media #{$maxTab} {
        &.dropdown {
          &.active {
            .dropdown--menu {
              display: block !important;
            }

            .dropdown-toggle {
              &:after {
                transform: rotate(180deg);
              }
            }
          }

          .dropdown--menu {
            padding: 0 0 0 20px;
            display: none;
            transition: all 0.2s ease-in-out;
            border-left: 1px solid #dfddd6;
            margin-top: 10px;

            h6 {
              font-size: 16px;
            }

            ul {
              margin-bottom: 10px;

              li {
                line-height: 20px;
                margin-bottom: 8px;
                a {
                  font-size: 14px;
                  padding: 10px 0;
                  display: flex;
                  align-items: center;
                  transition: color 0.2s ease-in;
                  color: #5b5863;
                  font-weight: 500;
                  svg {
                    margin-right: 8px;
                    margin-left: unset;
                  }
                }

                &.sec-title {
                  font-size: 12px;
                  opacity: 0.6;
                }
              }
            }
          }
        }
      }
    }

    @media #{$maxTab} {
      .navbar-collapse {
        border-top: 1px solid #ebe8df;
        margin: 20px 0 80px;
        max-height: 370px;
        overflow: auto;
      }
      .lang-collapse {
        border-top: 1px solid #ebe8df;
        margin: 20px 0 0;
        flex-basis: 100%;
        .dropdown--menu {
          &::before {
            display: none;
          }
          .dropdown-item {
            padding: 15px 0;
          }
        }
      }
    }

    .navbar-brand {
      margin-right: 33px;
      padding: 0 0 3px 0px;
      @media #{$betweentabdesk} {
        margin-right: 20px;
      }

      @media #{$maxTab} {
        padding: 0;

        img {
          height: 35px;
          width: auto;
        }
      }
    }

    .navbar-nav {
      @media #{$maxTab} {
        padding-top: 15px;
        .nav-item {
          margin-bottom: 8px;
        }
      }
    }
    .lang-dropdown:hover {
      @media (min-width: 992px) {
        ul {
          visibility: visible;
          opacity: 1;
          transform: translateY(0);
        }
      }
    }

    #languageButton {
      all: unset;
      display: flex;
      align-items: center;
      font-weight: 500;
      color: #5b5863;
      font-size: 14px;
      padding: 8px 5px;
      cursor: pointer;
      &:hover {
        opacity: 0.8;
      }

      @media #{$betweentabdesk} {
        font-size: 13px;
      }
      .icon {
        margin-right: 8px;
      }
    }
    .dropdown-item {
      font-weight: 500;
      color: #5b5863;
      display: flex;
      align-items: center;
      padding: 0.5rem 1.5rem;
      font-size: 14px;
      @media #{$betweentabdesk} {
        font-size: 13px;
      }
      span:last-child {
        margin-left: 10px;
        transition: all 0.2s ease;
      }
      &:hover {
        span:last-child {
          color: $primaryColor;
        }
      }
    }

    .dropdown--menu {
      padding: 0.5rem 0;
      @media (min-width: 992px) {
        background: #ffffff;
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        top: 100%;
        min-width: 170px;
        position: absolute;
        z-index: 1000;
        opacity: 0;
        visibility: hidden;
        transform: translateY(10px);
        transition: opacity, transform 0.7s cubic-bezier(0.4, 0, 0.2, 1);
        left: 5px;
      }
      &::before {
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        border-bottom: 12px solid #fff;
        top: -8px;
        left: 30px;
        content: "";
        position: absolute;
      }
    }

    .login {
      @media(min-width: 992px) {
        display: flex;
        align-items: center;
      }
      .login-item {
        &:first-child {
          margin-right: 10px;
          @media #{$betweentabdesk} {
            margin-right: 0;
          }
          @media #{$mobile} {
            margin-bottom: 20px;
          }
        }

        &:last-child {
          @media #{$maxTab} {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: 2;
            padding: 20px 20px 7px;
            box-shadow: 0 -1px 0 rgba(34, 46, 58, 0.1);
          }
          @media #{$tablet} {
            padding: 20px 0 0;
          }
        }

        .login-link {
          font-size: 14px;
          color: inherit;
          padding: 10px;
          font-weight: 500;
          transition: color 0.2s ease-in-out;
          @media #{$betweentabdesk} {
            font-size: 13px;
          }
          @media #{$maxTab} {
            color: #5b5863;
            font-size: 18px;
          }
          &:hover {
            color: $primaryColor;
          }
        }

        .get-started {
          padding: 10px 15px;
          font-size: 14px;
          display: block;
          text-align: center;
          transition: color 0.2s ease-in-out;
          background-color: $primaryColor;
          color: #fff;
          font-weight: 500;
          border-radius: 3px;
          @media #{$betweentabdesk} {
            font-size: 13px;
            padding: 10px;
          }
          @media #{$maxTab} {
            padding: 13.5px;
            font-size: 16px;
          }
          &:hover {
            background-color: $linkHoverColor;
          }
        }
      }
    }
  }

  .navbar-expand-lg .navbar-collapse {
    flex-grow: 1;
    align-items: center;
    flex-basis: 100%;
    @media (min-width: 992px) {
      display: flex !important;
      flex-basis: auto;
    }
  }
  .navbar-nav {
    @media (min-width: 992px) {
      display: flex;
      flex-direction: row;
    }
  }
}

/* Start navbar toggler */
#lang-icon,
#nav-icon {
  border: 1px solid #e1e0de;
  border-radius: 3px;
  width: 36px;
  height: 36px;
  position: relative;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  span {
    margin: auto;
  }
}

#nav-icon span {
  position: absolute;
  height: 2px;
  background: #ef4273;
  border-radius: 34px;
  transform: rotate(-45deg);
  transition: all 0.2s ease-in-out;
}

/* Icons */
#nav-icon span:nth-child(1) {
  top: 12px;
  left: 14px;
  width: 8px;
  transform-origin: left top;
}

#nav-icon span:nth-child(2) {
  top: 20px;
  left: 11px;
  width: 16px;
  transform-origin: 10% center;
}

#nav-icon span:nth-child(3) {
  top: 25px;
  left: 13px;
  transform-origin: left top;
  width: 8px;
}

#nav-icon.open span:nth-child(1) {
  transform: rotate(45deg);
  top: 15px;
  left: 10px;
  width: 16px;
  transform-origin: 40% center;
}

#nav-icon.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#nav-icon.open span:nth-child(3) {
  transform-origin: -2% center;
  top: 22px;
  left: 12px;
  width: 16px;
}
/* End navbar toggler */
.navbar-light .navbar-nav .nav-link {
  font-weight: 500;
  color: #5b5863;
  font-size: 14px;
  padding: 12px;
  display: block;
  transition: all 0.2s ease-in-out;
  @media #{$betweentabdesk} {
    padding: 12px 5px;
    font-size: 13px;
  }
  @media #{$maxTab} {
    line-height: 25px;
    padding-left: 0;
    font-size: 18px;
  }
}

.navbar-light .navbar-nav .nav-link:hover {
  color: $primaryColor !important;
}

.dropdown-toggle {
  &::after {
    display: inline-block;
    margin-left: 5px;
    vertical-align: middle;
    content: "";
    border-top: 0.3em solid;
    border-right: 0.3em solid transparent;
    border-bottom: 0;
    border-left: 0.3em solid transparent;
    transition: all 0.2s ease;
  }
  &:hover {
    &::after {
      transform: rotate(180deg);
    }
  }
}
/* End Header */
