.access-wevpn {
  padding: 105px 0 85px;
  @media #{$maxTab} {
    padding: 105px 0 60px;
  }
  .access-wevpn-title {
    padding-bottom: 45px;
  }
  .money-back-icon {
    img {
      filter: drop-shadow(0px 4px 10px #CFCFCF);
    }
  }
  .enter-code,
  .create-account {
    background: #ffffff;
    border-radius: 10px;
    margin: 0 0 30px;
    padding: 30px 40px 30px 30px;
    border-left: 10px solid #fd3e72;
    box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.05);
    @media (max-width: 1200px) {
      padding: 20px;
    }
    @media #{$tablet} {
      margin: 0 0 20px;
    }
    .p-lg {
      @media #{$tablet} {
        font-size: 14px;
      }
    } 
    .form-group {
      border: 1px solid #e0ded5;
      border-radius: 5px;
      height: 80px;
      padding-left: 20px;
      position: relative;
      &.error {
        border-color: $primaryColor;
      }
      label {
        font-weight: 500;
        font-size: 12px;
        line-height: 20px;
        margin: 16px 0 4px;
      }
      input {
        border: 0;
        width: 100%;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        &::placeholder {
          color: #8d8b92;
          font-weight: normal;
        }
      }
      .error-icon,.success-icon {
        display: none;
        position: absolute;
        top: 50%;
        right: 30px;
        transform: translateY(-50%);
        @media #{$maxTab} {
          right: 20px;
        }
      }
    }
    .coder-error {
      height: 0;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease;
      &.active {
        height: auto;
        opacity: 1;
        visibility: visible;
      }
    }
  }
  .license {
    @media #{$tablet} {
      padding: 20px;
    }
    @media #{$mobile} {
      margin: 0 0 30px;
    }
    ul li {
      @media #{$tablet} {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
        font-size: 15px;
      }
    }
  }
}
