.wevpn-utilize {
  padding: 40px 0 70px;
  &__text {
    p {
      margin-top: 30px;
      @media #{$maxTab} {
        margin-top: 20px;
      }
    }
  }
  .wevpn-utilize__img {
    @media #{$mobile} {
      margin-bottom: 40px;
    }
  }
}

.how-to-use.console .stepp {
  &:not(:last-child) {
    @media #{$mobile} {
      margin-bottom: 50px;
    }
  }
}
.how-to-use.console .steps .stepp__info h3 {
  font-size: 28px;
  line-height: 140%;
  margin-bottom: 14px;
  @media #{$mobile} {
    font-size: 20px;
  }
}

.how-to-use.console .steps .stepp__info p {
  font-size: 24px;
  line-height: 160%;
  @media #{$maxTab} {
    font-size: 19px;
    line-height: 30px;
  }
  @media #{$mobile} {
    font-size: 16px;
  }
  a {
    color: $primaryColor;
    text-decoration: underline !important;
    transition: all 0.3s ease-in-out;
    &:hover {
      color: $linkHoverColor;
      text-decoration: none !important;
    }
  }
}

.stepp__info .get-started {
  width: 100%;
  max-width: 270px;
  line-height: 64px;
  margin-top: 30px;
  @media #{$mobile} {
    max-width: unset;
  }
}

.how-to-use.console {
  padding-bottom: 80px;
  @media #{$mobile} {
    padding-bottom: 40px;
    .container {
      padding: 0 20px;
    }
  }
}

.how-to-use.console .steps .stepp__img {
  @media #{$mobile} {
    margin-bottom: 30px;
  }
}

