.sitemap-hero {
  padding: 150px 0 70px;
  @media #{$mobile} {
    padding: 60px 0 55px;
  }
}

.sitemap-links {
  padding-bottom: 100px;
  @media #{$mobile} {
    padding-bottom: 60px;
  }
  h3 {
    margin-bottom: 10px;
    margin-top: 80px;
    padding-left: 16px;
    @media #{$mobile} {
      margin-top: 50px;
    }
    position: relative;
    &:before {
      position: absolute;
      content: "";
      width: 4px;
      height: 22px;
      top: 50%;
      transform: translateY(-50%);
      background-color: $primaryColor;
      left: 0;
    }
    &:after {
      position: absolute;
      content: "";
      width: 100%;
      border-bottom: 1px solid #EBEBEB;
      left: 0;
      bottom: -10px;
    }
  }
  ul {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 20px;
    li {
      font-size: 18px;
      width: 25%;
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      @media #{$mobile} {
        width: 100%;
      }
      @media #{$tablet} {
        width: 50%;
      }
      @media #{$betweentabdesk} {
        width: 33.3%;
      }
      a {
        transition: all 0.3s ease;
        padding: 2px 0;
        &:hover {
          color: $primaryColor;
        }
      }
      .new-green-tag {
        margin-left: 8px;
      }
      .beta {
        font-size: 10px;
        line-height: 16px;
        background: $primaryColor;
        border-radius: 9px 9px 9px 0px;
        padding: 0 8px;
        display: inline-block;
        text-transform: uppercase;
        color: #fff;
        margin-left: 8px;
      }
    }
  }
}